@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  // блокировка скролла
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked; }

  &.css-scroll-fixed.open-only-mob,
  &.touchevents.css-scroll-fixed_only-mob {
    @include media('<tab') {
      @include scroll-blocked; } } }

body {
  position: relative;
  @include font-normal;
  @include font($font-size, $line-height);
  color: $text-color;
  min-width: $page-layout-min;
  background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

  // выделение текста
  *::selection {
    background-color: $base-color;
    color: white; } }

.max-wrap {
  max-width: $page-layout-width;
  @include push-auto; }

.layout {
  @if nth($page-layout-space, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space, 2)); }

  @include media('<mob') {
    @if nth($page-layout-space--mob, 2) != 0 {
      @include padding-horizontal(nth($page-layout-space--mob, 2)); } } }

.layout-narrow {
  @if nth($page-layout-space-narrow, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space-narrow, 2));

    @include media('<lap-s') {
      @if nth($page-layout-space-narrow--tab, 2) != 0 {
        @include padding-horizontal(nth($page-layout-space-narrow--tab, 2)); } }

    @include media('<mob') {
      @if nth($page-layout-space--mob, 2) != 0 {
        @include padding-horizontal(nth($page-layout-space--mob, 2)); } } } }

.layout-text {
  max-width: $text-layout-width;
  @include push-auto; }

.layout-flood {
  max-width: $text-layout-width;
  @include push-auto; }

// Прижать футер к низу страницы
// На html, body необходимо задать высоту 100% (Задано в reset.sass)
body {
  display: flex;
  flex-direction: column; }

.header {
  flex-shrink: 0; }

.main {
  flex-shrink: 0; }

.footer {
  margin-top: auto;
  flex-shrink: 0; }

