@import "../utils/functions";
@import "../utils/placeholders";

// ==========================================================================
// Variables
// ==========================================================================

// Medium query
$breakpoints: ("desk": 1920px, "desk-s": 1600px, "lap-l": 1440px, "lap": 1366px, "lap-s": 1280px, "net": 1200px, "tab": 992px, "tab-s": 768px, "mob": 640px, "mob-s": 480px, "mob-xs": 375px, "mob-xxs": 375px);

// Fonts style
// ==========================================================================
//$font-norm: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default
$font-norm: "circe-regular", Helvetica, Arial, sans-serif !default;
$font-bold: "circe-bold", Helvetica, Arial, sans-serif !default;
$font-bold-italic: "gilroy-bold-italic", Helvetica, Arial, sans-serif !default;
$font-extra-bold: "circe-extra-bold", Helvetica, Arial, sans-serif !default;
$font-italic: "gilroy-italic", Helvetica, Arial, sans-serif !default;
$font-light: "circe-light", Helvetica, Arial, sans-serif !default;
$font-alt: Georgia, serif !default;

// Reset font-weight and font-style
// Use for font-face
@mixin font-reset() {
  font: {
    weight: normal;
    style: normal; } }

// Main font
@mixin font-normal() {
  font-family: $font-norm;
 }  //+font-reset

@mixin font-bold() {
  font-family: $font-bold;
  font-weight: normal; }

@mixin font-extra-bold() {
  font-family: $font-extra-bold;
  font-weight: normal; }

@mixin font-italic() {
  font-family: $font-italic;
  font-style: normal; }

@mixin font-bold-italic() {
  font-family: $font-bold-italic;
  font-weight: normal;
  font-style: normal; }

@mixin font-light() {
  font-family: $font-light;
  font-weight: normal; }

// Alternative font
@mixin font-alt() {
  font-family: $font-alt; }

@mixin font-alt-bold() {
  font-family: $font-alt;
  font-weight: bold; }

@mixin font-alt-bold-italic() {
  font-family: $font-alt;
  font-weight: bold;
  font-style: italic; }

// Base
// ==========================================================================

$font-size: 1.8rem !default;
$line-height: 2.8rem !default;
$letter-spacing: 0.025em !default;
// ----------------------------------------------------
$font-size--mob: 1.6rem !default;
$line-height--mob: 2.2rem !default;
$letter-spacing--mob: 0.025em !default;

// Spaces
$bottom-space: $line-height !default;
$bottom-space--mob: 2rem !default;

// Heading h1
$font-size-h1: 3.6rem !default;
$line-height-h1: 5.2rem !default;
$line-height-h1-kef: $line-height-h1 / $font-size-h1;
// ----------------------------------------------------
$font-size-h1--net: 3.4rem !default;
$line-height-h1--net: round-float($font-size-h1--net * $line-height-h1-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
$font-size-h1--tab: 3.0rem !default;
$line-height-h1--tab: round-float($font-size-h1--tab * $line-height-h1-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
$font-size-h1--mob: 2.6rem !default;
$line-height-h1--mob: round-float($font-size-h1--mob * $line-height-h1-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
@mixin font-h1() {
  font-size: $font-size-h1;
  line-height: $line-height-h1;

  @include media('<net') {
    font-size: $font-size-h1--net;
    line-height: $line-height-h1--net; }

  @include media('<tab-s') {
    font-size: $font-size-h1--tab;
    line-height: $line-height-h1--tab; }

  @include media('<mob') {
    font-size: $font-size-h1--mob;
    line-height: $line-height-h1--mob; } }

// Heading h2
$font-size-h2: 3.0rem !default;
$line-height-h2: 4.2rem !default;
$line-height-h2-kef: $line-height-h2 / $font-size-h2;
// ----------------------------------------------------
$font-size-h2--net: 2.8rem !default;
$line-height-h2--net: round-float($font-size-h2--net * $line-height-h2-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
$font-size-h2--tab: 2.4rem !default;
$line-height-h2--tab: round-float($font-size-h2--tab * $line-height-h2-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
$font-size-h2--mob: 2.0rem !default;
$line-height-h2--mob: round-float($font-size-h2--mob * $line-height-h2-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
@mixin font-h2() {
  font-size: $font-size-h2;
  line-height: $line-height-h2;

  @include media('<net') {
    font-size: $font-size-h2--net;
    line-height: $line-height-h2--net; }

  @include media('<tab-s') {
    font-size: $font-size-h2--tab;
    line-height: $line-height-h2--tab; }

  @include media('<mob') {
    font-size: $font-size-h2--mob;
    line-height: $line-height-h2--mob; } }

// Heading h3
$font-size-h3: 2.4rem !default;
$line-height-h3: 3.0rem !default;
$line-height-h3-kef: $line-height-h3 / $font-size-h3;
// ----------------------------------------------------
$font-size-h3--tab: 2.2rem !default;
$line-height-h3--tab: round-float($font-size-h3--tab * $line-height-h3-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне
// ----------------------------------------------------
$font-size-h3--mob: 1.8rem !default;
$line-height-h3--mob: round-float($font-size-h3--mob * $line-height-h3-kef, 1) !default; // Использовать рассчетное значение, если нет в дизайне

// ----------------------------------------------------
@mixin font-h3() {
  font-size: $font-size-h3;
  line-height: $line-height-h3;

  @include media('<net') {
    font-size: $font-size-h3--tab;
    line-height: $line-height-h3--tab; }

  @include media('<tab-s') {
    font-size: $font-size-h3--mob;
    line-height: $line-height-h3--mob; } }

// Heading h4
$font-size-h4: $font-size !default;
$line-height-h4: $line-height !default;

// Heading h5
$font-size-h5: $font-size !default;
$line-height-h5: $line-height !default;

// Heading h6
$font-size-h6: $font-size !default;
$line-height-h6: $line-height !default;


// Colors
// ==========================================================================

// Color of brands
$base-color: #6e52dd !default;
$base-color-light: #8f77ed !default;
$base-color-ultra-light: #ccc3f3 !default;
$base-color-alt: #f8e71c !default;
// ----------------------------------------------------
$bg-color: #f5f5f5 !default;
$bg-color-alt: #f3f3f3 !default;
// ----------------------------------------------------
//$text-color: #131313 !default
$text-color: #4a4a4a !default; // По дизайну используется на внутренней странице
$text-color-light: #7e7e7e !default;
$text-color-disabled: #9b9b9b !default;
$text-color-ultra-light: #c3c3c3 !default;
$title-color: #131313 !default;
// ----------------------------------------------------
$link-color: $base-color !default;
// ----------------------------------------------------
$focus-color: #329af8 !default;
// ----------------------------------------------------
$border-color: #dbdbdb !default;
$border-color-light: #f1f1f1 !default;
// ----------------------------------------------------
$mark-color: #ef4180 !default;
$mark-color-alt: #96d728 !default;
// ----------------------------------------------------


// Class of typography styles
// ==========================================================================
// @example Sass - Использование
// .#{$user-content}
//
// @example css - Вывод CSS
// .article-content
$article-content: article-content !default;
$typography: typography !default;


// Layout
// ==========================================================================

// минимальная ширина сайта
// минимальная ширина сайта
$page-layout-min: 32rem !default;
//$page-layout-min: 102.4rem !default
// максимальная ширина сайта
$page-layout-width: 160rem !default;
//$page-layout-width: 128rem !default
// отступы для всего сайта
$page-layout-space: 0 2rem !default;
$page-layout-space--mob: 2rem 1rem !default;
// отступы для альтернативных элементов
$page-layout-space-narrow: 0 9.7rem !default;
$page-layout-space-narrow--tab: 0 2rem !default;
// максимальная ширина текстового блока
$text-layout-width: 60rem !default;
$text-layout-width-alt: 100rem !default;
// максимальная ширина текстового блока выходящего за пределы текста
$flood-layout-width: 103rem !default;
// внутренние отступы текстовых страниц: статей, обзоров
// первое значение в "парах" - вертикальные отступы установлено как 0,
// так как оно разное на разных страницах
$article-layout-space: 4rem 4rem;
$article-layout-space--lap: 3.5rem 3rem;
$article-layout-space--tab: 2.5rem 2rem; // горизонтальные отступы аналогичные $section-inner-space--tab
$article-layout-space--mob: 2rem 1.5rem; // горизонтальные отступы аналогичные $section-inner-space--mob
// внешние и внутренние отступы секций (большие лотические единици)
$section-inner-space: 3rem 2.4rem !default;
$section-inner-space--tab: 2rem 2rem !default; // '<lap-s' <1280
$section-inner-space--mob: 2rem 1.5rem !default; // '<mob' <640
$section-outer-space: 2.5rem 2rem !default;
$section-outer-space--tab: 2rem 2rem !default;
$section-outer-space--mob: 2rem 1.2rem !default;
// внешние и внутренние одиночных элементов (боксов)
$box-inner-space: 1.8rem 2.0rem !default;
$box-outer-space: 2.0rem 2.0rem !default;
// отступы внутри элементов по типу плитки или сетки
$grid-inner-space: 2.8rem 3rem !default;
$grid-inner-space--lg: 1.8rem 2rem !default;
$grid-inner-space--sm: 0.8rem 1.0rem !default;


// Z-index
// ==========================================================================
$z-popup: 9995;
$z-overlay: 9990;


// Header
// ==========================================================================
$header-height: 13rem !default;
$header-height--scrolled: 6.3rem !default;
$header-height--tab: 5rem !default;
$header-height--mob: 5rem !default;
//$header-top__r-width: 56rem !default
$header-top__r-width: 59rem !default;
$header-top__r-width--net: 50rem !default;
$header-btn-space--tab: 0 0.5rem !default;
$header-btn-space--mob: 0 0.3rem !default;
$header-btn-size: 4rem !default;
$header-btn-size--tab: 3rem !default;
$header-btn-size--mob: 3.4rem !default;
$header-btn-icon-size: 1.4rem !default;
$header-btn-icon-size--tab: 1.8rem !default;
$header-btn-icon-size--mob: 2.0rem !default;


// Catalog shutter
// ==========================================================================
//$catalog-is-open: 'catalog-is-open'


// Sidebar & Aside
// ==========================================================================
$sidebar-min-width: 24rem !default;
$sidebar-width: 19.3% !default;
$aside-min-width: 20rem !default;
$aside-min-width--net: 10rem !default;
$aside-width: 16.1% !default;


// Footer
// ==========================================================================
$footer-grid-space: 0 4.4rem !default;
$footer-grid-space--tab: 0 2rem !default;
$footer-grid-space--mob: 0 1rem !default;
// v h

// Sliders
// ==========================================================================
$slider-button-size: 46px 46px; // h w
$slider-button-size--mob: 46px 46px; // h w
$slider-arrow-size: 34px;
$slider-arrow-size--mob: 28px;
// Promo Slider
//$slider-promo-button-size: 60px 64px // h w
//$slider-promo-button-size--mob: 50px 50px // h w

// Promo section
// ==========================================================================

$promo-section-bottom: 11.5rem; // -11.5rem Контете ниже перекрывает часть промо секции
$promo-section-bottom--tab: 8rem; // -3.5rem Контете ниже перекрывает часть промо секции

// Social
// ==========================================================================
$facebook-color: #3a589e !default;
$vkontakte-color: #4b73a4 !default;
$twitter-color: #1da0f2 !default;
$odnoklassniki-color: #f2822e !default;
$youtube-color: #e62117 !default;
$telegram-color: #32aadf !default;
$instagram-color-start: #de4f5a !default;
$instagram-color-end: #a43b97 !default;


// Buttons
// ==========================================================================
$btn-height: 50px !default;
$btn-bg-color: $base-color-alt !default;
$btn-bg-hover-color: $btn-bg-color !default;
$btn-text-color: #333 !default;
$btn-alt-bg-color: $base-color-light !default;
$btn-alt-bg-hover-color: $base-color-light !default;
$btn-alt-text-color: white !default;
$btn-disabled-color: #ccc !default;


// Forms
// ==========================================================================

// List of inputs
$inputs: 'input[type="text"]', 'input[type="password"]', 'input[type="email"]', 'input[type="search"]', 'textarea';

// Colors of forms
$form-text-color: #777 !default;
$form-placeholder-color: rgba(black, 0.5) !default;
$form-bg-color: white !default;
$form-border-color: #d3d3d3 !default;
// validation
$form-error-color: #ff3322 !default;
$form-success-color: #0dad0d !default;
// disabled
$form-text-color--disabled: rgb(190, 190, 190) !default;
$form-bg-color--disabled: rgb(222, 222, 222) !default;
$form-border-color--disabled: rgb(222, 222, 222) !default;
// focus
$form-border-color--focus: lighten($form-text-color, 20%) !default;

// Fonts of forms
$form-font-size: 14px !default;
$form-line-height: 24px !default;
$form-input-height: 50px !default;
$form-letter-spacing: normal !default;

// @example Sass - Использование
// @example css - Вывод CSS
@mixin inputs-font() {
  //+font-alt
  font-family: inherit;
  font-size: $form-font-size;
  line-height: $form-line-height;
  @if $form-letter-spacing {
    letter-spacing: $form-letter-spacing; }
  color: $form-text-color; }

// Layout of formsn
$form-border-width: 1px !default;
$form-input-space-vt: floor(($form-input-height - $form-line-height - $form-border-width*2)/2) !default;
// верхний паддинг округляем в нижнюю сторону
$form-input-space-vb: ceil(($form-input-height - $form-line-height - $form-border-width*2)/2) !default;
// нижний паддинг округляем в верхнюю сторону
$form-input-space-h: 20px !default;
$form-input-space-h--has-prefix: 34px !default;
$form-bottom-space: 1rem !default;
$form-bottom-space--mob: 1rem !default;
$form-gap: $form-bottom-space !default;

//=== form-small
$form-input-height--small: 40px !default;
$form-input-space-vt--small: floor(($form-input-height--small - $form-line-height - $form-border-width*2)/2) !default;
// верхний паддинг округляем в нижнюю сторону
$form-input-space-vb--small: ceil(($form-input-height--small - $form-line-height - $form-border-width*2)/2) !default;
// нижний паддинг округляем в верхнюю сторону
$form-input-space-h--small: 12px !default;

// Vertical spaces
@mixin forms-space() {
  margin: 0 0 $form-bottom-space;

  @include media('<mob') {
    margin: 0 0 $form-bottom-space--mob; } }

// Checkboxes
// Radio Buttons
$checkbox-size: 18px !default;
$checkbox-check-size: 6px 10px !default;
$radio-check-size: 10px !default;
$checkbox-color: #d8d8d8 !default;

// Select
$select-space-v: 12px !default;
$select-arrow-width: 40px !default;
$select-arrow-height: 100% !default;
$multiple-item-space: 3px !default;

//=== form-small
$select-arrow-width--small: 20px !default;

// Style Selection
// ----------------------------------------
@mixin selects-font() {
  @include inputs-font; }

// ----------------------------------------
@mixin select-selection($width: 100%) {
  height: $form-input-height;
  width: $width;
  padding: $form-input-space-vt $select-arrow-width $form-input-space-vb $form-input-space-h;
  background: $form-bg-color;
  border: #{$form-border-width} solid #{$form-border-color};
  border-radius: 4px;
  box-shadow: none;
  text-indent: 0;
  outline: none; }
// ----------------------------------------

@mixin select-arrow() {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  @include size($select-arrow-width, $select-arrow-height);
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  svg {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 0.6rem;
    width: 1rem;
    transform: translate(-50%, -50%);

    path, use {
      stroke: $base-color; } } }

// States
@mixin form-success() {
  color: $form-success-color;
  border-color: $form-success-color;
 }  //box-shadow: inset 0 0 0 1px $form-success-color

@mixin form-error() {
  color: $form-error-color;
  border-color: $form-error-color;
 }  //box-shadow: inset 0 0 0 1px $form-error-color

@mixin form-has-value() {
  //color: blueviolet
  border-color: $form-border-color--focus;
 }  //box-shadow: inset 0 0 0 1px $form-border-color--focus

@mixin form-on-focus() {
  //color: $form-border-focus-color
  border-color: $form-border-color--focus; }


// Main section title
// ==========================================================================
$main-section-title-font-size: 3.6rem;
$main-section-title-line-height: 3.6rem;
$main-section-title-kef: round-float($main-section-title-line-height / $main-section-title-line-height, 1);
// ----------------------------------------
$main-section-title-font-size--tab: 3.2rem;
$main-section-title-line-height--tab: round-float($main-section-title-font-size--tab * $main-section-title-kef, 1);
// ----------------------------------------
$main-section-title-font-size--mob: 2.6rem;
$main-section-title-line-height--mob: round-float($main-section-title-font-size--mob * $main-section-title-kef, 1);


// Animation
// ==========================================================================
$animation-speed: 0.25s !default;
$animation-speed-mouseleave: 0.35s !default;
$animation-use: true !default;

// Support ie9
// ==========================================================================
$ie9: false;
