/* cart-link */
.cart-link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  max-width: 14rem;
  text-decoration: none;
  color: white;
  cursor: pointer;
  padding: 0.6rem 0.8rem 0.7rem;
  border-radius: 0 0 7px 7px;
  user-select: none;
  transition-property: background-color, color, opacity;
  transition-duration: 0.25s;
}

@media (max-width: 991px) {
  .cart-link {
    padding: 0 0.5rem;
    flex-basis: auto;
    max-width: none;
  }
}

@media (max-width: 639px) {
  .cart-link {
    padding: 0 0.3rem;
  }
}

.header-changed .cart-link {
  border-radius: 4rem;
}

.header-top .cart-link {
  margin-left: 2.0rem;
}

