    margin-left: 1.5rem;
  }
}

@media (max-width: 991px) {
  .header-top .cart-link {
    height: 5rem;
    margin-left: 0;
    margin-right: -0.5rem;
    user-select: none;
  }
}

@media (max-width: 639px) {
  .header-top .cart-link {
    margin-right: -0.3rem;
  }
}

.cart-link[data-title]::before {
  content: attr(data-title);
  display: block;
  min-width: 100%;
  padding: 0.4rem 0.8rem;
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: 2px;
  font-size: 1.2rem;
  line-height: 2rem;
  text-align: center;
  letter-spacing: normal;
  white-space: nowrap;
  z-index: 2;
  color: #4a4a4a;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, -2px);
  transform: translate3d(0, -2px, 0);
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  will-change: transform, opacity, visibility;
  transition-property: transform, opacity, visibility;
  transition-delay: 0s;
  transition-duration: 0.25s;
}

@media (max-width: 991px) {
  .cart-link[data-title]::before {
    display: none;
  }
}

.cart-link > i {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background-color: white;
  width: 3rem;
  height: 3rem;
  border-radius: 3rem;
  transition-property: background-color;
  transition-duration: 0.25s;
}

@media (max-width: 991px) {
  .cart-link > i {
    width: 3rem;
    height: 3rem;
    border-radius: 3rem;
    background-color: white;
  }
}

@media (max-width: 639px) {
  .cart-link > i {
    width: 3.4rem;
    height: 3.4rem;
    border-radius: 3.4rem;
  }
}

.cart-link > i::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1.4rem;
  height: 1.4rem;
  margin: -0.7rem 0 0 -0.7rem;
  background: url(../img/icon-close.svg) no-repeat;
  background-size: contain;
  opacity: 0;
  will-change: opacity;
  transition-property: opacity;
  transition-duration: 0.25s;
  display: block;
}

@media (min-width: 992px) {
  .cart-link > i::after {
    left: 2.3rem;
  }
}

.cart-link > i svg {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  height: 100%;
  will-change: opacity;
  transition-property: opacity;
  transition-duration: 0.25s;
}

.cart-link > i svg path {
  transition-property: fill;
  transition-duration: 0.25s;
}

.cart-link > i svg path.svg-ico-cart-circle {
  fill: white;
}

.cart-link > i svg path.svg-ico-cart-cart {
  fill: #6e52dd;
}

.cart-link > i + .cart-link__text {
  padding-left: 0.7rem;
}

.cart-link .counter {
  display: block;
  position: absolute;
  left: 0.2rem;
  top: 0.2rem;
  width: 1.6rem;
  height: 1.6rem;
  padding: 0.1rem 0 0;
  font-family: "circe-regular", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-align: center;
  color: white;
  background-color: #e61f1f;
  border-radius: 1.6rem;
  z-index: 10;
  transition: opacity 0.25s;
}

@media (max-width: 991px) {
  .cart-link .counter {
    margin: 0;
    padding: 0.1rem 0 0;
    left: auto;
    right: 0.2rem;
    top: 0.6rem;
    width: 1.2rem;
    height: 1.2rem;
    font-size: 1rem;
    line-height: 1.2rem;
    border-radius: 1.2rem;
  }
}

@media (max-width: 639px) {
  .cart-link .counter {
    width: 1.6rem;
    height: 1.6rem;
    font-size: 1rem;
    line-height: 1.6rem;
    border-radius: 1.6rem;
  }
}

.cart-link.quick-cart_opened .counter {
  opacity: 0;
}

.cart-link.push .counter {
  animation: incCount 0.8s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

.cart-link.pick .counter {
  animation: decCount 0.8s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

@keyframes incCount {
  0% {
    transform: translate(0, 0.8rem) scale(1.5);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes decCount {
  0% {
    transform: translate(0, -0.8rem) scale(0.8);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

.cart-link__text {
  font-size: 1.2rem;
  line-height: 1;
  color: currentColor;
}

@media (max-width: 991px) {
  .cart-link__text {
    display: none;
  }
}

.cart-link__text > strong, .cart-link__text > span {
  font-family: "circe-regular", Helvetica, Arial, sans-serif;
  display: block;
  white-space: nowrap;
}

@media (min-width: 992px) {
  .no-touch .cart-link:not(.disabled):not(.quick-cart_opened):hover, .cart-link:not(.disabled).quick-cart_opened {
    color: #6e52dd;
    background-color: white;
  }
  .no-touch .cart-link:not(.disabled):not(.quick-cart_opened):hover > i, .cart-link:not(.disabled).quick-cart_opened > i {
    background-color: #6e52dd;
  }
  .no-touch .cart-link:not(.disabled):not(.quick-cart_opened):hover > i svg path.svg-ico-cart-circle, .cart-link:not(.disabled).quick-cart_opened > i svg path.svg-ico-cart-circle {
    fill: #6e52dd;
  }
  .no-touch .cart-link:not(.disabled):not(.quick-cart_opened):hover > i svg path.svg-ico-cart-cart, .cart-link:not(.disabled).quick-cart_opened > i svg path.svg-ico-cart-cart {
    fill: white;
  }
}

@media (min-width: 992px) {
  .cart-link:not(.disabled).quick-cart_opened > i {
    background-color: inherit;
  }
}

.cart-link:not(.disabled).quick-cart_opened > i::after {
  opacity: 1;
}

.cart-link:not(.disabled).quick-cart_opened > i svg {
  opacity: 0;
}

.no-touch .cart-link:not(.disabled)[data-title]:hover::before {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
  transform: translate3d(0, 0, 0);
}

.no-touch .cart-link:not(.disabled).quick-cart_opened[data-title]:hover::before {
  opacity: 0;
}

.cart-link:not(.disabled).push > i {
  animation: pushInCart 1.2s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

.cart-link:not(.disabled).pick > i {
  animation: pickOutCart 1.2s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

@keyframes pushInCart {
  0% {
    transform: translate(0, 0.8rem) scale(1.25, 1);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes pickOutCart {
  0% {
    transform: translate(0, -0.5rem) scale(0.85, 1);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

.cart-link.disabled {
  cursor: default;
  pointer-events: none;
}

.cart-link.disabled > i svg path.svg-ico-cart-cart {
  fill: #c3c3c3;
}

