@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* swiper slider */
/* slider pagination */
.swiper-pagination {
  pointer-events: none;
  line-height: 0; }

.swiper-pagination-bullet {
  display: inline-block;
  vertical-align: top;
  $size: 8px;
  @include size($size);
  outline: none;
  border-radius: $size;
  border: none;
  color: $base-color;
  opacity: 0.3;
  margin: 6px;
  pointer-events: auto;
  background: white;
  // transition
  $prop: opacity, transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .swiper-container-horizontal>.swiper-pagination-bullets & {
    margin: 0 6px; }

  // hover
  &:hover {
    opacity: 0.8; } }

.swiper-pagination-bullet-active {
  opacity: 1;
  cursor: default;

  &:hover {
    opacity: 1;
    cursor: default; } }

/* swiper slider end */
