/* === helper classes === */
.mark {
  color: $mark-color;
  font-style: inherit;
  font-weight: inherit; }

.push-auto {
  margin-left: auto;
  margin-right: auto; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.nobr {
  white-space: nowrap; }

.hide-text {
  @include hide-text; }

.hide {
  display: none !important; }

// Только на разрешениях больше планшетных (условно, больше 991px)
.desktop-only {
  @include media('<tab') {
    display: none; } }

// Только на планшетных и мобильных разрешениях (условно, меньш 992px)
.no-desktop-only {
  @include media('>=tab') {
    display: none; } }

// Только на планшетных разрешениях (условно, от 992px до 641 включительно)
.tablet-only {
  @include media('>=tab', '<mob') {
    display: none; } }

// Только не на планшетных разрешениях (условно, от 993px и выше, и от 640 и ниже)
.no-tablet-only {
  @include media('<tab', '>=mob') {
    display: none; } }

// Только на мобильных разрешениях (условно, с 640px и ниже)
.mobile-only {
  @include media('>=mob') {
    display: none; } }

// Только не на мобильных разрешениях (условно, с 641px и выше)
.no-mobile-only {
  @include media('<mob') {
    display: none; } }

