  //background-color: $bg-color
  //border-radius: 50% 50% 0 0 / 100% 100% 0 0

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    bottom: -1.3rem;
    width: 100%;
    height: 7.7rem;
