/* spinner */
$spinner-size: 32px 113px;
$spinner-btn-width: 35px;
.ui-spinner {
  width: nth($spinner-size, 2);
  padding: 0 #{$spinner-btn-width};
  background-color: #fff;
  user-select: none;

  &.ui-state-disabled {
    color: #e6e6e6; } }

.spinner-js {
  opacity: 0;
  visibility: hidden;

  &.ui-spinner-input {
    opacity: 1;
    visibility: visible; } }

.ui-widget.ui-widget-content {
  border: 1px solid $border-color;
  border-radius: 5px;
  overflow: hidden; }

.ui-spinner-input, .ui-spinner-input[type="text"] {
  height: 30px;
  width: 100%;
  color: black;
  margin: 0;
  padding: 7px 4px;
  @include font-normal;
  @include font(12px, 16px);
  vertical-align: top;
  text-align: center;
  border: none;
  background: none;
  box-shadow: none;
  user-select: auto;

  -moz-appearance: textfield;

  &::-webkit-inner-spin-button,
  &::-webkit-outer-spin-button {
    -webkit-appearance: none; }

  @include media('<mob-s') {
    height: 36px;
    padding: 9px 4px;
    @include font(16px, 18px); }

  &:focus, &:hover {
    box-shadow: none !important;
    border-color: transparent !important; } }

.ui-spinner-button {
  position: absolute;
  top: 0;
  height: 100%;
  width: $spinner-btn-width;
  padding: 0;
  margin: 0;
  cursor: pointer;
  color: currentColor; }

.ui-spinner a.ui-spinner-button {
  border-color: inherit;
  border-style: solid;
  outline: none;
  user-select: none;
 }  //-webkit-tap-highlight-color: transparent

.ui-spinner-up {
  right: 0;
  border-width: 0 0 0 1px; }

.ui-spinner-down {
  left: 0;
  border-width: 0 1px 0 0; }

.ui-button-icon-only .ui-icon {
  $this-icon-lines-size: 12px, 2px;
  // width, height

  @include size(nth($this-icon-lines-size, 1), nth($this-icon-lines-size, 2));
  position: absolute;
  @include position(50%);
  margin-top: -#{round(nth($this-icon-lines-size, 2)/2)};
  margin-left: -#{round(nth($this-icon-lines-size, 1)/2)};
  overflow: visible;

  &::before, &::after {
    @include pseudo();
    @include position(0);
    @include size(nth($this-icon-lines-size, 1), nth($this-icon-lines-size, 2));
    background-color: $text-color-light;
    transition: background-color $animation-speed; } }

.ui-spinner-up {
  .ui-button-icon {

    &::before {
      transform: rotate(0.25turn); } } }

.ui-spinner-down {

  [aria-valuenow="0"] ~ & {
    cursor: default;

    .ui-button-icon {
      &::before, &::after {
        background-color: $text-color-disabled !important; } } } }

.ui-spinner-button {
  // hover
  &:hover {
    .ui-button-icon {
      &::before, &::after {
        background-color: $base-color; } } }

  // active
  //.ui-spinner-button:active
  //.ui-button-icon
  //  transform: translate(0, 1px)

  // disabled
  &.disabled {
    .ui-button-icon {
      opacity: 0.3;
      pointer-events: none; } } }

/* spinner end */

/*ui autocomplete drop */
.ui-autocomplete {
  border-radius: 0;
  border: none;
  padding: 0;
  font-size: 14px;
  line-height: 18px;
  background: #fff;
  box-sizing: border-box;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.15);
  border-top: 1px solid $border-color-light;

  li {
    padding: 0;
    margin: 0; }

  .ui-menu-item-wrapper {
    padding: 11px 20px;
    transition-property: color, background;
    transition-duration: $animation-speed; }

  .ui-state-active,
  .ui-state-focus {
    margin: 0;
    background-color: $base-color;
    color: $bg-color; } }

/*ui autocomplete drop end */

/* stars rating */
.br-theme-css-stars .br-widget a.br-active:after, .br-theme-css-stars .br-widget a.br-selected:after {
  color: $base-color-alt; }

/* stars rating end */

/* simple bar */
[data-simplebar] {
  opacity: 0;

  .no-js & {
    opacity: 1;
    overflow: auto; } }

[data-simplebar="init"] {
  opacity: 1; }

.simplebar-track {
  z-index: 5; }

/* simple bar end */

/* simple form */
.simple-form__group {
  margin-bottom: 3rem;

  &:last-child {
    margin-bottom: 0; } }

/* simple form end */

/* tabs */
.tabs__panels-js,
.tabs-nested__panels-js {
  position: relative;
  z-index: 1; }

.tabs__panels-js, .tabs-nested__panels-js {

  & > div {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    outline: none;
    width: 100%; } }

/* tabs end */

/* breadcrumbs */
.breadcrumbs {
  margin-bottom: 1.1rem;
  padding: 0 #{nth($section-inner-space, 2)};

  @include media('<lap-s') {
    padding: 0 #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding: 0 #{nth($section-inner-space--mob, 2)}; }

  &:last-child {
    margin-bottom: 0; } }

.breadcrumbs__list {
  cursor: default;
  overflow: hidden;
  @include font-light;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: white;

  &, .#{$article-content} & {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      padding: 0 10px 0 0;
      margin: 0;

      &::before, &:first-child::before {
        content: none; } } }

  .layout-article & {
    max-width: none; }

  li {
    display: inline;
    position: relative;

    &::after {
      content: '';
      position: relative;
      display: inline-block;
      vertical-align: baseline;
      width: 5px;
      height: 5px;
      border: solid rgba(white, 0.2);
      border-width: 1px 1px 0 0;
      transform: rotate(45deg);
      margin: -1px 0 1px 9px; }

    &:last-child::after {
      content: none; } }

  a {
    padding-top: 0.5em;
    color: rgba(white, 0.7);
    text-decoration: none;
    cursor: pointer;
    // transition
    $prop: color;
    //will-change: $prop
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    @include underlinedLinks(rgba(white, 0.2)); } }

/* breadcrumbs end */

/* i-list */
.i-list {
  position: relative;
  margin-bottom: 2rem;

  @include media('<mob') {
    margin-bottom: 1rem; }

  &:last-child {
    margin-bottom: 0; } }

.i-list__item {
  position: relative;
  margin-bottom: 2rem;
  padding-left: calc(3rem + 2rem);

  @include media('<mob') {
    margin-bottom: 1rem; }

  &:last-child {
    margin-bottom: 0; } }

.i-list__icon {
  position: absolute;
  left: 0;
  top: 0;
  $size: 3rem;
  height: $size;
  width: $size; }

.i-list__content {
  font-size: 1.6rem;
  line-height: 2.0rem;

  h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5,
  p, ul, ol {
    margin-bottom: 1.5rem;

    &:last-child {
      margin-bottom: 0; } } }

/* i-list end */

/* pagination */
// pagination
$pagination-hspace: 1.2rem;
$pagination-vspace: 0.4rem;
$pagination-font-size: 1.4rem;
$pagination-height: 3rem;
$pagination-width: $pagination-height;
$pagination-border-width: 0;

.pagination {
  @include font-bold;
  @include clear-spacing;
  color: inherit;
  text-align: center;
  cursor: default;
  margin-bottom: $bottom-space;
  @include push-auto;
  @include clear;

  @include media('<mob') {
    position: relative;
    margin-bottom: $bottom-space--mob;
    padding-bottom: $pagination-height + $pagination-vspace*2; }

  &:last-child {
    margin-bottom: 0; } }

.pg-left {
  float: left;

  @include media('<mob') {
    float: none;
    position: absolute;
    bottom: 0;
    right: 50%; } }

.pg-right {
  float: right;

  @include media('<mob') {
    float: none;
    position: absolute;
    bottom: 0;
    left: 50%; }

  &::before {
    @include pseudo();
    width: 0;
    height: 2rem;
    margin-top: -1rem;
    top: 50%;
    left: -0.1rem;
    border-left: 0.2rem solid;
    display: none;

    @include media('<mob') {
      display: block; } } }

.pg-first-arr, .pg-last-arr, .pg-prev-arr, .pg-next-arr {
  //display: none
  position: absolute;
  @include position(50%, 0);
  @include size(1.2rem, 0.2rem);
  margin-top: -0.1rem;
  letter-spacing: 0;
  border-top: 0.2rem solid $text-color;

  @if $animation-use {
    transition: {
      property: border-color;
      duration: $animation-speed; } }

  &::before, &::after {
    @include pseudo();
    top: 0;
    @include size(0.8rem);
    margin-top: -0.1rem;
    border: 0 solid $text-color;
    backface-visibility: hidden;

    @if $animation-use {
      transition: {
        property: border-color;
        duration: $animation-speed; } } } }

.pg-first-arr, .pg-prev-arr {
  &::before, &::after {
    transform: rotate(-45deg);
    transform-origin: left top;
    left: 0;
    margin-left: -0.1rem;
    border-width: 0.2rem 0 0 0.2rem; } }

.pg-last-arr, .pg-next-arr {
  left: auto;
  right: 0;

  &::before, &::after {
    right: 0;
    margin-right: -0.1rem;
    transform-origin: top right;
    transform: rotate(45deg);
    border-width: 0.2rem 0.2rem 0 0; } }

.pg-first-arr, .pg-last-arr {
  width: 1.8rem; }

.pg-first-arr {

  &::after {
    margin-left: 0.6rem; } }

.pg-last-arr {

  &::after {
    margin-right: 0.6rem; } }

.pg-prev-arr, .pg-next-arr {

  &::after {
    display: none; } }

.pg-first, .pg-last, .pg-prev, .pg-next {
  @include display-inline-block-middle;
  position: relative;
  //width: 27px
  @if $pagination-border-width != 0 {
    margin: 0 0 0 -#{$pagination-border-width}; }
  @else {
    margin: #{$pagination-vspace} #{$pagination-hspace}; }
  color: inherit;
  text-decoration: none;

  span {
    @include padding-horizontal(2rem); }

  &.disabled {
    opacity: 0.5;
    cursor: default; }

  .no-touch &:not(.disabled):hover {
    color: $base-color;

    [class*="-arr"] {
      &, &::before, &::after {
        border-color: $base-color; } } } }

//&:not(.disabled):active
//margin-top: 1px

.pg-first, .pg-last {
  display: none;

  span {
    @include padding-horizontal(2.6rem); }

  @include media('<mob') {
    display: none; } }

.pg-first, .pg-prev {
  span {
    padding-right: 0; } }

.pg-last, .pg-next {
  span {
    padding-left: 0; } }

.pg-list {
  &, .#{$article-content} &, .#{$typography} & {
    display: inline-block;
    vertical-align: middle;
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      margin: #{$pagination-vspace} 4px;
      padding: 0;

      @include media('<mob-s') {
        @include margin-horizontal(0); }

      &:before {
        content: none; } }

    li {
      @include display-inline-block-top;
      text-align: center;

      a, & > span {
        @include display-inline-block-top;
        color: inherit;
        background: transparent;
        min-width: $pagination-width;
        padding: 0 0.2rem;
        border-radius: 50%;

        @if $animation-use {
          transition: {
            property: color;
            duration: $animation-speed; } } }

      a {
        text-decoration: none;
        cursor: pointer;

        .no-touch &:hover {
          color: $base-color; } }

      &.active > span, &.active a, .no-touch &.active a:hover {
        color: white;
        background-color: $base-color;
        @if $pagination-border-width != 0 {
          border: {
            top-color: $base-color;
            bottom-color: $base-color; } } } } } }

// шрифт для элементов пагинации
.pg-first, .pg-last, .pg-prev, .pg-next, .pg-list a, .pg-list li > span {
  height: $pagination-height;
  @include font($pagination-font-size, #{$pagination-height - $pagination-border-width*2});
  letter-spacing: 0.05em;
  //text-transform: uppercase
  @if $pagination-border-width != 0 {
    border: #{$pagination-border-width} solid #{$border-color}; }

  @include media('<mob-s') {
    font-size: 14px; } }

.pg-list a, .pg-list li > span {
  letter-spacing: normal; }

/* pagination end */

/* images gallery */
.images-gallery {
  position: relative;
  padding: 2rem 0 4.4rem;
  margin-bottom: $bottom-space;

  @include media('<tab') {
    max-width: 60rem;
    @include push-auto; }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: -100rem;
    right: -100rem;
    height: 100%;
    background-color: white; }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden; }

  &.swiper-container {
    overflow: visible; }

  .swiper-wrapper {
    align-items: center; }

  .swiper-pagination {
    bottom: 1.8rem; }

  .swiper-pagination-bullet {
    background-color: $base-color;
    transform: scale(0.571) translateZ(0);
    @include size(14px); }

  .swiper-pagination-bullet-active {
    transform: scale(1) translateZ(0); }

  .images-gallery__slide {
    user-select: none;
    text-align: center; }

  .images-gallery__video {
    position: relative;

    &::before {
      content: "";
      display: block;
      @include ratio(16, 9); }

    iframe {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%; } } }

