/* description list */
.description-list {
  &, .#{$article-content} & {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0 #{nth($section-inner-space, 2)};
    margin: 0 auto;
    @include font(1.4rem, 1.8rem);
    letter-spacing: normal;
    color: $text-color-light;

    @include media('<lap-s') {
      padding: 0 #{nth($section-inner-space--tab, 2)}; }

    @include media('<mob') {
      display: block;
      //padding: 0 #{nth($section-inner-space--mob, 2)}
      padding: 0;
      @include font(1.6rem, 2.4rem); }

    li {
      position: relative;
      flex-shrink: 0;
      padding: 0 #{nth($section-inner-space, 2)};
      margin: 0 0 4.5rem;
      $width: 50.0%;
      flex-basis: $width;
      max-width: $width;

      @include media('<lap-s') {
        padding: 0 #{nth($section-inner-space--tab, 2)}; }

      @include media('<mob') {
        padding: 0;
        margin-bottom: 3rem;
        max-width: none; }

      &:nth-child(odd) {
        padding-left: 0; }

      &:nth-child(even) {
        padding-right: 0;

        &::after {
          content: "";
          position: absolute;
          left: 0;
          margin-left: -1px;
          top: 1rem;
          bottom: 0;
          border-left: 2px solid $border-color;

          @include media('<mob') {
            display: none; } } }

      &::before {
        content: none; }

      & > strong {
        $icon-size: 3.6rem;
        display: flex;
        align-items: center;
        position: relative;
        min-height: $icon-size;
        margin: 0 0 2rem;
        padding-left: 5.4rem;
        font-size: 1.4rem;
        line-height: 2.0rem;
        color: $title-color;

        @include media('<mob') {
          margin-bottom: 1rem;
          font-size: 1.6rem;
          line-height: 2.4rem; }

        & > i {
          display: inline-block;
          position: absolute;
          left: 0;
          top: 50%;
          @include size($icon-size);
          margin: -#{round-float($icon-size / 2, 1)} 0 0;

          path {
            fill: $base-color; } } }

      p {
        margin-bottom: 1.8rem;

        &:last-child {
          margin-bottom: 0; } } } } }

/* description list end */

/* buttons */
.buttons {
  padding: 1rem #{nth($box-inner-space, 2)};

  @include media('<tab-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.buttons__item {
  margin-top: 1.2rem;

  &:first-child {
    margin-top: 0; }

  [class*="btn-"] {
    width: 100%; } }

/* buttons end */

/* single form */
$single-form-label-width: 10.3rem;
.single-form {
  padding: 1rem #{nth($box-inner-space, 2)};
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.single-form__layout {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include media('<mob-s') {
    display: block; } }

.single-form__label {
  flex-grow: 1;
  padding-right: 0.5rem;
  margin: 0;
  width: $single-form-label-width;
  @include font-bold;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: $text-color;

  @include media('<mob-s') {
    display: inline-block;
    vertical-align: top;
    width: auto;
    padding-right: 0;
    margin-bottom: 1rem; } }

input[type="text"].single-form__input {
  flex-shrink: 0;
  $width: calc(100% - #{$single-form-label-width});
  flex-basis: $width;
  max-width: $width;
  padding: 10px 20px 9px 20px;
  height: 45px;
  margin: 0;

  @include media('<mob-s') {
    width: 100%;
    max-width: none; }

  .single-form_has-btn & {
    padding-right: 40px; } }

.single-form__send-btn {
  position: absolute;
  $size: 4.5rem 4.5rem;
  right: 0;
  top: 50%;
  height: nth($size, 1);
  width: nth($size, 2);
  margin-top: -#{round-float(nth($size, 2) / 2, 1)};
  padding: 0;
  color: $text-color;
  border: none;
  background: none;
  box-shadow: none;
  outline: none;
  // transition
  transition: color $animation-speed;

  [disabled] ~ & {
    pointer-events: none;
    opacity: 0.3; }

  @include media('<mob-s') {
    margin-top: 0;
    top: auto;
    bottom: 0; }

  &:hover {
    color: $base-color; }

  svg {
    $size: 1.4rem 2rem;
    position: absolute;
    left: 50%;
    top: 50%;
    height: nth($size, 1);
    width: nth($size, 2);
    margin: -#{round-float(nth($size, 1) / 2, 1)} 0 0 -#{round-float(nth($size, 2) / 2, 1)};

    path {
      fill: currentColor; } } }

.single-form__error-note,
.single-form__success-note {
  position: absolute;
  left: 0;
  top: 100%;
  height: auto;
  margin: 0.2rem 0 0;
  font-size: 1.2rem;
  line-height: 1.3rem;
  white-space: normal;
  text-align: left; }

/* single form end */

/* comment form */
.comment-form {
  padding: 2rem #{nth($box-inner-space, 2)};
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; }

  & > label {
    display: block;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $title-color;
    margin-bottom: 1.5rem; } }

/* comment form end */

/* rolls */
.rolls {
  margin-bottom: 4rem;

  &:last-child {
    margin-bottom: 0; } }

.rolls__item {
  border: 1px solid $border-color;
  border-radius: 4px;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; } }

.rolls__header {
  display: flex;
  align-items: center;
  position: relative;
  padding: 0.8rem #{nth($section-inner-space, 2)} 0.8rem 5rem;
  min-height: 5rem;
  @include font(1.6rem, 2rem);
  color: $text-color-light;

  &.rolls__hand-js {
    cursor: pointer; }

  @include media('<lap-s') {
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-right: #{nth($section-inner-space--mob, 2)}; }

  // Hover
  .no-touch &:hover {
    color: $base-color-light; } }

.rolls__angle {
  $element: &;
  $size: 2.4rem;
  position: absolute;
  height: $size;
  width: $size;
  left: 1.6rem;
  top: 50%;
  margin-top: -#{round-float($size / 2, 1)};
  @include font(0, 0);
  color: $base-color;
  border: 1px solid $border-color;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 50%;
  border-radius: $size;
  cursor: pointer;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: background-color, border-color, transform;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  @at-root {
    .rolls__angle-icon {
      $size: 1rem;
      position: absolute;
      top: 50%;
      left: 50%;
      height: $size;
      width: $size;
      margin: -#{round-float($size / 2, 1) - 0.1} 0 0 -#{round-float($size / 2, 1)};
      fill: $base-color; } }

  &:hover {
    .no-touch & {
      border-color: $base-color; } }

  .rolls-active & {
    transform: rotate(0.5turn); } }

.rolls__panel {
  padding: 0 #{nth($section-inner-space, 2)} 2rem;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.js .rolls:not(.ms-rolls_initialized) {
  .rolls__panel {
    display: none; } } // Важно! До инициализации скрипта, скрыть все панели, а после показать.

.rolls__panel-js {
  //opacity: 0
  opacity: 1;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .no-js & {
    opacity: 1; } }

/* rolls end */

