    pointer-events: none; }

  .product {
    background-color: white; } }

// arrows
$swiper-button-size: 14px, 20px;
.tape-slider__prev, .tape-slider__next {
  position: absolute;
  right: 0;
  top: 0;
  height: nth($swiper-button-size, 1);
  width: nth($swiper-button-size, 2);
  margin-top: 2.4rem;
  margin-right: #{nth($section-inner-space, 2)};
  will-change: transform;
  cursor: pointer;
  // transition
  $prop: color;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<lap-s') {
    margin-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    margin-right: #{nth($section-inner-space--mob, 2)}; }

  & > span {
    @include hide-text; }

