/* ============== HEADER ============== */
/* ==================================== */
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: $page-layout-min;
  height: $header-height;
  //max-height: $header-height
  z-index: 99;
  // transition
  $prop: box-shadow, transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<tab') {
    height: $header-height--tab; }

  &:not(.header-ready) {
    opacity: 0; }

  .no-js & {
    opacity: 1; }

  &.start-hidden {
    @include media('>=tab') {
      transform: translate(0, -100%) translateZ(0); } }

  &.header-changed {
    @include media('>=tab') {
      max-height: $header-height--scrolled; } }

  .page-scrolled & {
    box-shadow: 0 2px 5px rgba(black, 0.15); }

  &.header_float {
    position: relative;
    left: auto;
    top: auto; } }

.header-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: $base-color;

  // Add transition special for tablets width
  & {
    @include media('<tab') {
      $prop: background-color, opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: 0.12s;
        timing-function: ease-in-out; } } }

  .home-page & {
    opacity: 0;

    .page-scrolled & {
      opacity: 0.98; } } }

/* header-top */
.header-top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 9;
  // transition
  //$prop: transform
  //will-change: $prop
  //transition:
  //property: $prop
  //duration: $animation-speed

  @include media('<tab') {
    justify-content: flex-end;
    height: 100%; }

  .header-changed & {
    @include media('>=tab') {
      transform: translate(0, 1rem); } } }


.header-top__l {
  $width: calc(100% - 4rem - #{$header-top__r-width});
  width: $width;
  max-width: $width;

  @include media('<net') {
    $width: calc(100% - 2rem - #{$header-top__r-width--net});
    width: $width;
    max-width: $width; }

  @include media('<tab') {
    display: none; }

  // for ie 9
  //@if $ie9
  //.no-flexwrap &
  //  display: inline-block
 }  //  vertical-align: top

.header-top__r {
  width: $header-top__r-width;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;

  // for ie 9
  @if $ie9 {
    .no-flexwrap & {
      display: inline-block;
      vertical-align: top; } }

  @include media('<net') {
    width: $header-top__r-width--net; }

  @include media('<tab') {
    width: auto; }

  & > div {
    // for ie 9
    @if $ie9 {
      .no-flexwrap & {
        display: inline-block;
        vertical-align: top; } } } }

/* user options */
$uo-item-size: 4.3rem 5.4rem;
$uo-space: 3rem;
$uo-icon-size: 1.6rem;
$uo-border-radius: 7px;

.user-options {
  position: relative;
  display: flex;
  border-radius: 0 0 $uo-border-radius $uo-border-radius;
  background-color: white;
  box-shadow: 0 0 1px rgba(black, 0.15);

  @include media('<tab') {
    background: none;
    box-shadow: none;
    border-radius: 0; }

  .header-changed & {
    @include media('>=tab') {
      //border-radius: $uo-border-radius
      border-radius: 4rem; } } }

.user-options__item {
  position: relative;
  height: nth($uo-item-size, 1);
  min-width: nth($uo-item-size, 2);

  @include media('<tab') {
    height: auto;
    min-width: 0; }

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top; }

  // Vertical separators between items
  &::before, &::after {
    $size: 1.8rem 0.2rem;
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    height: nth($size, 1);
    width: round-float(nth($size, 2) / 2, 1);
    margin: -#{round-float(nth($size, 1) / 2, 1)} 0;
    background-color: #979797;
    opacity: 0.2;

    // Remove vertical separators between items
    @include media('<tab') {
      display: none; } }

  &::before {
    left: 0; }

  &::after {
    right: 0; }

  &:first-child::before,
  &:last-child::after {
    display: none; }

  & > a, & > div, & > span, & > strong {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 5px round-float($uo-space / 2, 1);
    color: #131313;
    text-decoration: none;
    text-align: center;
    cursor: default;
    user-select: none;
    //-webkit-tap-highlight-color: transparent
    // transition
    $prop: color, box-shadow, background-color;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    @include media('<tab') {
      height: $header-height--tab;
      padding: $header-btn-space--tab; }

    @include media('<mob') {
      padding: $header-btn-space--mob; }

    // tooltip
    &[data-title]::before {
      content: attr(data-title);
      display: block;
      min-width: 100%;
      padding: 0.4rem 0.8rem;
      position: absolute;
      left: 0;
      top: 100%;
      margin-top: 2px;
      @include font(1.2rem, 2rem);
      text-align: center;
      letter-spacing: normal;
      white-space: nowrap;
      z-index: 2;
      //color: white
      color: $text-color;
      @include antialiased;
      opacity: 0;
      visibility: hidden;
