  // hover
  &:hover {
    background-color: $base-color; } }

.quick-cart__empty {
  display: none;
  padding: 1.5rem 0; }

/* quick cart end */

/* header-common */
.header-common {
  display: flex;
  align-items: flex-start;
  position: relative;
  padding-top: 2.3rem;
  z-index: 5;
  @include clear;
  // transition
  //$prop: transform
  //will-change: $prop
  //transition:
  //property: $prop
