/* catalog opener */
a.catalog-opener {
  $btn-menu-lines-width: 18px;
  $btn-menu-lines-height: 2px;
  $btn-menu-lines-space: 3px;
  $btn-menu-lines-color: $base-color;
  $btn-menu-lines-color--mob: $base-color;
  $btn-menu-animation-speed: $animation-speed;
  $btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

  $user-options-cross-width: $btn-menu-lines-width;
  $user-options-cross-height: $btn-menu-lines-height;
  $user-options-cross-color: $btn-menu-lines-color;
  $user-options-cross-color--mob: $btn-menu-lines-color--mob;

  // =====================

  //display: none
  position: relative;
  padding: 0.8rem 1.5rem 0.8rem 5rem;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  color: #4a4a4a;
  text-decoration: none;
  border-radius: 2rem;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: color, background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<desk-s') {
    font-size: 1.6rem; }

  @include media('<lap-l') {
    font-size: 1.4rem; }

  @include media('<net', '>=tab') {
    padding-right: 1em;

    .header-changed & {
      @include size($header-btn-size);
      border-radius: $header-btn-size;
      padding: 0; } }

  @include media('<tab') {
    padding: 0;
    //height: 4rem
    //width: 4rem
    @include size($header-btn-size--tab);
    border-radius: $header-btn-size--tab;
    background-color: white; }

  @include media('<mob') {
    @include size($header-btn-size--mob);
    border-radius: $header-btn-size--mob; }

  // крест
  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 1.4rem;
    @include size($user-options-cross-width, $user-options-cross-height);
    background-color: $user-options-cross-color;
    margin-top: -#{round($user-options-cross-height / 2)};
    will-change: opacity, transform;
    opacity: 0;
    transition: {
      property: transform, opacity;
      duration: $btn-menu-animation-speed;
      delay: 0s;
      timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

    @include media('<net') {
      .header-changed & {
        left: 50%;
        margin-left: -#{round($user-options-cross-width / 2)}; } }

    @include media('<tab') {
      left: 50%;
      margin-left: -#{round($user-options-cross-width / 2)}; } }

  // бутерброд
  & > i {
    position: absolute;
    left: 1.4rem;
    top: 50%;
    @include size($btn-menu-lines-width, $btn-menu-lines-height);
    font-size: 0;
    line-height: 0;
    background-color: $btn-menu-lines-color;
    margin-top: -#{round($btn-menu-lines-height / 2)};
    backface-visibility: hidden;
    transition: {
      property: opacity;
      duration: $btn-menu-animation-speed;
      delay: $btn-menu-animation-delay; }

    @include media('<net') {
      .header-changed & {
        left: 50%;
        margin-left: -#{round($user-options-cross-width / 2)}; } }

    @include media('<tab') {
      left: 50%;
      margin-left: -#{round($btn-menu-lines-width / 2)}; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      @include size($btn-menu-lines-width, $btn-menu-lines-height);
      background-color: $btn-menu-lines-color;
      backface-visibility: hidden;
      transition: {
        property: transform;
        duration: $btn-menu-animation-speed;
        delay: $btn-menu-animation-delay; } }

    //first line menu
    &::before {
      top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

    //third line menu
    &::after {
      top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

  // text
  //& > span
  //@include media('<tab')
  //  display: none

  .catalog-opener__text-full {
    @include media('<net') {
      display: none; }

    .header-changed & {
      @include media('>=tab') {
        display: none; } } }

  .catalog-opener__text-short {
    @include media('<net') {
      .header-changed & {
        display: none; } }

    @include media('<tab') {
      display: none; }

    .header:not(.header-changed) & {
      @include media('>=net') {
        display: none; } } }

  // hover
  &:hover {
    color: $base-color; }

  // opened
  &.catalog-is-open {

    &::before, &::after {
      transition-delay: $btn-menu-animation-delay;
      opacity: 1; }

    &::before {
      transform: rotate(45deg) translateZ(0); }

    &::after {
      transform: rotate(-45deg) translateZ(0); }

    & > i {
      opacity: 0;
      transition-delay: 0s;

      &::before, &::after {
        transition-delay: 0s; }

      &::before {
        transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

      &::after {
        transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } }

  .search-is-open &, .header-changed & {
    @include media('>=tab') {
      color: white;

      &::before, &::after, & > i, & > i::before, & > i::after {
        background-color: currentColor; }

      &:hover {
        color: $base-color;
        background-color: white; } } } }

/* catalog opener end */

/* catalog shutter */
.catalog-shutter {
  position: fixed;
  left: 0;
  top: $header-height;
  width: 100%;
  height: calc(100% - #{$header-height});
  //background-color: rgba(black, 0.3)
  z-index: 9999;
  opacity: 0;
  pointer-events: none;
  user-select: none;
  $prop: transform, opacity, top;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .header-changed ~ & {
    @include media('>=tab') {
      top: $header-height--scrolled;
      height: calc(100% - #{$header-height--scrolled}); } }

  @include media('<tab') {
    top: $header-height--tab;
    height: calc(100% - #{$header-height--tab}); }

  &.catalog-is-open {
    opacity: 1;
    pointer-events: auto;
    user-select: auto; } }

.catalog-shutter__layout {
  height: 100%;
  padding-top: 1rem;
  padding-bottom: 1rem;

  @include media('<tab') {
    padding-top: 0; } }

.catalog-menu {
  max-height: 100%;
  //max-height: calc(100vh - #{$header-height} - 2rem)
  overflow: auto;
  margin-bottom: $bottom-space;
  background: white;
  box-shadow: 0 12px 10px rgba(0, 0, 0, 0.17);
  border-radius: 7px;
  transform-origin: 20rem top;
  $translate: 0, -0.5rem;
  $scale: 0.85, 0.85;
  transform: translate($translate) scale($scale);
  transform: translate3d($translate, 0) scale3d($scale, 1);
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: transform, opacity;
    duration: $animation-speed; }

  @include media('<mob') {
    $translate: 0, -0.5rem;
    $scale: 1, 1;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1); }

  .catalog-is-open & {
    $translate: 0, 0;
    $scale: 1, 1;
    transform: translate($translate);
    transform: translate3d($translate, 0); }

  &:last-child {
    margin-bottom: 0; } }

