/* search panel */
$search-form-size: 4rem 100%;
$search-form-size--mob: 5rem 100%;
$search-btn-size: 4rem;
$search-btn-size--tab: 4rem;
$search-btn-size--mob: 5rem;
$search-btn-space: 0 0.5rem;
.search-form {
  position: absolute;
  left: 0;
  top: 50%;
  height: nth($search-form-size, 1);
  width: nth($search-form-size, 2);
  margin-top: -#{round-float(nth($search-form-size, 1) / 2, 1)};
  padding: 0 #{nth($search-form-size, 1) + nth($search-btn-space, 2)} 0 0;
  z-index: 99;
  opacity: 0;
  visibility: hidden;

  @include media('>=mob', '<tab') {
    opacity: 1;
    visibility: visible;
    background-color: white;
    border-radius: nth($search-form-size, 1); }

  @include media('<tab') {
    padding-right: 0; }

  @include media('<mob') {
    position: fixed;
    top: $header-height--tab;
    left: 0;
    margin: 0;
    height: nth($search-form-size--mob, 1);
    width: nth($search-form-size--mob, 2);
    background-color: white;
    border-right-style: none;
    box-shadow: 0 2px 2px rgba(#000, 0.15);
    $translate: 0, -1.0rem;
    $scale: 1, 0.8;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    // transition
    $prop: opacity, visibility, transform;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; } }

  .search-is-open & {
    opacity: 1;
    visibility: visible;

    @include media('<mob') {
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1); } }

  .header-changed & {
    @include media('>=mob') {
      opacity: 1;
      visibility: visible;
      padding-right: 0; } }

  form {
    display: block;
    margin: 0;
    padding: 0;
    height: 100%; } }

.search-form__field {
  position: relative;
  margin: 0;
  z-index: auto;

  &::after {
    $loader-size: nth($search-form-size, 1);
    // ==================
    content: "";
    display: block;
    position: absolute;
    //right: $search-btn-size
    left: 0;
    top: 50%;
    @include size($loader-size);
    margin: -#{$loader-size / 2} 0 0;
    background: rgba(white, 0.65) url(../../img/preloader.svg) no-repeat center;
    background-size: contain;
    border-radius: $loader-size;
    pointer-events: none;
    z-index: 5;
    transition: opacity 0.15s;
    opacity: 0;

    .loading & {
      opacity: 1; } }

  .search-form__input {
    $font-size: 1.6rem;
    $line-height: 2.0rem;
    $ident-v: #{round-float((nth($search-form-size, 1) - $line-height) / 2, 1)};
    display: block;
    padding: #{$ident-v} #{$search-btn-size} #{$ident-v} #{$search-btn-size};
    width: 100%;
    height: nth($search-form-size, 1);
    color: #000;
    cursor: text;
    background-color: white;
    //box-shadow: inset 1px 1px 2px rgba(black, 0.6)
    box-shadow: none;
    border: 0;
    //border-radius: 0.5rem
    border-radius: nth($search-form-size, 1);
    font-family: inherit;
    font-size: $font-size;
    line-height: $line-height;

    @include media('>=mob', '<tab') {
      background: none; }

    @include media('<tab') {
      padding-right: $search-btn-size--tab; }

    @include media('<mob') {
      $ident-v: #{round-float((nth($search-form-size--mob, 1) - $line-height) / 2, 1)};
      padding: $ident-v #{$search-btn-size--mob} $ident-v #{$search-btn-size};
      height: nth($search-form-size--mob, 1); }

    //.header-changed &
    //border-radius: nth($search-form-size, 1)

    @include placeholder-focus {
      opacity: 1; }

    &:not([disabled]):not(.error):not(.success):not(.filled) {
      &.focused {
        box-shadow: none; } }

    &:not([disabled]):not(.error):not(.success) {
      &.filled {
        box-shadow: none; } } } }

// search panel buttons
.search-form__btn, .toggle-search {
  height: $search-btn-size;
  width: $search-btn-size;
  flex-shrink: 0;
  border-radius: $search-btn-size;
  margin: 0 0 0 nth($search-btn-space, 2);
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: background-color, color;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<tab') {
    @include size($search-btn-size--tab);
    border-radius: $search-btn-size--tab; }

  @include media('<mob') {
    @include size($search-btn-size--mob);
    border-radius: $search-btn-size--mob; }

  & > svg {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    $size: 2.4rem;
    @include size($size);
    margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)};
    pointer-events: none;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    @include media('<lap') {
      $size: 2.2rem;
      @include size($size);
      margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)}; }

    @include media('<mob') {
      $size: $header-btn-icon-size--mob;
      @include size($size);
      margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)}; }

    //+media('<mob')
    //$size: 1.8rem
    //@include size($size)
    //margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)}

    path {
      fill: currentColor; } }

  & > span {
    @include hide-text; } }

// search panel submit button
.search-form__btn {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -#{round-float($search-btn-size / 2, 1)};
  display: inline-block;
  vertical-align: top;
  background: none;
  color: $base-color;

  @include media('<tab') {
    margin-top: -#{round-float($search-btn-size--tab / 2, 1)};
    margin-left: 0; }

  @include media('<mob') {
    margin-top: -#{round-float($search-btn-size--mob / 2, 1)}; }

  // If header is change
  .header-changed & {
    background: none; }

  & > input {
    position: absolute;
    width: 100%;
    height: 100%;
    border: none;
    background: none;
    box-shadow: none;
    padding: 0;
    margin: 0;
    cursor: pointer; }

  & > svg {
    $size: 2rem;
    @include size($size);
    margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)};

    @include media('<tab') {
      $size: 2.2rem;
      @include size($size);
      margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)}; } }

  // hover
  &:hover {
    background-color: white;
    color: $base-color; } }

// search panel switcher button
.toggle-search {
  position: relative;
  display: block;
  background-color: white;
  color: $base-color;
  z-index: 99;

  @include media('<tab') {
    margin-left: 0; }

  @include media('<tab', '>=mob') {
    display: none; }

  @include media('<mob') {
    @include size($header-btn-size--mob);
    border-radius: $header-btn-size--mob; }

  // Крест для закрытии вместо иконки
  &::after {
    content: "";
    @include add-cross; }

  .search-is-open & {
    // Показать крест закрытия
    &::after {
      opacity: 1; }

    // Лупу спрятать
    svg {
      opacity: 0; } }

  .header-changed & {
    @include media('>=tab') {
      opacity: 0;
      visibility: hidden; } }

  // hover
  &:not(.search-is-open):hover {
    @include media('>=tab') {
      .no-touch & {
        background-color: $base-color;
        color: white; } } } }

/* search panel end */

/* search fast result */
.s-result {
  position: fixed;
  left: 0;
  width: 100%;
  z-index: 9999;
  opacity: 0;
  pointer-events: none;
  user-select: none;
  $prop: transform, opacity, top;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }
  top: $header-height;
  height: calc(100% - #{$header-height});

  .header-changed ~ & {
    @include media('>=tab') {
      top: $header-height--scrolled;
      height: calc(100% - #{$header-height--scrolled}); } }

  @include media('<tab') {
    top: $header-height--tab;
    height: calc(100% - #{$header-height--tab}); }

  @include media('<mob') {
    top: calc(#{$header-height--tab} + 6rem);
    height: calc(100% - #{$header-height--tab} - 6rem); }

  &.s-result_show {
    opacity: 1;
    pointer-events: auto;
    user-select: auto; }

  &__layout {
    height: 100%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    width: $flood-layout-width;
    max-width: 90%;

    @include media('<tab') {
      padding-top: 0; } }

  &__holder {
    max-height: 100%;
    //max-height: calc(100vh - #{$header-height} - 2rem)
    overflow: auto;
    margin-bottom: $bottom-space;
    background: white;
    box-shadow: 0 12px 10px rgba(0, 0, 0, 0.17);
    border-radius: 7px;
    transform-origin: 20rem top;
    $translate: 0, -0.5rem;
    $scale: 0.85, 0.85;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: transform, opacity;
      duration: $animation-speed; }

    @include media('<mob') {
      $translate: 0, -0.5rem;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1); }

    .s-result_show & {
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate);
      transform: translate3d($translate, 0); }

    &:last-child {
      margin-bottom: 0; } }

  &__content {
    display: block; }

  &__row,
  &__title {
    padding-left: 3rem;
    padding-right: 3rem;

    @include media('<lap-s') {
      $space: nth($section-inner-space--tab, 2);
      padding-left: $space;
      padding-right: $space; }

    @include media('<mob') {
      $space: nth($section-inner-space--mob, 2);
      padding-left: $space;
      padding-right: $space; } }

  &__title {
    padding-bottom: 1.5rem;
    padding-top: 1.5rem;
    @include font-bold;
    font-size: 2.4rem;
    line-height: 1.43;
    color: $title-color;
    background-color: $bg-color-alt;
    border-bottom: 1px solid $border-color;

    @include media('<mob') {
      font-size: 2.0rem; } }

  &__row {
    padding-bottom: 1.5rem;
    padding-top: 1.5rem;
    border-bottom: 1px solid $border-color;

    &:last-child {
      border-bottom: none; }

    .float-list {
      margin-top: -1.2rem;
      font-size: inherit;
      line-height: inherit; } }

  &__list {
    position: relative;

    &-item {
      position: relative;
      padding: 1.5rem 0;
      border-top: 1px solid $border-color;

      &:first-child {
        border: none; } }

    &-inner {
      text-decoration: none;
      color: inherit;
      display: flex;
      align-items: center; }

    &-figure {
      float: left;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      $width: 11.8rem;
      flex-basis: $width;
      max-width: $width;
      width: $width;
      height: 7.8rem;
      margin-right: 2.0rem;

      @include media('<mob') {
        $width: 9rem;
        flex-basis: $width;
        max-width: $width;
        width: $width;
        margin-right: #{nth($page-layout-space--mob, 2)}; }

      img {
        display: block;
        max-width: 100%;
        max-height: 100%; } }

    &-content {
      overflow: hidden; }

    &-title {
      display: block;
      margin-bottom: 1rem;
      @include font-normal;
      font-weight: normal;
      color: $title-color;
      transition: color $animation-speed; }

    &-count {
      display: inline-block;
      color: $text-color-ultra-light;
      margin-right: 0.5rem; }

    &-price {
      display: inline-block;
      @include font-bold;
      color: $text-color; }

    &-inner:hover {
      [class*="-title"] {
        color: $link-color; } } } }

/* search fast result end */

