    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    $space: nth($footer-grid-space, 2);
    margin-left: -#{$space};
    margin-right: -#{round-float($space / 2, 1)};

    @include media('<lap-s') {
      $space: nth($footer-grid-space--tab, 2);
      margin-left: -#{$space};
      margin-right: -#{round-float($space / 2, 1)}; }

    @include media('<mob') {
      display: block; } }

  &__col {
    text-align: center;
    $space: nth($footer-grid-space, 2);
    padding-left: $space;
    padding-right: round-float($space / 2, 1);

    @include media('<lap-s') {
      $space: nth($footer-grid-space--tab, 2);
      padding-left: $space;
      padding-right: round-float($space / 2, 1); }

    @include media('<mob') {
      margin-top: 2rem; }

    &:first-child {
      flex-shrink: 0;
      text-align: left;

      @include media('<mob') {
        text-align: center;
        margin-top: 0; } }

    &:last-child {
      flex-shrink: 0;
      $width: 25%;
      flex-basis: $width;
      max-width: $width;
      text-align: left;

      @include media('<tab') {
        $width: 30%;
        flex-basis: $width;
        max-width: $width; }

      @include media('<tab-s') {
        flex-basis: auto;
        max-width: none; }

      @include media('<mob') {
        text-align: center; } }

    a {
      color: inherit;
      text-decoration: none;

      .no-touch &:hover {
        text-decoration: underline; } } } }

/* footer bottom end */

/* license */
.license {
  color: rgba(#848484, 0.6); }

/* license end */

/* developer */
.developer {
  display: inline-block;
  line-height: 1;

  a {
    text-decoration: none;
    color: #fff; } }

.developer__logo {
  display: block;
  position: relative;
  margin-bottom: 0.2rem;
  width: 16rem;
  font-size: 0;
  line-height: 0;

  @include media('<tab-s') {
    width: 12rem; }

  &::before {
    content: "";
    display: block;
    @include ratio(247, 84); }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: url("../../img/logo-dev.png") no-repeat center center;
    background-size: contain; } }

.developer__label {
  font-size: 1.6rem;
  line-height: 2.4rem;
  letter-spacing: 0.14em;

  @include media('<tab-s') {
    font-size: 1.3rem;
    line-height: 2rem;
    letter-spacing: 0.08em; }

  a {
    box-shadow: inset 0 0 0 currentColor;
    text-decoration: none;
    color: currentColor; } }

// Hover
.developer {
  .developer__label {
    white-space: nowrap;

    a {
      text-decoration: none;
      transition: box-shadow $animation-speed;

      &:hover {
        text-decoration: none;
        box-shadow: inset 0 -1px 0 currentColor; } } }

  .developer__logo {
    &::after {
      transition: transform $animation-speed; }

    &:hover {
      &::after {
        $translate: 0, -0.5rem;
        transform: translate($translate);
        transform: translate3d($translate, 0); } } } }

/* developer end */

/* ==================================== */
/* ============= CONTENT ============== */
/* ==================================== */
.main {
  //flex-shrink: 1
  //flex-grow: 1
  position: relative;
  clear: both;
  padding-top: $header-height + 2;

  @include media('<tab') {
    padding-top: $header-height--tab + 2; }

  .header_float ~ & {
    padding-top: 1.4rem; }

  //+media('<lap-s')
  //padding-top: $header-height--sm

  .home-page & {
    padding-top: 0;

    @include media('<mob') {
      padding-bottom: nth($page-layout-space--mob, 1); } }

  .no-js & {
    padding-top: 2rem; } }

/* main heading */
.main-heading {
  position: relative;
  padding: 2.1rem #{nth($section-inner-space, 2)} 0.6rem;
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.main-heading__title {
  margin-bottom: 0;
  margin-right: 2rem; }

/* main heading end */

/* main-section */
.main-section {
  position: relative;
  padding-top: 2rem;
  padding-bottom: 2rem;

  @include media('<mob') {
    $space: round-float(nth($page-layout-space--mob, 1) / 2, 1);
    padding-bottom: $space;
    padding-top: $space; } }

.main-section__title {
  @include font-bold;
  font-size: $main-section-title-font-size;
  line-height: $main-section-title-line-height;
  color: #131313;
  margin-bottom: 0.56em;

  @include media('<lap-s') {
    margin-right: 0;
    font-size: $main-section-title-font-size--tab;
    line-height: $main-section-title-line-height--tab; }

  @include media('<mob') {
    margin-bottom: 0.5em;
    font-size: $main-section-title-font-size--mob;
    line-height: $main-section-title-line-height--mob; }

  &:last-child {
    margin-bottom: 0; } }

.mark-anchor {
  position: absolute;
  left: 0;
  top: -#{$header-height};
  height: 0;
  font-size: 0;
  line-height: 0;
  pointer-events: none;
  overflow: hidden;
  user-select: none; }

/* main-section end */

/* section */
.section {
  position: relative;
  margin-bottom: 2.5rem;

  &__title {
    @include font-bold;
    font-size: 2.4rem;
    line-height: 3.2rem;
    color: #131313;
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0; } }

  &__item {
    margin-bottom: 3rem; } }

/* section end */

/* page with sidebar */
.content {
  overflow: hidden; // Нужен для обрезки выходящих за пределы контента элементов. Например слайдера на внутренней странице.
  position: relative;
  width: 100%;
  background-color: white;
  border-radius: 5px;
  margin-bottom: 2rem;
  @include clear; }

.content-align {
  float: left;
  width: 100%;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: $sidebar-width;
    height: 100%;
    width: 0;
    border-right: 1px solid $border-color;

    @include media('<lap-s') {
      left: $sidebar-min-width; }

    @include media('<tab') {
      display: none; } } }

.content-inner {
  margin: 0 #{$aside-width} 0 #{$sidebar-width};
  min-height: 32rem;

  @include media('<lap') {
    margin: 0 #{$aside-min-width} 0 #{$sidebar-min-width}; }

  @include media('<lap-s') {
    margin-right: $aside-min-width--net; }

  @include media('<tab') {
    margin: 0; } }

.sidebar {
  position: relative;
  display: inline;
  float: left;
  margin: 0 0 0 -100%;
  width: $sidebar-width;
  background-color: white;
  border-radius: 5px;
  z-index: 1;

  @include media('<lap-s') {
    width: $sidebar-min-width; }

  @include media('<tab') {
    display: none; } }

// article nav
.articles-nav {
  &, & ul {
    list-style: none;
    margin: 0;
    padding: 0;
    @include font(1.4rem, 1.8rem);
    color: $text-color-light; }

  ul {
    display: none;
    padding-top: 1.2rem; }

  li {
    margin-top: 1.2rem;
    padding-left: 2rem;
    cursor: default;

    &.current {
      & > ul {
        display: block; }

      & > a, & > span {
        @include font-bold; }

      & > span {
        color: $base-color-light; } } }

  $articles-nav-first-inner-space: 3rem 4rem;
  $articles-nav-first-inner-space--net: 2.5rem 3rem;
  & > li {
    border-top: 1px solid $border-color;
    padding: 0;
    margin: 0;

    &:first-child {
      border-top: none; }

    & > a, & > span {
      display: block;
      padding: $articles-nav-first-inner-space;
      @include font(1.6rem, 2.4rem);
      color: #000;

      @include media('<lap-s') {
        padding: $articles-nav-first-inner-space--net; } }

    & > ul {
      padding: 0 nth($articles-nav-first-inner-space, 2) nth($articles-nav-first-inner-space, 1);

      @include media('<lap-s') {
        padding: 0 nth($articles-nav-first-inner-space--net, 2) nth($articles-nav-first-inner-space--net, 1); }

      & > li {
        position: relative;
        padding-left: 2.4rem;
        margin-top: 1.3rem;

        &:first-child {
          margin-top: 0; }

        &::before {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          top: 0.5em;
          width: 1.4rem;
          height: 0.2rem;
          background-color: $base-color-light; } } } }

  a {
    color: inherit;
    text-decoration: none;
    transition: color $animation-speed;

    &:hover {
      color: $base-color; } } }

/* page with sidebar end */

/* article share */
.article-share-layout {
  position: absolute;
  right: 5rem;
  top: 5rem;
  z-index: 3;

  @include media('<lap-s') {
    right: 3rem;
    top: 3rem; }

  @include media('<tab') {
    position: relative;
    right: auto;
    top: auto;
    left: auto;
    bottom: auto;
    overflow: hidden;
    padding: $section-inner-space--tab; } }

.article-share {
  display: inline-block;
  vertical-align: top;
  margin-left: auto;
  padding: 2rem;
  border-radius: 4px;
  background-color: rgba(white, 0.98);

  @include media('<tab') {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: -1rem; } }

.article-share__item {
  display: block;
  margin-bottom: 4rem;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-align: left;
  color: $text-color-ultra-light;
  text-decoration: none;
  transition: color $animation-speed;

  @include media('<tab') {
    margin: 1rem; }

  &:last-child {
    @include media('>=tab') {
      margin-bottom: 0; } }

  &:hover {
    color: $base-color-light; }

  & > span {
    @include hide-text; }

  svg {
    $size: 2rem;
    display: inline-block;
    vertical-align: top;
    height: $size;
    width: $size; }

  path {
    fill: currentColor; } }

/* article share end */

/* product stickers */
// --> single sticker <--
$p-sticker-size: 2.6rem 2.6rem;
$p-sticker-size--mob: 1.8rem 1.8rem;
.p-sticker {
  flex-shrink: 0;
  height: nth($p-sticker-size, 1);
  width: nth($p-sticker-size, 2);
  padding: 1px 1rem 0;
  font-size: 1.2rem;
  line-height: nth($p-sticker-size, 1);
  text-align: center;
  color: white;
  overflow: hidden;
  text-indent: -9999px;
  background-color: $base-color-light;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: $p-sticker-size;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  @include media('<mob') {
    height: nth($p-sticker-size--mob, 1);
    width: nth($p-sticker-size--mob, 2);
    border-radius: $p-sticker-size--mob;
    padding: 0.1rem 0.6rem 0;
    font-size: 1rem;
    line-height: nth($p-sticker-size--mob, 1); }

  & > b {
    @include font-bold; }

  & > strong {
    @include font-extra-bold; } }

// --> type wow <--
.p-sticker_wow {
  $p-sticker-size: 1.7rem 5.2rem;
  height: nth($p-sticker-size, 1);
  width: nth($p-sticker-size, 2);
