  background-color: transparent;
  background-size: contain;
  pointer-events: none;

  @include media('<mob') {
    $p-sticker-size: 1.3rem 3.7rem;
    height: nth($p-sticker-size, 1);
    width: nth($p-sticker-size, 2);
    background-size: contain; } }

// --> type sale <--
.p-sticker_sale {
  background-color: $base-color-light;
  background-image: url(../../img/sticker-sale.svg);

  @include media('<mob') {
    background-size: 1.2rem; } }

// --> type new <--
.p-sticker_new {
  background-color: $base-color-light;
  background-image: url(../../img/sticker-new.svg);

  @include media('<mob') {
    background-size: 1.4rem; } }

// --> type gif <--
.p-sticker_gif {
  background-color: $base-color-light;
  background-image: url(../../img/sticker-gift.svg);

  @include media('<mob') {
    background-size: 1.4rem; } }

// --> type top <--
.p-sticker_top {
  background-color: $mark-color-alt;
  background-image: url(../../img/sticker-top.svg);

  @include media('<mob') {
    background-size: 1.4rem; } }

// --> type text <--
.p-sticker_text {
  width: auto;
  text-indent: 0;
  cursor: default;
  @include truncate(12rem); }

// --> type installment <--
.p-sticker_installment {
  background-color: $mark-color; }

// --> type exclusive <--
.p-sticker_exclusive {
  background-color: $mark-color; }

// --> type last <--
.p-sticker_last {
  background-color: $base-color-light; }

// --> type premium <--
.p-sticker_premium {
  color: black;
  background-color: $base-color-alt; }

// --> stickers list <--
.p-stickers {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start; }

// --> stickers list item <--
.p-stickers__item {
  margin: 0 0.6rem 0.6rem 0; }

/* product stickers end */

/* dl-list */
.dl-list {
  &, .#{$article-content} & {
    $dd-width: 20rem;
    $dl-space: 0.5rem;
    list-style: none;
    margin: 0 0 $bottom-space;
    padding: 0;

    &:last-child {
      margin-bottom: 0; }

    li {
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin: 0.5rem -#{$dl-space};
      padding: 0;
      cursor: default;
      background-color: white;
      // Цвет фона можно перестилить через родителя при необходимости

      @include media('<mob-s') {
        display: block;
        margin-left: 0;
        margin-right: 0.4rem; }

      &:before {
        content: none; }

      & > div {
        position: relative;
        display: inline-block;
        vertical-align: top;
        padding: 0 $dl-space;

        @include media('<mob-s') {
          display: inline;
          padding: 0; }

        &:first-child {
          max-width: calc(100% - #{$dd-width});

          @include media('<mob-s') {
            max-width: none; }

          &::after {
            content: "";
            display: block;
            position: absolute;
            left: 100%;
            bottom: 0.45em;
            width: 9999px;
            border-top: 1px dotted currentColor;

            @include media('<mob-s') {
              display: inline;
              content: ":";
              border: none;
              position: static;
              width: auto;
              left: auto;
              bottom: auto; } } }

        &:last-child {
          max-width: #{$dd-width - 1};
          background-color: inherit;
          text-align: right;

          @include media('<mob-s') {
            text-align: left;
            max-width: none;
            @include font-bold; } } }

      // hover
      &:hover {
        .no-touch & {
          background-color: #f1f1f1; } } } } }

/* dl-list end */

/* grid js */
.grid-js {
  &:not(.grid-completed) {
    overflow: hidden;
    white-space: nowrap; } }

/* grid js end */

/* promo slider */
.main-section_promo {
  height: 57.6rem;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: -#{$promo-section-bottom};
  background-color: $base-color-light;

