/* advise */
$advise-figure-size: 9.2rem 9.2rem;
$advise-figure-size--net: 6.4rem 6.4rem;
$advise-figure-space: 2rem;
$advise-figure-space--net: 1rem;
$advise-content-space: nth($advise-figure-size, 2) + $advise-figure-space;
$advise-content-space--net: nth($advise-figure-size--net, 2) + $advise-figure-space--net;
.advise {
  position: relative;
  //overflow: hidden

  .m-aside & {
    padding-right: 1px; } }

.advise__contact {
  position: relative;
  padding: 3.7rem 1.6rem;
  background-color: white;
  box-shadow: 0 0 3px rgba(black, 0.17);
  z-index: 1;
  @include clear;

  @include media('<mob') {
    padding: 2.5rem #{nth($section-inner-space--mob, 2)}; } }

.advise__figure {
