    border-width: 0.3rem; }

  img {
    display: block;
    border-radius: round-float(nth($advise-figure-size, 1) / 2, 1);
    width: 100%;
    height: 100%;
    max-width: none;

    @include media('<lap-s') {
      border-radius: round-float(nth($advise-figure-size--net, 1) / 2, 1); } } }

.advise__name {
  display: block;
  margin-left: $advise-content-space;
  @include font-bold;
  font-size: 1.6rem;
  line-height: 2.0rem;
  color: #000;
  margin-bottom: 0.2rem;

  @include media('<lap-s') {
