    &.chat-telegram {
      background-position: -51px -3px; }

    &.chat-whatsapp {
      background-position: -102px -3px; } } }

.advise__callback {
  display: block;
  text-decoration: none;
  @include font-bold;
  font-size: 1.7rem;
  line-height: 2.5rem;
  text-align: center;
  background-color: #ffe99c;
  padding: 1rem;
  color: #000;
  // transition
  $prop: background-color;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<tab') {
    padding: 0.8rem; }

  @include media('<mob') {
    font-size: 1.8rem;
    line-height: 2.0rem; }

  & > span {
    display: block;
    padding: 2rem 2.5rem;
    background-color: white;
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px #efefef;

