  &:hover {
    background-color: $base-color; } }

// advise_small
$advise-figure-size--small: 6.4rem 6.4rem;
$advise-figure-space--small: 2rem;
$advise-content-space--small: nth($advise-figure-size--small, 2) + $advise-figure-space--small;
.advise_small {
  .advise__contact {
    padding: 2.0rem 1.6rem;

    @include media('<mob') {
      padding: 1.5rem #{nth($page-layout-space--mob, 2)}; } }

  .advise__figure {
    margin-right: $advise-figure-space--small;
    margin-top: -0.4rem;
    height: nth($advise-figure-size--small, 1);
    width: nth($advise-figure-size--small, 2);
    border-radius: round-float(nth($advise-figure-size--small, 1) / 2, 1);
    border-width: 3px;

    img {
      border-radius: round-float(nth($advise-figure-size--small, 1) / 2, 1); } }

  .advise__name, .advise__past, .advise-chat {
    margin-left: $advise-content-space--small; }

  .advise-chat__list {
    &, .#{$article-content} & {
      margin: 0 -0.2rem -1rem; }

    a {
      $size: 2.4rem;
      height: $size;
      width: $size;
      background-size: 103px;

      &.chat-viber {
        background-position: -5px -4px; }

      &.chat-telegram {
        background-position: -38px -4px; }

      &.chat-whatsapp {
        background-position: -73px -4px; } } }

  .advise__callback {
    font-size: 1.4rem;
    line-height: 2.0rem;
    padding: 0.8rem;

    @include media('<mob') {
      font-size: 1.8rem; }

    & > span {
      padding: 1.5rem 2rem; } } }

// advise form
.advise-callback {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  padding: 2.4rem 2rem 2rem;
  background-color: white;
  border: 1px solid #f5f5f5;
  box-shadow: 0 5px 11px rgba(0, 0, 0, 0.08);
  pointer-events: none;
  opacity: 0;
  z-index: 99;
  $scale: 0.85, 0.85;
  transform: scale($scale);
  transform: scale3d($scale, 1);
  transform-origin: right bottom;
  // transition
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  @include media('<mob') {
    padding: 3.4rem #{nth($section-inner-space--mob, 2)} #{nth($section-inner-space--mob, 2)};
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15); }

  // opened
  &.advise-callback_open {
    pointer-events: auto;
    opacity: 1;
    $scale: 1, 1;
    transform: scale($scale);
    transform: scale3d($scale, 1); } }

.advise-callback__form-input {
  &:last-child {
    margin-bottom: 0; } }

.advise-callback__form-input_btn {
  margin-top: 2.4rem; }

.advise-callback-form__btn {
  display: block;
  width: 100%; }

.advise-callback-close {
  $size: 1.8rem;
  position: absolute;
  right: 0.3rem;
  top: 0.3rem;
  width: $size;
  height: $size;
  color: white;
  background-color: #cecece;
  border-radius: round-float($size / 2, 1);
  text-indent: -9999px;
  overflow: hidden;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  transform: rotate(-90deg) translateZ(0);
  transition: background-color $animation-speed;

  @include media('<tab', '>=mob') {
    .no-touch .m-aside__align:hover & {
      right: 2rem; } }

  @include media('<mob') {
    $size: 2.4rem;
    width: $size;
    height: $size;
    border-radius: $size; }

  svg {
    display: block;
    $size: 0.8rem;
    @include size($size);
    position: absolute;
    left: 50%;
    top: 50%;
    $space: round-float($size / 2, 1);
    margin: -#{$space} 0 0 -#{$space};

    @include media('<mob') {
      $size: 1.4rem;
      @include size($size);
      $space: round-float($size / 2, 1);
      margin: -#{$space} 0 0 -#{$space}; } }

  path {
    fill: currentColor; }

  // hover
  &:hover {
    background-color: $base-color; } }

/* advise end */

/* game-zone-anchor */
.game-zone-anchor {
