$ql-item-space: 1.2rem;
$ql-columns: 5;
$ql-columns--lap: 4;
$ql-columns--tab: 3;
$ql-columns--mob: 1;
.main-section_quick-links {
  position: relative;
  padding-top: 0;
  padding-bottom: 2rem;
  z-index: 5;

  @include media('<mob') {
    $space: round-float(nth($page-layout-space--mob, 1) / 2, 1);
    padding-bottom: $space;
    padding-top: $space; }

  //opacity: 0.2
 }  //pointer-events: none

//.quick-links
//margin-bottom: $bottom-space

//&:last-child
//  margin-bottom: 0

.quick-links__list {
