/* categories */
$categories-item-col: 3;
.categories {
  position: relative; }

.categories__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0; }

.categories__item {
  position: relative;
  $width: round-float(100% / $categories-item-col - 0.01, 2);
  flex-shrink: 0;
  flex-basis: $width;
  max-width: $width;
  width: $width;

  &::before {
    content: "";
    display: block;
    position: relative;
    @include ratio(1, 1); }

  &:hover {
    .no-touch & {
      z-index: 1; } } }

.categories__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 0.8em;
  @include font-bold;
  @include font(1.6rem, 2.4rem);
  text-align: center;
  text-decoration: none;
  color: inherit;
  background-color: #fff;
  box-shadow: 0 0 0 1px $border-color-light;
  // transition
  $prop: color, box-shadow, background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<mob') {
    font-size: 1.4rem;
    line-height: 1.8rem;
    padding: 0.6em; }

  @include media('<mob-xs') {
    font-size: 1.2rem;
    line-height: 1.6rem; }

  &-mob-icon {
    display: none;

    @include media("<tab-s") {
      display: block;
      margin: 0 auto -0.2rem;
      @include font-extra-bold;
      font-size: 3.6rem;
      line-height: 1; } }

  svg {
    display: block;
    margin-bottom: 0.5rem;

    @include media("<tab-s") {
      display: none; }

    &, path {
      fill: currentColor; }

    rect {
      stroke: currentColor; } }

  span {
    display: block;
    max-height: 1.6rem * 3;
    overflow: hidden;

    @include media('<mob') {
      max-height: 1.8rem * 3; }

    @include media('<mob-xs') {
      max-height: 1.6rem * 3; } }

  &:hover {
    .no-touch & {
      box-shadow: 0 0 0 1px $base-color-light, inset 0 0 0 1px $base-color-light;
      color: $base-color; } } }

