  @include media('<tab') {
    max-width: 32rem; } }

// topics thumbs select
.topics-thumbs-select {
  @include pseudo-select-common($base-color-light);

  @include media('>=tab') {
    display: none; } }

// topics thumbs
.topics-thumbs {
  display: flex;
  flex-wrap: wrap;
  font-size: 1.6rem;
  line-height: 2.4rem;

  @include media('<tab') {
    display: block; // Not flex
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 24rem;
    background-color: white;
    border-radius: 1.2rem;
    box-shadow: 0 1px 2px rgba(black, 0.15);
    z-index: 9;
    // hide
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: left top;
    $translate: -0.2rem, -0.2rem;
    $scale: 0.8, 0.8;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    // transition
    $prop: transform, opacity, visibility;
    will-change: $prop;
