/* rate */
.p-rate {
  $size: 12px 86px;
  // размер звездочного рейтинга
  $width: nth($size, 2);
  // ширина звездочного рейтинга
  $star-width: 12px;
  // ширина одной звезды
  $space: round(($width - $star-width * 5) / 8);
  // расчетное значение отступов по бокам
  $width-full: $width + $space * 2;
  // расчетное значение полной ширины звездочного рейтинга
  width: $width-full;
  margin-left: -$space;
  background: url(../../img/rate-l-empty.svg) no-repeat center;
  background-size: nth($size, 2) nth($size, 1);

  & > span {
    display: block;
    width: $width-full;
    height: nth($size, 1);
    background: url(../../img/rate-l-full.svg) no-repeat $space center;
    background-size: $width;
    text-indent: -9999px;
    overflow: hidden; } }

.p-rate_lg {
  $size: 16px 129px;
  // размер звездочного рейтинга
  $width: nth($size, 2);
  // ширина звездочного рейтинга
  $star-width: 17px;
  // ширина одной звезды
  $space: round(($width - $star-width * 5) / 8);
  // расчетное значение отступов по бокам
  $width-full: $width + $space * 2;
  // расчетное значение полной ширины звездочного рейтинга
  width: $width-full;
  margin-left: -$space;
  background: url(../../img/rate-xl-empty.svg) no-repeat center;
  background-size: nth($size, 2) nth($size, 1);

  & > span {
    display: block;
    width: $width-full;
    height: nth($size, 1);
    background: url(../../img/rate-xl-full.svg) no-repeat $space center;
    background-size: $width;
    text-indent: -9999px;
    overflow: hidden; } }

/* rate end */

/* stock */
.p-stock {
  position: relative;
  display: inline-block;
  padding: 0.3rem 0 0.3rem 2.8rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: #000;

  & > i {
    $size: 1.8rem;
    position: absolute;
    left: 0;
    top: 50%;
    width: $size;
    height: $size;
    border: 1px solid $base-color;
    border-radius: round-float($size / 2, 1);
    transform: translate(0, -50%);

    &::before {
      $size: 0.5rem 0.8rem;
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      height: nth($size, 1);
      width: nth($size, 2);
      margin: -#{round-float(nth($size, 1)/2, 1) + 0.1} 0 0 -#{round-float(nth($size, 2)/2, 1)};
      border-style: solid;
      border-color: inherit;
      border-width: 0 0 1px 1px;
      transform: rotate(-45deg); } }

  // off
  &.off {
    color: $text-color-ultra-light;

    & > i {
      border-color: currentColor;

      &::before {
        transform: rotate(0deg);
        height: 0;
        margin-top: 0; } } } }

