/* flip product */
.flip-product {
  $el: &;
  $dur: 0.4s;
  $del: 0.1s;

  display: block;
  user-select: none;
  text-decoration: none;
  color: inherit;
  -webkit-tap-highlight-color: transparent;
  perspective: 1200px;

  .topics__item &,
  .products__item &,
  .tape-slider__item & {
    height: 100%; }

  &-frame {
    display: block;
    position: relative;
    transform-style: preserve-3d;
    transform: rotateY(0);
    transition: transform $dur linear $del;

    .topics__item &,
    .products__item &,
    .tape-slider__item & {
      height: 100%; }

    @at-root {
      #{$el}.active & {
        transform: rotateY(180deg); } } }

  &-card {
    width: 100%;
    height: 100%;
    transition: opacity 0.1s linear #{$del + round-float($dur / 2, 1)};

    &_front {
      background-color: #fff;
      color: #124989;
      z-index: 1;

      @at-root {
        #{$el}.active & {
          opacity: 0;
          pointer-events: none; } } }

    &_back {
      position: absolute;
      left: 0;
      top: 0;
      font-size: 18px;
      line-height: 1.5;
      background-color: #fff;
      transform: rotateY(-180deg);
      overflow: hidden;
      display: flex;
      align-items: center;
      opacity: 0;
      pointer-events: none;

      @at-root {
        #{$el}.active & {
          opacity: 1;
          pointer-events: auto; } } }

    &_mark {
      background-color: $base-color-alt;
      color: #000; }

    &__alight {
      width: 100%;
      padding: 1.5rem 2rem; } } }

