// === list view ===
// Перестиливание элементов превью товара для отображения в виде списка (детальный вид)
[data-view="list-view"] {
  $product-item-space: 3.2rem 2.6rem;
  $product-item-space--tab: $section-inner-space--tab;
  $product-item-space--mob: $section-inner-space--mob;
  $offer-width: 23.6rem;

  // === flip product ===
  .flip-product-frame {
    transform: rotateX(0); }

  .flip-product:hover .flip-product-frame {
    transform: rotateX(180deg); }

  .flip-product-card_back {
    transform: rotateX(-180deg); }

  // === product ===
  .product {
    flex-direction: row;

    @include media('<mob') {
      display: block; } }

  // === product main ===
  .product__main {
    position: relative;
    $width: calc(100% - #{$offer-width});
    width: $width;
    min-height: 0;
    flex-basis: $width;
    //flex-grow: 1
    margin: 0;
    padding: nth($product-item-space, 1) nth($product-item-space, 2);

    @include media('<net') {
      padding-bottom: nth($product-item-space, 1) + 4; } // Снизу нужно добавить отступ для кнопок "сравнить" и "в избранное"

    @include media('<lap-s') {
      padding: #{nth($product-item-space--tab, 1)} #{nth($product-item-space--tab, 2)} #{nth($product-item-space--tab, 1) + 4}; } // Снизу нужно добавить отступ для кнопок "сравнить" и "в избранное"

    @include media('<mob') {
      width: auto;
      padding: #{nth($product-item-space--mob, 1)} #{nth($product-item-space--mob, 2)} #{nth($product-item-space--mob, 1)}; }

    & > a {
      display: inline;
      margin: 0;

      @include media('<net') {
        display: block; } } }

  // === [product align left] ===
  .product__figure {
    float: left;
    width: 50%;
    padding-right: round-float(nth($product-item-space, 2) / 2, 1);

    @include media('<net') {
      float: none;
      width: auto;
      padding-right: 0; } }

  // === product figure ===
  .product__figure {
    height: 22.8rem;
    margin-bottom: 3rem;
    // Под изображением на абсолюте прикреплены кнопки сравнения и добавления в избранное

    @include media('<net') {
      height: 18rem;
      margin-bottom: 2rem; } }

  // === [product align right] ===
  .product__stickers, .product__title, .product__meta, .products__features {
    float: right;
    clear: right;
    width: 50%;
    padding-left: round-float(nth($product-item-space, 2) / 2, 1);

    @include media('<net') {
      float: none;
      width: auto;
      padding-left: 0; } }

  // === product stickers ===
  // --> sticker wow <--
  .product__sticker_wow {
    $space: 3.5rem 1.8rem;
    right: nth($space, 2);
    top: nth($space, 1);

    @include media('<mob') {
      top: 2.2rem; } }

  // --> stickers <--
  .product__stickers {
    position: relative;
    left: auto;
    top: auto;
    right: auto;
    margin-bottom: 1rem; }

  // === product title ===
  .product__title {
    margin-bottom: 0.6rem;

    @include media('<mob') {
      font-size: 1.8rem;
      line-height: 2.4rem; } }

  // === product rating ===
  .product__rate {
    margin-bottom: 1.9rem; }

  // === product meta (container) ===
  .product__meta {
    margin-bottom: 1.1rem; }

  // === products features ===
  .products__features {
    //display: block
    padding: 0 0 0 1.2rem;
    font-size: 1.4rem;
    line-height: 1.8rem;

    @include media('<net') {
      padding: 0; } }

  // === product footer ===
  .product__footer {
    $width: $offer-width;
    width: $width;
    flex-basis: $width;
    //flex-grow: 1
    margin: 0;
    padding: nth($product-item-space, 1) 0;

    @include media('<lap-s') {
      padding: nth($product-item-space--tab, 1) 0; }

    @include media('<mob') {
      width: auto;
      padding-top: 0; }

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: nth($product-item-space, 1);
      bottom: nth($product-item-space, 1);
      width: 0;
      border-left: 1px solid $border-color;

      @include media('<lap-s') {
        top: nth($product-item-space--tab, 1);
        bottom: nth($product-item-space--tab, 1); } } }

  // === product offer ===
  .product__offer {
    display: block;
    height: auto;
    min-height: 10rem;
    margin: 0 0 2rem;
    padding: 0 nth($product-item-space, 2);
    text-align: right;

    @include media('<lap-s') {
      padding: 0 nth($product-item-space--tab, 2); }

    @include media('<mob') {
      min-height: 0;
      padding: 0 nth($product-item-space--mob, 2); } }

  // === product stock ===
  .product__stock {
    padding: 0.5rem 0 0.5rem 3rem;
    margin-bottom: 0;

    & > i {
      $size: 2.2rem;
      width: $size;
      height: $size;
      border-radius: round-float($size / 2, 1);

      &::before {
        $size: 0.6rem 1rem;
        height: nth($size, 1);
        width: nth($size, 2);
        margin: -#{round-float(nth($size, 1)/2, 1) + 0.1} 0 0 -#{round-float(nth($size, 2)/2, 1)}; } }

    &.off {
      & > i {
        &::before {
          height: 0;
          margin-top: 0; } } } }

  // === product price ===
  .product__price {
    flex-basis: auto;
    line-height: 4rem; }

  .product__price-old {
    & > strong, & > em {
      font-size: 2rem; } }

  .product__price-new {
    & > strong, & > em {
      font-size: 3.0rem; } }


  // === product option ===
  // --> product action <--
  // compare button
  .product__options-btn_compare {
    & > span {
      display: inline-block; } }

  // favorite (like) button
  .action-btn_like {
    position: relative; }

  // product action (container)
  .product__action {
    display: block;
    width: calc((100% - #{$offer-width}) / 2);
    position: absolute;
    left: 0;
    bottom: nth($product-item-space, 2);
    padding-right: round-float(nth($product-item-space, 2) / 2, 1);
    margin: 0;
    text-align: center;
    cursor: default;

    @include media('<lap-s') {
      bottom: nth($product-item-space--tab, 2);
      padding-right: round-float(nth($product-item-space--tab, 2) / 2, 1); }

    @include media('<net') {
      width: calc(100% - #{$offer-width}); }

    @include media('<mob') {
      position: relative;
      left: auto;
      bottom: auto;
      padding-right: 0;
      width: auto;
      margin-bottom: 2rem; }

    @include media('<mob-s') {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 1rem; } }

  .product__action-btn {
    display: inline-block;
    vertical-align: top;
    margin-left: 1.2rem;
    margin-right: 1.2rem;

    @include media('<mob-s') {
      display: block;
      margin: 0 0 1rem; } }

  // --> product purchase <--
  .product__purchase {
    margin-bottom: 1rem; }

  // purchase button
  .purchase-btn {
    justify-content: center;
    position: relative;
    padding: 1.2rem 3.8rem;
    height: auto;
    width: auto;
    @include font-bold;
    @include font(1.8rem, 2.2rem);
    color: $btn-text-color;
    text-align: center;
    background-color: $btn-bg-color;
    border: none;
    border-radius: 4px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);

    .purchase-btn__to-cart {
      display: none; }

    .purchase-btn__buy {
      display: block; }

    & > i {
      $size: 1.8rem;
      position: absolute;
      left: 1rem;
      top: 50%;
      height: $size;
      width: $size;
      opacity: 0;
      $translate: 10px, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      margin-top: -#{round-float($size / 2, 1) + 0.1};

      @include media('<mob') {
        margin: -#{round-float($size / 2, 1) + 0.1} 0 0; } }

    //svg
    //$size: 1.8rem
    //position: absolute
    //left: 1rem
    //top: 50%
    //height: $size
    //width: $size
    //opacity: 0
    //$translate: 10px, 0
    //transform: translate($translate)
    //transform: translate3d($translate, 0)
    //margin-top: -#{round-float($size / 2, 1) + 0.1}

    //path
    //  fill: $base-color

    & > span {
      padding: 0;
      text-align: left;

      @include media('<mob-s') {
        opacity: 1;
        font-size: inherit;
        line-height: inherit; } }

    // hover
    &:hover {
      .no-touch & {
        color: $btn-text-color;
        background-color: $base-color-alt;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4), 0 3px 6px rgba(black, 0.2); }

      //svg
      //path
 }      //  fill: $base-color

    // active
    //&:active
    //top: 0
    //box-shadow: none

    // added
    &.added {

      .purchase-btn__buy {
        display: none; }

      & > i {
        filter: none;
        opacity: 1;
        $translate: 0, 0;
        transform: translate($translate);
        transform: translate3d($translate, 0);
        // transition
        $prop: transform, opacity;
        will-change: $prop;
        transition: {
          property: $prop;
          duration: $animation-speed * 2;
          timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28); } }

      //svg
      //background: none
      //box-shadow: none
      //border-radius: 0
      //opacity: 1
      //$translate: 0, 0
      //transform: translate($translate)
      //transform: translate3d($translate, 0)
      //// transition
      //$prop: transform, opacity
      //will-change: $prop
      //transition:
      //  property: $prop
      //  duration: $animation-speed * 2
 } }      //  timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28)

  // --> product installment <--
  .product__installment {
    display: block; }

  // product options (container)
  .product__options {
    height: auto;
    position: static;
    display: block;
    margin: 0;
    padding: 0 nth($product-item-space, 2);

    @include media('<lap-s') {
      padding: 0 nth($product-item-space--tab, 2); }

    @include media('<mob') {
      padding: 0 nth($product-item-space--mob, 2); }

    &::before, &::after {
      display: none; }

    .btn-alt {
      width: 100%; } }

  .product__options-btn_like {
    // hover
    .no-touch &:hover {
      background-color: transparent;
      color: $base-color;

      svg {
        path {
          fill: transparent;
          stroke: $mark-color; } } }

    &.added {
      svg {
        path {
          fill: $mark-color; } }

      // hover
      .no-touch &:hover {
        background-color: transparent; } } } }

