      height: #{nth($at-item-size--tab, 1)} !important; }

    .no-touch &:hover, .touch & {
      overflow-x: auto;
      overflow-y: hidden;
      -webkit-overflow-scrolling: touch; } }

  @include media('<mob') {
    height: #{nth($at-item-size--mob, 1) + 0.6} !important;

    .touch & {
      height: #{nth($at-item-size--mob, 1)} !important; } }

  &:not(.grid-completed) {
    overflow: hidden;
    white-space: nowrap; } }

.articles-tiles__item {
  display: inline-block;
  vertical-align: top;
  width: round-float(100% / $at-columns, 2);
  padding: round-float($at-item-space / 2, 1);

  @include media('<tab') {
    display: inline-block;
    vertical-align: top;
    position: relative !important; // Перебить js стили
    left: auto !important; // Перебить js стили
    top: auto !important; // Перебить js стили
    width: auto !important; // Перебить js стили
    padding: 0 round-float($at-item-space--tab / 2, 1);
    white-space: normal;
    letter-spacing: normal; } }

.articles-tiles__item_wide {
  width: round-float(100% / $at-columns * 2, 2); }

.articles-tiles-elem {
  height: 0;
  display: block;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  //-webkit-tap-highlight-color: transparent

  .articles-tiles__item:nth-child(1n) & {
    @include media('>=lap-s') {
      height: 42rem; }

    @include media('<lap-s', '>=tab') {
      height: 37rem; } }

  .articles-tiles__item:nth-child(2n) & {
    @include media('>=lap-s') {
      height: 20.5rem; }

    @include media('<lap-s', '>=tab') {
      height: 18rem; } }

  .articles-tiles__item:nth-child(3n) & {
    @include media('>=lap-s') {
      height: 42rem; }

    @include media('<lap-s', '>=tab') {
      height: 37rem; } }

  .articles-tiles__item:nth-child(4n) & {
    @include media('>=lap-s') {
      height: 20.5rem; }

    @include media('<lap-s', '>=tab') {
      height: 18rem; } }

  .articles-tiles__item:nth-child(5n) & {
    @include media('>=lap-s') {
      height: 20.5rem; }

    @include media('<lap-s', '>=tab') {
      height: 18rem; } }

  @include media('<tab') {
    height: nth($at-item-size--tab, 1);
    width: nth($at-item-size--tab, 2); }

  @include media('<mob') {
    height: nth($at-item-size--mob, 1);
    width: nth($at-item-size--mob, 2); } }

.articles-tiles-elem__figure {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #080f1f;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    @include linear-gradient(to top, rgba($base-color, 0.5) 0%, rgba($base-color, 0.3) 30%, rgba($base-color, 0) 100%);
    transition: opacity $animation-speed; } }

.articles-tiles-elem__content {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: $at-elem-space;

  @include media('<mob') {
    padding: $page-layout-space--mob; }

  [data-align~="top"] & {
    justify-content: flex-start; }

  [data-align~="middle"] & {
    justify-content: center; }

  [data-align~="bottom"] & {
    justify-content: flex-end; }

  [data-align~="center"] & {
    align-items: center;
    text-align: center; }

  [data-align~="left"] & {
    align-items: flex-start; }

  [data-align~="right"] & {
    align-items: flex-end;
    text-align: right; } }

.articles-tiles-elem__title {
  @include font-bold;
  $fz: 2.4rem;
  $lh: 3.5rem;
  font-size: $fz;
  line-height: $lh;
  $kef-lh: $lh / $fz;
  color: currentColor;

  @include media('<lap-s') {
    $fz: 2rem;
    font-size: $fz;
    line-height: round-float($fz * $kef-lh, 1); }

  .articles-tiles__item_text-small & {
    $fz: 1.8rem;
    $lh: 2.7rem;
    font-size: $fz;
    line-height: $lh;
    $kef-lh: $lh / $fz;

    @include media('<lap-s') {
      $fz: 1.6rem;
      font-size: $fz;
      line-height: round-float($fz * $kef-lh, 1); } } }

.rulez-text {
  display: block;
  @include font-extra-bold;
  $fz: 3.6rem;
  $lh: 5.3rem;
  font-size: $fz;
  line-height: $lh;
  $kef-lh: $lh / $fz;
  text-transform: uppercase;
  color: #131313;

  @include media('<lap-s') {
    $fz: 3.0rem;
    font-size: $fz;
    line-height: round-float($fz * $kef-lh, 1); }

  & > span {
    color: #7ed321; } }

.articles-tiles-elem__label {
  display: inline-block;
  vertical-align: top;
  padding: 0.4rem calc(1.2rem - 0.23em) 0.2rem 1.2rem;
  margin: 0 0 1.2rem;
  font-size: 1.0rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  letter-spacing: 0.23em;
  color: #000;
  background-color: white;

  &:last-child {
    margin-bottom: 0; } }

.articles-tiles-elem__date {
  display: block;
  font-size: 1.4rem;
  line-height: 1.8rem;
  margin-bottom: 0.5rem;

  &:last-child {
    margin-bottom: 0; } }

// Hover
.articles-tiles-elem {
  .no-touch &:hover {
    .articles-tiles-elem__figure {
      &::after {
        opacity: 1; } } } }

/* articles main previews end */

