/* helper alt */
.helper-alt-section {
  margin-bottom: 0;
  background-color: white;
  $space: #{nth($section-inner-space, 2) * 2};
  padding: {
    left: $space;
    right: $space; }

  @include media('<lap-s') {
    background: none;
    $space: 0;
    padding: {
      left: $space;
      right: $space; } } }

.helper-alt {
  overflow: hidden;
  padding: 1rem 0 1.6rem;

  @include media('<lap-s') {
    padding: {
      top: 0;
      bottom: 3rem;
      left: 0;
      right: 0; } }

  //+media('<mob')
  //padding:
  //  top: 0
 }  //  bottom: 0

.helper-alt__list {
  display: flex;
  margin: 0 -3rem;
  cursor: default;

  @include media('<desk-s') {
    margin: 0 -#{nth($page-layout-space, 2)}; }

  @include media('<tab') {
    flex-wrap: wrap; }

  @include media('<mob') {
    margin: 0 -#{nth($page-layout-space--mob, 2)}; } }

.helper-alt__item {
  position: relative;
  flex-grow: 1;
  $width: 25%;
  flex-basis: $width;
  width: $width;
  padding: 0 3rem;
  margin: 2rem 0;
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: $text-color-light;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 1rem;
    bottom: 0;
    border-left: 1px solid $border-color; }

  @include media('<desk-s') {
    padding: 0 #{nth($page-layout-space, 2)}; }

  @include media('<tab') {
    $width: 50%;
    flex-basis: $width;
    max-width: $width; }

  @include media('<tab-s') {
    $width: 100%;
    flex-basis: $width;
    max-width: $width;

    &::before {
      content: none; } }

  @include media('<mob') {
    padding: 0 #{nth($page-layout-space--mob, 2)}; }

  &:first-child {
    &::before {
      content: none; } }

  a {
    color: inherit;
    text-decoration: none; }

  & > strong,
  a > strong {
    display: flex;
    align-items: center;
    position: relative;
    padding: 0 0 0 5rem;
    min-height: 4rem;
    margin-bottom: 1rem;
    @include font-bold;
    font-size: 1.6rem;
    line-height: 2.0rem;
    color: $title-color;
    transition: color $animation-speed;

    svg {
      display: inline-block;
      position: absolute;
      left: 0;
      top: 50%;
      $size: 3.4rem;
      width: $size;
      height: $size;
      margin-top: -#{round-float($size / 2, 1)};

      path {
        fill: $base-color; } }

    & > i {
      position: absolute;
      left: 0;
      top: 50%;
      $size: 3.4rem;
      width: $size;
      height: $size;
      margin-top: -#{round-float($size / 2, 1)}; } }

  p {
    margin: 0; }

  mark {
    background: none;
    color: $base-color; }

  // Hover
  .no-touch & a:hover {
    & > strong {
      color: $base-color; } } }

