// variables
$m-container-space: 0 3rem 3rem;
$m-container-space--tab: 0 2rem 2rem;
$m-container-space--mob: 0 1rem 1.8rem;

$p-filters-width: 34.8rem;
$p-filters-width--lap: 28rem;
$p-filters-width--net: 24rem;
$p-filters-width--tab: 32rem;

$p-filters-angle-size: 2.4rem;
$p-filters-angle-arrow-size: 1rem; // width height

$p-filters-space: 1.4rem 0; // v h

$p-results-height: 5.2rem;
$p-results-fixed-height: 7rem;

.m-container {
  position: relative;
  display: flex;
  background-color: white;
  margin-bottom: 2.5rem;
  min-height: 45rem;

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    display: block; }

  &:last-child {
    margin-bottom: 0; }

  //&::before
  //content: ""
  //display: block
  //position: absolute
  //left: $p-filters-width - 0.1
  //top: 0
  //height: 100%
  //border-right: 1px solid $border-color

  //@include media('<desk-s')
  //  left: $p-filters-width--lap - 0.1

  //@include media('<lap-s')
  //  left: $p-filters-width--net - 0.1

  //// Фильтры - попапом
  //@include media('<#{$p-filters-breakpoint}')
 }  //  display: none

