//content: none

// m-aside
.m-aside {
  $width: $p-filters-width;
  width: $width;
  max-width: $width;
  flex-basis: $width;
  flex-shrink: 0;
  align-self: flex-start;
  border-right: 1px solid $border-color;

  @include media('<desk-s') {
    $width: $p-filters-width--lap;
    flex-basis: $width;
    width: $width;
    max-width: $width; }

  @include media('<lap-s') {
    $width: $p-filters-width--net;
    flex-basis: $width;
    width: $width;
    max-width: $width; }

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: $p-filters-width--tab;
    padding-top: $header-height--tab;
    max-width: none;
    background-color: white;
    border: none;
    z-index: $z-popup;
    $translate: -100%, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    // transition
    $prop: transform;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &.filters_is-open {
      transform: translate(0, 0);
      transform: translateX(0); } }

  .filters-results-show & {
    padding-bottom: $p-results-height;

    .filters-result-fixed & {
      // Только до того, как фильтры перестиливаются в попап
      @include media('>=#{$p-filters-breakpoint}') {
        padding-bottom: $p-results-fixed-height; } } }

  &, & .m-aside__holder {
    // Фильтры - попапом
    @include media('<#{$p-filters-breakpoint}') {
      // ширину устанавливаем на основной
      // и вложенный контейнер,
      // чтобы корректно обрезать скролл
      // на не-тач устройствах
      width: $p-filters-width--tab; }

    @include media('<mob') {
      width: 100%; } } }

.m-aside__align {
  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    overflow-x: hidden;
    overflow-y: auto;
    height: 100%;

    .no-touch & {
      overflow: hidden; }

    .no-touch &:hover {
      overflow-y: auto; } } }

// btn-filters-close
.btn-filters-close {
  display: inline-block;
  position: relative;
  $size: 4rem;
  @include size($size);
  cursor: pointer;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  svg {
    display: block;
    @include size(2rem);
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -1rem 0 0 -1rem;

    path {
      fill: currentColor; } } }

.m-aside__header {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  align-items: center;
  width: 100%;
  height: $header-height--tab;
  padding: 0.5rem 5rem 0.5rem 2rem;
  font-size: 2.0rem;
  line-height: 2.4rem;
  border-bottom: 1px solid $border-color;

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    display: flex; }

  @include media('<mob') {
    padding-left: nth($section-inner-space--mob, 2); }

  .p-filters-activated {
    display: none; }

  .btn-filters-close {
    position: absolute;
    right: 1rem;
    top: 50%;
    margin-top: -2rem; } }

// m-content
.m-content {
  $width: calc(100% - #{$p-filters-width});
  flex-basis: $width;
  flex-grow: 1;
  flex-shrink: 0;
  width: $width;
  max-width: $width;

  @include media('<desk-s') {
    $width: calc(100% - #{$p-filters-width--lap});
    flex-basis: $width;
    width: $width;
    max-width: $width; }

  @include media('<lap-s') {
    $width: calc(100% - #{$p-filters-width--net});
    flex-basis: $width;
    width: $width;
    max-width: $width; }

  @include media('<#{$p-filters-breakpoint}') {
    width: auto;
    max-width: none; }

  &::after {
    content: "";
    display: block;
    clear: both; } }

.m-heading, .m-options, .m-tags, .m-description, .load-more, .products-pagination {
