    @include media('<lap-s') {
      padding-left: nth($m-container-space--tab, 2);
      padding-right: nth($m-container-space--tab, 2); }

    @include media('<mob') {
      padding-left: nth($m-container-space--mob, 2);
      padding-right: nth($m-container-space--mob, 2); } } }

.m-heading {
