/* m-container end */

/* categories-menu */
.categories-menu {
  &, & ul {
    list-style: none;
    margin: 0;
    padding: 1.4rem 0 0;
    @include font(1.6rem, 2.4rem);
    color: $text-color-light; }

  li {
    position: relative;
    padding: 0 0 0 2.4rem;
    margin: 0 0 1.4rem;
    cursor: default;

    &:first-child {
