    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0.6em;
      width: 1.4rem;
      height: 0.2rem;
      background-color: $base-color-light; }

    &.current {
      & > a, & > span {
        @include font-bold; }

      & > span {
        color: $base-color-light; } } }

  a {
    color: inherit;
    text-decoration: none;
    transition: color $animation-speed;

    &:hover {
      color: $base-color; } }

  & > li {

    & > a, & > span {
      color: #000; }

    & > ul {
      @include font(1.4rem, 2.0rem);

      & > li {
        padding-left: 0;

        &::before {
          content: none; } } } } }

/* filters-results */
.p-results {
  position: absolute;
  bottom: 0;
  height: $p-results-height;
  width: $p-filters-width;
  background-color: white;
  border: 0 solid $border-color;
  border-width: 1px 1px 0 0;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  $prop: opacity, visibility, transform;
  will-change: $prop;
  transition: {
    property: opacity, visibility, transform;
    duration: $animation-speed;
    delay: 0.5s; }

  @include media('<desk-s') {
    width: $p-filters-width--lap; }

  @include media('<lap-s') {
    width: $p-filters-width--net; }

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    position: fixed;
    left: 0;
    bottom: 0;
    border-right: 0;
    width: $p-filters-width--tab;
    z-index: $z-popup + 1;
    $translate: -100%, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);

    &.filters_is-open {
      transform: translate(0, 0);
      transform: translateX(0); } }

  @include media('<mob') {
    width: 100%; }

  .filters-results-show & {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transition: {
      delay: 0s; } }

  .filters-result-fixed & {
    // Только до того, как фильтры перестиливаются в попап
    @include media('>=#{$p-filters-breakpoint}') {
      height: $p-results-fixed-height;
      position: fixed;
      bottom: -1px; } } }

.p-results__layout {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    display: block;
    margin: 0 -0.5rem; } }

// selected results
.p-results__selected {
  color: $text-color-light;

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    display: none; }

  & > strong {
    display: block;
    @include font-normal;
    @include font(1.4rem, 1.6rem);
    font-weight: normal; }

  & > span {
    display: block;
    @include font(1.4rem, 1.6rem);
    white-space: nowrap;

    & > em {
      @include font-bold;
      color: $text-color; } } }

// apply results
.btn-filters-apply {
  display: none;
  padding: 0.7rem 1.2rem;
  @include font-normal;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-decoration: none;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  color: $btn-text-color;
  background-color: $btn-bg-color;
  border: none;
  border-radius: 0.4rem;
  box-shadow: 0 1px 1px rgba(black, 0.4);
  user-select: none;
  outline: none;
  transition: box-shadow $animation-speed;
  z-index: 1;

  // Фильтры - попапом
  @include media('<#{$p-filters-breakpoint}') {
    display: inline-block; }

  & > em {
    @include font-bold;
    font-style: normal; }

  // hover
  .no-touch &:hover {
    box-shadow: 0 2px 2px rgba(black, 0.4); }

  &:active {
    top: 1px; }

  &:disabled {
    box-shadow: none !important;
    background-color: $btn-disabled-color !important;
    color: white !important;
    cursor: default;

    &:active {
      top: auto; } } }

// clear results
.btn-filters-clear {
  padding: 0.7rem 1.2rem;
  @include font-normal;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  color: $btn-text-color;
  background: $border-color;
  border: none;
  border-radius: 0.4rem;
  box-shadow: 0 1px 1px rgba(black, 0.4);
  user-select: none;
  outline: none;
  z-index: 1;

  // hover
  .no-touch &:hover {
    box-shadow: 0 2px 2px rgba(black, 0.4); }

  &:active {
    top: 1px; }

  &:disabled {
    box-shadow: none !important;
    background-color: $btn-disabled-color !important;
    color: white !important;
    cursor: default;

    &:active {
      top: auto; } } }

// results options
.p-results__options {
  display: flex;
  align-items: center;

  & > button, & > a {
    margin-left: 1rem;

    // Фильтры - попапом
    @include media('<#{$p-filters-breakpoint}') {
      margin: 0 0.5rem;
      flex-grow: 1; } } }

// results loading
.p-results-loading {
  @include size(calc(100% + 2px));
  position: absolute;
  left: -1px;
  top: -1px;
  @include font-bold;
  @include font(1.4rem, 50px);
  color: white;
  text-align: center;
  z-index: 5;
  background-color: rgba(153, 153, 153, 1);
  opacity: 0;
  visibility: hidden;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed; }

  .js-loading & {
    opacity: 1;
    visibility: visible; } }

/* filters-results end */

/* view-switcher */
.view-switcher {
  $size: 2.8rem;
  $space: 1rem;
  position: relative;
  width: $size * 2 + $space;
  flex-shrink: 0;
  @include clear;

  &:not(.toggle-view_initialized) {
    opacity: 0;
    pointer-events: none; }

  .no-js & {
    opacity: 1;
    pointer-events: auto; }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: $size;
    height: $size;
    background-color: $base-color;
    border-radius: 3px;
    // transition
    $prop: transform;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  &[data-view="grid-view"] {
    &::before {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } }

  &[data-view="list-view"] {
    &::before {
      $translate: $size + $space, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } }

  a {
    position: relative;
    float: left;
    display: block;
    width: $size;
    height: $size;
    text-decoration: none;
    color: #b0a1f0;
    margin-left: $space;
    border-radius: 3px;
    // transition
    $prop: color;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &:first-child {
      margin-left: 0; }

    svg {
      display: inline-block;
      vertical-align: top;
      width: $size;
      height: $size; }

    path {
      fill: currentColor; }

    & > span {
      @include hide-text; }

    // hover
    .no-touch &:not(.active) {
      &:hover, &:focus {
        color: $base-color; } }

    // active
    &.active {
      cursor: default; } } }

.m-heading__switcher {
  $width: 6.6rem;
  flex-basis: $width;
  flex-shrink: 0;
  margin-left: 2rem;

  @include media('<mob-xs') {
    margin: 0.5rem 0 0;
    position: absolute;
    right: nth($m-container-space--mob, 2);
    top: 100%;
    z-index: 5; } }

/* view-switcher end */

/* options panel */
.filters-switcher {
  display: none;
  position: relative;
  @include font(1.4rem, 20px);
  color: $text-color-light;
  text-decoration: none;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: background-color, color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<#{$p-filters-breakpoint}') {
    display: flex;
    align-items: center; }

  @include media("<mob-xs") {
    position: absolute;
    left: nth($m-container-space--mob, 2);
    top: 0;
    z-index: 5; }

  & > em {
    display: inline-block;
    position: relative;
    $size: 3.0rem;
    height: $size;
    width: $size;
    color: $base-color-ultra-light;
    background-color: $base-color;
    border-radius: $size;

    &::before {
      content: "";
      //display: block
      @include add-cross($color: white);
      display: none;

      @include media('<tab') {
        display: block; } } }

  svg {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    $size: 1.8rem;
    @include size($size);
    $half-space: round-float($size / 2, 1);
    margin: -#{$half-space} 0 0 -#{$half-space};

    &, path {
      fill: currentColor; } }

  & > span {
    margin-right: 1rem;
 }    //display: none

  & > em + span {
    margin-right: 0;
    margin-left: 1rem; }

  .p-filters-activated {
    $size: 1.6rem;
    position: absolute;
    right: -0.5rem;
    top: -0.5rem;
    @include size($size);
    @include font-normal;
    font-size: 1.2rem;
    line-height: $size;
    text-align: center;
    letter-spacing: normal;
    color: white;
    background-color: $base-color-ultra-light;
    border: 1px solid $base-color-ultra-light;
    border-radius: $size;
    z-index: 2;

    &.hide {
      display: none; } }

  // hover
  .no-touch &:hover {
    color: $base-color;

    & > em {
      background-color: $base-color;
      color: white; } }

  // is open
  &.filters_is-open {
    color: $base-color;

    & > em {
      &::before {
        opacity: 1; }

      svg {
        opacity: 0; } } } }

/* options panel end */

/* control sort */
// container
.cs-container {
  position: relative;
  opacity: 0;
  pointer-events: none;

  .no-js & {
    opacity: 1;
    pointer-events: auto; }

  &.cs_initialized {
    opacity: 1;
    pointer-events: auto; } }

// selector
.cs-selector {
  @include pseudo-select-common($base-color);

  & > span > span {
    display: none; }

  &.asc > span > span[data-cs-trend="asc"] {
    display: inline-block; }

  &.desc > span > span[data-cs-trend="desc"] {
    display: inline-block; }

  @include media('>=tab') {
    display: none; } }

.cs-drop {
  @include media('<tab') {
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 24rem;
    @include font(1.6rem, 2.4rem);
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 1px 2px rgba(black, 0.15);
    z-index: 9;
    // hide
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: left top;
    $translate: -0.2rem, -0.2rem;
    $scale: 0.8, 0.8;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    // transition
    $prop: transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  // Select opened
  &.cs_select-open {
    @include media('<tab') {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1); } } }

.cs-drop__item {
  display: inline-block;
  vertical-align: top;

  @include media('<tab') {
    display: block; } }

.cs-drop__elem {
  position: relative;
  display: inline-block;
  vertical-align: top;
  color: $text-color-light;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-decoration: none;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  @at-root {
    // Переключатели
    .cs-drop__elem_toggle {
      margin: 0.5rem 2.4rem 0.5rem 0;
      padding: 0.3rem 0 0.3rem 2.4rem;

      @include media('<lap-s') {
        margin-right: 2rem; }

      // Скрыть переключатели со стрелочками
      // на маленьких разрешениях
      @include media('<tab') {
        display: none; }

      & > span {
        transition: box-shadow $animation-speed; }

      & > i {
        position: absolute;
        left: 0;
        top: 0;
        @include size(1rem, 100%);
        z-index: 5;
        // transition
        $prop: transform;
        will-change: $prop;
        transition: {
          property: $prop;
          duration: $animation-speed; }

        // Сортировать по убыванию
        .desc & {
          $rotate: 0turn;
          transform: rotate($rotate) translateZ(0); }

        // Сортировать по возрастанию
        .asc & {
          $rotate: -0.5turn;
          transform: rotate($rotate) translateZ(0); }

        svg {
          display: block;
          position: absolute;
          left: 50%;
          top: 50%;
          height: 0.6rem;
          width: 1rem;
          transform: translate(-50%, -50%);
          stroke: $base-color;

          path {
            stroke: $base-color; } } }

      // active
      .cs_active & {
        color: $base-color;

        & > i {
          svg {
            stroke: $base-color; } } }

      // click
      //&:active
      //top: 1px

      // hover
      &:hover {
        .no-touch & {
          color: $base-color;
          //& > span
 } } } }          //box-shadow: inset 0 -1px 0 currentColor
  @at-root {
    // Элетенты селекта
    .cs-drop__elem_option {
      display: block;
      padding: 1.2rem 1.4rem;
      // transition
      $prop: background-color, color;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

      // Скрыть опшины (элементы меню селекта)
      // на больших разрешениях
      @include media('>=tab') {
        display: none; }

      // hover
      &:hover {
        color: white;
        background-color: $base-color-light; }

      // active
      .cs_active.desc &[data-cs-trend="desc"] {
        @include font-bold;
        color: $base-color;
        background-color: rgba($base-color-light, 0.2); }

      .cs_active.asc &[data-cs-trend="asc"] {
        @include font-bold;
        color: $base-color;
        background-color: rgba($base-color-light, 0.2); } } } }

/* control sort end */

/* filters tags */
.m-tags {
  display: none;
  background-color: white;
  z-index: 9;

  .filters-results-show & {
    display: block; } }

.p-filters-tags {
  padding: 0.5rem 0 1.5rem;
  border-bottom: 1px solid $border-color;
  position: relative;
  line-height: 2.0rem;
  margin-left: -1rem; }

.p-filters-tags__item {
  $tag: &;

  $this-icon-lines-width: 10px;
  $this-icon-lines-height: 1px;
  //display: inline
  display: inline-block;
  vertical-align: top;
  position: relative;
  @include font(1.4rem, 2.0rem);
  color: $base-color;
  white-space: nowrap;
  cursor: pointer;
  margin-left: 1rem;
  user-select: none;

  & > span {
    position: relative;
    display: inline-block;
    margin-right: $this-icon-lines-width + 3;
    white-space: normal;
    //text-decoration: underline

    &::after {
      content: "";
      display: block;
      position: absolute;
      border-top: 1px solid currentColor;
      width: calc(100% + 0.4rem);
      left: -0.2rem;
      bottom: 50%;
      margin: -1px 0;
      will-change: transfom;
      transition: transform $animation-speed;
      transform: scale(0, 1) translateZ(0);
      transform-origin: left center; } }

  @at-root {
    #{$tag}:hover {
      & > span::after {
        transform: scale(1, 1) translateZ(0); } } }

  & > i {
    position: relative;
    display: inline-block;
    vertical-align: top;
    //opacity: 0
    @include size(#{round($this-icon-lines-width)});
    margin-left: -$this-icon-lines-width;
    top: 6px;
    @include font(0, 0);
    color: $text-color-light;
    $prop: opacity, color;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &::before,
    &::after {
      @include pseudo();
      left: 50%;
      top: 50%;
      backface-visibility: hidden;
      @include size($this-icon-lines-width, $this-icon-lines-height);
      margin: -#{round($this-icon-lines-height*0.5)} 0 0 -#{round($this-icon-lines-width*0.5)};
      background-color: currentColor; }

    &::before {
      transform: rotate(-45deg); }

    &::after {
      transform: rotate(45deg); } } }

/* filters tags end */

/* filters */
.p-filters-list {
  position: relative;
  padding: 0 2rem;

  @include media('<mob') {
    padding: 0 #{nth($section-inner-space--mob, 2)}; } }

.p-filters-item {
  position: relative;
  border: solid $border-color;
  padding: 1.9rem 0;
  border-width: 0 0 1px;

  @include media('<mob') {
    padding: 1.2rem 0; } }

// switcher
.p-filters-select {
  position: relative;
  cursor: pointer;
  padding: nth($p-filters-space, 1) nth($p-filters-space, 2) nth($p-filters-space, 1) 3.3rem;
  line-height: 1;
  user-select: none;
 }  //-webkit-tap-highlight-color: transparent

// title
.p-filters-title {
  @include font-normal;
  @include font(1.8rem, 2.2rem);
  color: #000; }

// angle
.p-filters-angle {
  position: absolute;
  left: nth($p-filters-space, 2);
