  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  & > svg {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: $p-filters-angle-arrow-size;
    width: $p-filters-angle-arrow-size;
    $space: round-float($p-filters-angle-arrow-size / 2, 1);
    margin: -#{$space} -#{$space};
    //transform: rotate(-45deg)

    &, path {
      stroke: currentColor; } } }

// counter
.p-filters-selected {
  position: absolute;
  display: inline-block;
  vertical-align: top;
  opacity: 0;
  pointer-events: none;
  //===================
  $param: transform, opacity;
  will-change: $param;
  transition: {
    property: $param; }

  &::before {
    content: "("; }

  &::after {
