// drop
.p-filters-drop {
  position: relative;
  display: none; }

.p-filters-drop-list {
  $item-space: 1.4rem;
  //overflow-x: hidden
  //overflow-y: auto
  //max-height: 33rem

  &, .#{$article-content} & {
    list-style: none;
    margin: 0 0 0 -0.6rem;
    padding: #{nth($p-filters-space, 1)} 0 #{nth($p-filters-space, 1) - $item-space};

    li {
      position: relative;
      padding-left: 0;
      margin: 0 0 $item-space 0.6rem;

      &::before {
        content: none; }

      &:first-child {
        &:before {
          content: none; } } } }

  label {
    display: block; }

  // item float
  &.item-float {
    @include clear;

    li {
      float: left;
      line-height: 1; } }

  // group of form's elements
  .form-row {
    $space: 2rem;
    $half-space: round-float($space / 2, 1);
    $space--net: 1rem;
    $half-space--net: round-float($space--net / 2, 1);
    margin-left: -#{$half-space};
    margin-right: -#{$half-space};

    @include media('<lap-s') {
      margin-left: -#{$half-space--net};
      margin-right: -#{$half-space--net}; }

    .input-wrap {
      margin-left: $half-space;
      margin-right: $half-space;
      width: calc(100% / 2 - #{$space});

      @include media('<lap-s') {
        margin-left: $half-space--net;
        margin-right: $half-space--net;
        width: calc(100% / 2 - #{$space--net}); }

      @include media('<mob') {
        width: auto; } } } }

.p-filters-drop-footer {
  .btn-filters-clear {
 } }    //display: none

// ===STATES===
// hover
.p-filters-select {
  .no-touch &:hover {
    .p-filters-title {
      color: $base-color; } } }

// has checked filters
.p-filters-item.p-filters-on {
  .p-filters-select {
    .p-filters-title {
      @include font-bold;
      color: $base-color; } } }

// drop open
.p-filters-item.p-filters-is-open {

  .p-filters-angle {
    transform: rotate(0.5turn); }

  .p-filters-drop {
    display: block; } }

/* filters end */

/* loading cover */
.loader-cover {
  @include size(100%);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99999;
  background-color: rgba(255, 255, 255, 0.50);
  opacity: 0;
  visibility: hidden;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed; }

  @include media('<tab') {
    position: fixed;
    z-index: 10100; }

  .js-loading &,
  .js-loading ~ & {
    opacity: 1;
    visibility: visible; }

  &::before {
    $loader-size: 120px;
    // ==================
    content: "";
    display: block;
    position: fixed;
    left: 50%;
    top: 50%;
    @include size($loader-size);
    transform: translate(-50%, -50%);
    background-image: url(../../img/preloader.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain; } }

/* loading cover end */

/* collections */
$collections-space: $section-inner-space;
$collections-space--tab: $section-inner-space--tab;
$collections-space--mob: $section-inner-space--mob;
$collections-title-width: 12.5rem;
.collections {
  position: relative;
  padding: $collections-space;
  display: flex;
  align-items: center;
  background-color: white;

  @include media('<lap-s') {
    padding: $collections-space--tab; }

  @include media('<tab-s') {
    display: block; }

  @include media('<mob') {
    padding: $collections-space--mob; }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: nth($collections-space, 1) + 0.5;
    bottom: nth($collections-space, 1) + 0.5;
    left: $collections-title-width + nth($collections-space, 2);
    width: 0;
    border-left: 1px solid $border-color;

    @include media('<lap-s') {
      top: nth($collections-space--tab, 1) + 0.5;
      bottom: nth($collections-space--tab, 1) + 0.5;
      left: $collections-title-width + nth($collections-space--tab, 2); }

    @include media('<tab-s') {
      display: none; }

    //+media('<mob')
    //top: nth($collections-space--mob, 1) + 0.5
    //bottom: nth($collections-space--mob, 1) + 0.5
 } }    //left: $collections-title-width + nth($collections-space--mob, 2)

.collections__title {
  flex-shrink: 0;
  flex-basis: $collections-title-width;
  max-width: $collections-title-width;
  @include font-bold;
  color: $title-color;
  font-size: 2.1rem;
  line-height: 2.2rem;
  margin: 0;

  @include media('<tab-s') {
    max-width: none;
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0; } } }

.collections__content {
  flex-grow: 1;
  padding: 0 0 0 #{nth($collections-space, 2) + 1.3};

  @include media('<lap-s') {
    padding-left: #{nth($collections-space--tab, 2) + 1.3}; }

  @include media('<tab-s') {
    padding-left: 0; }

  //+media('<mob')
  //padding-left: #{nth($collections-space--mob, 2) + 1.3}

  // for ie 9
  @if $ie9 {
    .no-flexwrap & {
      margin-left: $collections-title-width; } } }

// collections nav
.collections__nav {
  position: relative;
  margin-bottom: 1rem;

  @include media('<tab') {
    max-width: 24rem; }

  &:last-child {
    margin-bottom: 0; } }

// collections thumbs select
.collections__select {
  @include pseudo-select-common($base-color-light);

  @include media('>=tab') {
    display: none; } }

// collections thumbs
.collections__thumbs {
  display: flex;
  flex-wrap: wrap;
  font-size: 1.4rem;
  line-height: 2.0rem;

  @include media('<tab') {
    display: block; // Not flex
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 24rem;
    background-color: white;
    border-radius: 1.2rem;
    box-shadow: 0 1px 2px rgba(black, 0.15);
    z-index: 9;
    // hide
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: left top;
    $translate: -0.2rem, -0.2rem;
    $scale: 0.8, 0.8;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    // transition
    $prop: transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  a {
    color: $text-color-light;
    text-decoration: none;
    margin: 0.5rem 4rem 0.5rem 0;
    user-select: none;
    //-webkit-tap-highlight-color: transparent
    // transition
    $prop: opacity;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    // for ie 9
    @if $ie9 {
      .no-flexwrap & {
        display: inline-block;
        vertical-align: top; } }

    @include underlinedAnchor($text-color-light);

    @include media('<lap-s') {
      margin-right: 2.5rem; }

    @include media('<tab') {
      display: block;
      margin: 0;
      padding: 1.2rem 1.4rem;

      span {
        border: none; }

      &:hover {
        .no-touch & {
          color: white;
          background-color: $base-color-light; } } }

    // active
    &.tabs-active {
      color: #000;
      //+font-bold
      cursor: default;

      & > span {
        border-color: transparent; }

      @include media('<tab') {
        color: $base-color;
        background-color: rgba($base-color-light, 0.2); } } }

  // Select opened
  &.tabs-select-open {
    @include media('<tab') {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1); } } }

/* collections end */

/* float-list */
.float-list-wrap {
  overflow: hidden; }

.float-list {
  font-size: 1.4rem;
  line-height: 2.0rem;
  cursor: default;

  &, .#{$article-content} & {
    list-style: none;
    margin: 0 0 2rem;
    padding: 0;

    &:last-child {
      margin-bottom: 0; }

    li {
      margin: 1.2rem 1.2rem 0 0;
      padding: 0;

      &:before {
        content: none; } } }

  li {
    display: inline-block;
    vertical-align: top;
    color: $base-color; }

  a {
    color: #777;
    text-decoration: none;
    cursor: pointer;

    @include underlinedLinks(#777); }

  li.current {
    a {
      color: $base-color;

      @include underlinedLinks($base-color); } } }

/* float-list end */

/* product card */
$card-cont-width: 52rem;
$card-cont-width--net: 40rem;
// 520px (43.8%) на ширине 1280px

.p-card {
  background-color: white;
  padding: 2.5rem nth($section-inner-space, 2) 4.2rem;
  margin-bottom: 4.8rem;
  @include clear;

  @include media('<net') {
    padding-bottom: 3rem; }

  @include media('<lap-s') {
    padding-left: nth($section-inner-space--tab, 2);
    padding-right: nth($section-inner-space--tab, 2); }

  @include media('<tab') {
    margin-bottom: 3rem; }

  @include media('<mob') {
    padding-left: nth($section-inner-space--mob, 2);
    padding-right: nth($section-inner-space--mob, 2); }

  &:last-child {
    margin-bottom: 0; } }

// === product card caption ===
// ============================
// title
.p-card-title {
  margin: 0; }

// heading
.p-card__caption {
  margin-bottom: 1rem;
  display: flex;
  align-items: flex-start;

  @include media("<tab") {
    display: block;
    margin-bottom: 2.5rem; } }

.p-card__caption-title {
  margin-right: 2rem;

  @include media("<tab") {
    margin: 0 0 1rem; }

  @include media("<mob-xs") {
    font-size: 2rem;
    line-height: 2.6rem; } }

.p-card__caption-rate {
  flex: 1 0 14.1rem;
  max-width: 14.1rem;
  margin: 1rem 0 1.5rem;

  @include media("<tab") {
    margin: 0 0 0 -0.5rem; } }


// === product card main ===
// =========================
$gallery-thumbs-size: 5rem;
$gallery-thumbs-item-space: 1.4rem;
$gallery-thumbs-arrow-size: 4rem 100%;
$gallery-thumbs-height: $gallery-thumbs-size * 5 + $gallery-thumbs-item-space * 4;
$gallery-thumbs-height--sm: $gallery-thumbs-size * 3 + $gallery-thumbs-item-space * 2;
.p-card__main {
  position: relative;
  float: left;
  clear: left;
  width: calc(100% - #{$card-cont-width});
  margin-bottom: 2rem;

  @include media('<net') {
    width: calc(100% - #{$card-cont-width--net}); }

  @include media('<tab') {
    float: none;
    width: auto; } }

// === product card labels ===
.p-card-stickers {
  margin-bottom: 0.5rem;

  &:last-child {
    margin-bottom: 0; } }

// card gallery
.p-card-gallery {
  position: relative;
  //padding: 0 $gallery-thumbs-size 0 #{$gallery-thumbs-size + 30}
  padding: 0 5rem 0 #{5 + $gallery-thumbs-size};
  margin-bottom: 2.4rem;

  @include media('<lap-s') {
    padding: 0 2rem 0 #{2 + $gallery-thumbs-size}; }

  @include media('<tab-s') {
    padding: 0; }

  @include media('<mob-s') {
    margin-left: -#{nth($section-inner-space--mob, 2)};
    margin-right: -#{nth($section-inner-space--mob, 2)}; }

  &:last-child {
    margin-bottom: 0; }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none; } }

// card gallery images
.p-card-gallery-images {
  margin: 0 auto;
  max-width: 50rem;
  //min-width: $gallery-thumbs-height + nth($gallery-thumbs-arrow-size, 1) * 2
  height: $gallery-thumbs-height + nth($gallery-thumbs-arrow-size, 1) * 2;

  @include media('<desk-s') {
    max-width: 38rem; }

  @include media('<tab-s') {
    max-width: none;
    margin-bottom: 2rem;
    height: 32rem; }

  @include media('<mob') {
    height: 20rem; } }

.p-card-gallery-images__item {
  user-select: none;
  outline: none;

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-decoration: none;
    color: currentColor;
    user-select: none;
    outline: none;

    cursor: zoom-in; }

  img {
    max-width: 100%;
    max-height: 100%;
    flex-shrink: 0; } }

// card gallery thumbs
.p-card-gallery-thumbs {
  display: flex;
  flex-direction: column;
  //flex-wrap: wrap
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  width: $gallery-thumbs-size;
  min-height: 100%;

  @include media('<tab-s') {
    flex-direction: row;
    position: relative;
    left: auto;
    top: auto;
    width: auto;
    height: $gallery-thumbs-size; }

  .swiper-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: $gallery-thumbs-height;
    width: 100%;

    @include media('<tab-s') {
      height: auto; } }

  .swiper-wrapper {
    height: auto; } }

.p-card-gallery-thumbs__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: $gallery-thumbs-size;
  height: $gallery-thumbs-size;
  border: 1px solid $border-color;
  border-radius: 2px;
  // transition
  $prop: border-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    //background-color: $base-color
    @include linear-gradient(to top, black 0%, $base-color 100%);
    opacity: 0;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  &.swiper-slide-thumb-active {
    //border: 1px solid rgba($base-color, 0.1)

    &::after {
      opacity: 0.12; } }

  img {
    display: block;
    max-width: calc(100% - 4px);
    max-height: calc(100% - 4px); } }

.p-card-gallery-thumbs__arrow-prev,
.p-card-gallery-thumbs__arrow-next {
  position: relative;
  height: 4rem;
  //background-color: $base-color
  cursor: pointer;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  @include media('<tab-s') {
    flex-shrink: 0;
    width: 3rem;
    height: 100%; }

  &::after {
    top: 50%;
    left: 50%;
    content: "";
    position: absolute;
    opacity: 1;
    pointer-events: none;
    @include size(1.2rem);
    transform: rotate(45deg);
    border: 0 solid #303030;
    margin: -1.5rem -1rem;

    @include media('<tab-s') {
      transform: rotate(-45deg); } }

  &:focus {
    outline: none; }

  &.swiper-button-disabled {
    pointer-events: none;
    opacity: 0.5; } }

.p-card-gallery-thumbs__arrow-prev {
  &::after {
    border-width: 2px 0 0 2px;
    margin: -4px -5px;

    @include media('<tab-s') {
      margin: -7px -4px; } }

  //&:active
 }  //top: -1px

.p-card-gallery-thumbs__arrow-next {
  &::after {
    border-width: 0 2px 2px 0;
    margin: -9px -5px;

    @include media('<tab-s') {
      margin: -7px -8px; } } }

// p-card-services
.p-card__services {
  float: left;
  clear: left;
  overflow: hidden;
  width: calc(100% - #{$card-cont-width});
  padding: 0.5rem 5rem 0.5rem 0;
  margin-bottom: 4rem;

  @include media('<net') {
    width: calc(100% - #{$card-cont-width--net}); }

  @include media('<lap-s') {
    padding-right: nth($section-inner-space--tab, 2); }

  @include media('<tab') {
    float: none;
    width: auto;
    margin-bottom: 3rem; }

  @include media('<mob') {
    padding-right: nth($section-inner-space--mob, 2); } }

.p-card-services__list {
  display: flex;
  margin: 0 -2rem;

  @include media('>=tab', '<net') {
    display: block; }

  @include media('<tab-s') {
    display: block; } }

.p-card-services__item {
  flex-grow: 1;
  $width: 33.33%;
  flex-basis: $width;
  width: $width;
  padding: 0 2rem;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: $text-color-light;
  border-left: 2px solid $border-color-light;

  @include media('>=tab', '<net') {
    width: auto;
    margin-top: 2rem;

    &:first-child {
      margin-top: 0; } }

  @include media('<tab-s') {
    width: auto;
    margin-top: 3rem;

    &:first-child {
      margin-top: 0; } }

  a {
    color: inherit;
    text-decoration: none; }

  & > strong,
  a > strong {
    display: block;
    position: relative;
    @include font-bold;
    font-size: 1.6rem;
    line-height: 2rem;
    color: #000;
    padding: 0.2rem 0 0.2rem 4.2rem;
    margin-bottom: 2rem;
    transition: color $animation-speed;

    &.text-large {
      font-size: 2rem;
      line-height: 2.4rem;
      margin-bottom: 1rem; }

    @include media('>=tab', '<net') {
      font-size: 1.6rem;
      line-height: 2rem;
      margin-bottom: 1rem; }

    @include media('<tab-s') {
      margin-bottom: 1rem; }

    & > i {
