      display: inline-block;
      position: absolute;
      left: 0;
      top: 50%;
      width: $size;
      height: $size;
      margin-top: -#{round-float($size / 2, 1)};

      &.icon-gift {
        $size: 3.6rem;
        width: $size;
        height: $size;
        margin-top: -#{round-float($size / 2, 1)}; } }

    svg {
      $size: 2.4rem;
      display: inline-block;
      position: absolute;
      left: 0;
      top: 50%;
      width: $size;
      height: $size;
      margin-top: -#{round-float($size / 2, 1)};

      path {
        fill: $base-color; } } }

  p {
    margin: 0; }

  mark {
    background: none;
    color: $base-color;

    &.mark_gift {
      @include font-bold;
      color: #fa2c64;
 } }      //box-shadow: inset 0 -1px 0 currentColor

  // Hover
  .no-touch & a:hover {
    & > strong {
      color: $base-color; } } }

.p-card-services__item_gift {
  // Hover
  .no-touch & a:hover {
    & > strong {
      color: #fa2c64; } } }

// recommended-prod
.recommended-prod {
  display: flex;
  align-items: center;
  margin: 0 0 #{$bottom-space};

  @include media('<mob') {
    margin-bottom: $bottom-space--mob; }

  &:last-child {
    margin-bottom: 0; }

  &__figure {
    $width: 10rem;
    $height: 6rem;
    $ratio: $height / $width;
    display: flex;
    flex-grow: 0;
    flex-shrink: 1;
    align-items: center;
    justify-content: center;
    flex-basis: $width;
    max-width: $width;
    height: round-float($width * $ratio, 1);
