    &:last-child {
      margin-bottom: 0; } } }

// product card installment
.p-card__installment {
  float: left;
  clear: left;
  overflow: hidden;
  width: calc(100% - #{$card-cont-width});
  padding: 0 5rem 0 0;

  @include media('<lap-l') {
    float: none;
    width: 100%;
    padding-right: 0; } }


// === product card aside ===
// ==========================
// card aside action
.p-card-action {
  padding: 0 #{nth($box-inner-space, 2)};
  margin-bottom: 0.7rem;
  text-align: right;
  white-space: nowrap;
  cursor: default;
  direction: rtl; // Чтобы при переполнении контента, содержимое уходило влево, а не вправо, как по дефолту

  @include media('<tab') {
    text-align: left;
    direction: ltr;
    padding: 0; }

  &:last-child {
    margin-bottom: 0; } }

.p-card-action__btn {
  margin: 0 0 1rem 5rem;
  direction: ltr;

  @include media('<net') {
    margin-left: 2rem; }

  @include media('<tab') {
    margin-left: auto;
    margin-right: 2rem; }

  @include media('<mob-s') {
    vertical-align: middle; }

  &.action-btn_like {
    @include media('<mob-s') {
      $size: 3.2rem;
      height: $size;
      width: $size;
      padding: 0;
      background-color: #eaeaea;
      border-radius: $size;
      border: none;

      & > span {
        display: none; }

      & > svg {
        @include size(1.8rem);
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        &, path {
          fill: white;
          stroke: white; } }

      &.added {
        background-color: $mark-color; } } } }

// card aside stock
.p-card-stock {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0.3rem 0 0.3rem 2.8rem;
  flex-basis: 50%;
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #000;

  & > i {
    $size: 1.8rem;
    position: absolute;
    left: 0;
    top: 50%;
    width: $size;
    height: $size;
    background-color: #7ed321;
    border-radius: round-float($size / 2, 1);
    transform: translate(0, -50%);

    &::before {
      $size: 0.5rem 0.8rem;
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      height: nth($size, 1);
      width: nth($size, 2);
      margin: -#{round-float(nth($size, 1)/2, 1) + 0.1} 0 0 -#{round-float(nth($size, 2)/2, 1)};
      border-style: solid;
      border-color: white;
      border-width: 0 0 1px 1px;
      transform: rotate(-45deg); } }

  // off
  &.off {
    color: $text-color-disabled;

    & > i {
      border-color: currentColor;

      &::before {
        transform: rotate(0deg);
        height: 0;
        margin-top: 0; } } } }

// code
.p-card-code {
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.2rem;
  color: $form-error-color;

  &__label {
    color: $text-color; } }

// price
.p-card-price {
  //font-size: 1.6rem
  line-height: 4.7rem;

  @include media('<net') {
    display: flex;
    //flex-direction: column-reverse // Сначала новая цена, потом старая
    flex-direction: column;
    line-height: 1; } }

// price old
.p-card-price__old {
  display: block;
  margin: 0 0 0.8rem;
  @include oldPriceStyle();

  //+media('<net')
  //order: 2
  //display: block
  //width: 100%
  //margin-right: 0

  &:last-child {
    margin-bottom: 0; }

  .p-card-price.off & {
    color: $text-color-ultra-light; } }

// price new
.p-card-price__new {
  display: block;
  line-height: 4.5rem;
  white-space: nowrap;
  color: $text-color;

  @include media('<net') {
    line-height: 3.8rem; }

  & > strong {
    display: inline-block;
    vertical-align: baseline;
    @include font-bold;
    font-size: 4.5rem;

    @include media('<net') {
      font-size: 3.8rem; } }

  & > em {
    display: inline-block;
    vertical-align: baseline;
    @include font-normal;
    font-size: 2.4rem;
    font-style: normal; }

  //+media('<net')
  //order: 1
  //display: block
  //width: 100%
  //margin-bottom: 0.5rem

  @include media('<mob') {
    & > strong {
      font-size: 3.8rem; } }

  .p-card-price.off & {
    color: $text-color-ultra-light; } }

// price installment
.p-card-price__inst {
  position: relative;
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: $text-color;

  //+media('<net')
  //order: 3

  &-value {
    display: inline-block; }

  & > strong {
    display: inline-block;
    vertical-align: baseline;
    @include font-bold;
    font-size: 2.0rem; }

  & > em {
    display: inline-block;
    vertical-align: baseline;
    @include font-normal;
    font-size: 1.6rem;
    font-style: normal; }

  .p-card-price.off & {
    color: $text-color-ultra-light; } }

.p-card-price__note {
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  text-transform: uppercase;
  color: $form-error-color;
  padding-top: 1rem;

  @include media('<net') {
    font-size: 1.4rem;
    line-height: 2rem; } }

// button buy
.btn-buy {
  position: relative;
  padding: 1.4rem 3.8rem;
  @include font-bold;
  @include font(1.8rem, 2.2rem);
  text-decoration: none;
  text-align: center;
  color: #000;
  background-color: $btn-bg-color;
  border: none;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
  user-select: none;
  transition: box-shadow $animation-speed;
  //-webkit-tap-highlight-color: transparent

  & > i {
    $size: 1.8rem;
    //display: none
    position: absolute;
    left: 1rem;
    top: 50%;
    height: $size;
    width: $size;
    opacity: 0;
    $translate: 10px, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    margin-top: -#{round-float($size / 2, 1) + 0.1};

    svg {
      display: inline-block;
      vertical-align: top;
      width: $size;
      height: $size;

      path {
        fill: $base-color;
        // transition
        $prop: fill, stroke;
        transition: {
          property: $prop;
          duration: $animation-speed; } } } }

  .btn-buy__in-cart {
    display: none; }

  // button buy states
  // hover
  &:hover {
    .no-touch & {
      color: #000;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4), 0 3px 6px rgba(black, 0.15); } }

  // active
  &:active {
    top: 1px; }

  // added
  &.added {
    .btn-buy__buy {
      display: none; }

    .btn-buy__in-cart {
      display: block; }

    & > i {
      background: none;
      box-shadow: none;
      border-radius: 0;
      opacity: 1;
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      // transition
      $prop: transform, opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed * 2;
        timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28); } } } }

.btn-buy_stretch {
  display: block; }

// options
.p-card-options {
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; } }

.p-card-options__item {
  position: relative;
  margin-top: 0.7rem;

  &:first-child {
    margin-top: 0; } }

// buttons
.p-card-options__btn {
  display: block;
  width: 100%; }

// p-card-offer
.p-card-offer {
  position: relative;
  border: 1px solid $border-color-light;
  padding: 2rem;

  @include media('<mob') {
    padding: nth($section-inner-space--mob, 2); } }

.p-card-offer__meta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;

  @include media('<mob-s') {
    display: block;
    margin-bottom: 2rem; }

  &:last-child {
    margin-bottom: 0; }

  .p-card-stock, .p-card-code {
    margin-bottom: 1rem;

    @include media('<mob-s') {
      display: block; } }

  .p-card-stock {
    padding-right: 1rem; } }

.p-card-offer__buy {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 2rem;

  @include media('<mob-s') {
    display: block; }

  //.p-card-price, .p-card-options
  //  flex-basis: 50%
  //  flex-grow: 1

  .p-card-price {
    flex-shrink: 0;
    flex-grow: 1;
    $width: 50%;
    flex-basis: $width;
    max-width: $width;
    padding-right: 1rem;
    line-height: 2.4rem;

    @include media('<mob-s') {
      padding-right: 0;
      margin-bottom: 2rem;
      max-width: none; } }

  .p-card-options {
    flex-shrink: 0;
    flex-grow: 1;
    $width: 50%;
    flex-basis: $width;
    max-width: $width;

    @include media('<mob-s') {
      max-width: none; } }

  &:last-child {
    margin-bottom: 0; } }

.p-card-offer__feature {
  $lh: 1.6rem;

  position: relative;
  overflow: hidden;
  padding: 0 2.2rem 0 0;
  margin-top: 2rem;
  font-size: 1.4rem;
  line-height: $lh;

  &#jsTruncateFeatures {
    max-height: #{round-float(($lh + 0.3) * 5, 1)};

    @include media('<tab') {
      max-height: none;
      height: auto !important; }

    .no-js & {
      max-height: none; } }

  &-list {
    margin: 0;
    padding: 0;
    list-style: none;
    color: #858585;

    li {
      padding-left: 0;
      margin-bottom: 0.3rem;

      &::before {
        content: none; }

      & > span {
        display: inline-block;
        color: #282828; } } } }

.truncate-feature {
  &__shadow {
    position: absolute;
    bottom: -1rem;
    left: -1rem;
    right: -1rem;
    height: 2.9rem;
    $color: white;
    box-shadow: 0 0 1rem white;
    @include linear-gradient(to left, $color 0%, rgba($color, 1) 55%, rgba($color, 0.5) 85%, rgba($color, 0) 100%);
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    z-index: 9;

    @include media('<tab') {
      display: none; } }

  &__control {
    height: 2.2rem;
    width: 100%;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 10;

    @include media('<tab') {
      display: none; } }

  &__btn {
    height: 100%;
    width: 100%;
    position: absolute;
    right: 0;
    bottom: 0;
    @include font(0, 0);
    color: $text-color;
    transition: color $animation-speed;

    @include media('<tab') {
      display: none; }

    & > span + span {
      display: none; }

    svg {
      $size: 1.2rem;

      @include size($size);
      display: block;
      position: absolute;
      right: 2px;
      bottom: 5px;
      transition: transform $animation-speed;

      path {
        stroke: currentColor; } }

    // active
    &.active {
      width: 2.2rem;

      svg {
        transform: rotate(180deg) translateZ(0); } }

    // hover
    &:hover {
      .no-touch & {
        color: $base-color; } } } }


// payment
.p-card-offer__payment {
  font-size: 1.4rem;
  line-height: 2rem;
  color: #7e7e7e;
  margin-top: 2.5rem;
  padding: 1rem 2rem 1rem 2rem;
  border-left: 3px solid $base-color;
  background-color: rgba($base-color, 0.1);
  border-radius: 0 4px 4px 0;

  &-title {
    display: block;
    position: relative;
    @include font-bold;
    font-size: 1.6rem;
    line-height: 2.0rem;
    color: $title-color;
    margin-bottom: 0.5rem; } }

// advantages
.p-card__advantages {
  font-size: 1.6rem;
  line-height: 2.4rem;
  margin-bottom: 2rem;

  &-title {
    display: block;
    position: relative;
    @include font-bold;
    font-size: 2rem;
    line-height: 2.8rem;
    color: $title-color;
    margin-bottom: 0.5rem; } }

// advice
.p-card__advice {
  margin-top: 2rem; }

// credit
.credit {
  position: relative; }

// nav
.credit-nav {
  position: relative;

  @include media('<mob') {
    max-width: 24rem;
    margin-bottom: 1rem; }

  &:last-child {
    margin-bottom: 0; } }

// select
.credit-select {
  position: relative;
  display: block;
  $line-height: 4.4rem;
  height: $line-height;
  width: 100%;
  padding: 0 3.6rem 0 2rem;
  @include font-bold;
  @include font(1.6rem, $line-height);
  color: $text-color;
  text-decoration: none;
  cursor: pointer;
  background-color: #f9f7ff;
  border-radius: $line-height;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: background-color, color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  & > i {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    @include size(3.6rem, 100%);
    user-select: none;
    //-webkit-tap-highlight-color: transparent

    svg {
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      height: 0.6rem;
      width: 1rem;
      transform: translate(-50%, -50%);

      &, path {
        fill: currentColor; } } }

  & > span {
    display: inline-block;
    vertical-align: top;
    @include truncate(100%); }

  em, i {
    @include font-normal;
    font-style: normal; }

  & > em {
    display: none; }

  @include media('>=mob') {
    display: none; } }

// thumbs
.credit-thumbs {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  cursor: default;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  @include media('<mob') {
    display: block; // Not flex
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 24rem;
    background-color: white;
    border-radius: 1.2rem;
    box-shadow: 0 1px 2px rgba(black, 0.15);
    z-index: 9;
    // hide
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: left top;
    $translate: -0.2rem, -0.2rem;
    $scale: 0.8, 0.8;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    // transition
    $prop: transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  a {
    position: relative;
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    color: #000;
    font-size: 1.4rem;
    line-height: 2.0rem;
    padding: 1rem 3.3rem 0.9rem 1.6rem;
    border-radius: 4px 4px 0 0;
    // transition
    $prop: background-color, color, box-shadow;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }
    user-select: none;
    //-webkit-tap-highlight-color: transparent

    @include media('<lap-s') {
      font-size: 1.3rem; }

    @include media('<mob') {
      display: block;
      margin: 0;
      padding: 1.2rem 1.4rem;
      border-radius: 0;

      span {
        border: none; }

      &:hover {
        .no-touch & {
          color: white;
          background-color: $base-color-light; } } }

    & > em {
      $size: 1.2rem;
      $arr-size: 0.4rem;
      position: absolute;
      right: 1.2rem;
      top: 50%;
      @include size($size);
      margin-top: -#{round-float($size / 2, 1)};
      border-radius: round-float($size / 2, 1);
      background-color: #d7d0ef;
      text-indent: -9999px;
      overflow: hidden;
      // transition
      $prop: transform;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

      @include media('<mob') {
        display: none; }

      &::before {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        height: $arr-size;
        width: $arr-size;
        margin: {}
        border: solid white;
        border-width: 0 0 1px 1px;
        margin: -0.3rem -0.2rem;
        transform: rotate(-45deg); } }

    // hover
    &:hover {
      background-color: #d7d0ef; }

    // active
    //&:active
    //top: 1px

    // is open
    &.tabs-active {
      @include font-bold;
      background-color: #f9f7ff;
      pointer-events: none;

      & > em {
        transform: rotate(0.5turn);
        background-color: $base-color; }

      @include media('<mob') {
        color: $base-color;
        background-color: rgba($base-color-light, 0.2); } } }

  // Select opened
  &.tabs-select-open {
    @include media('<mob') {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1); } } }

// panels
.credit-panel {
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: $text-color-light;
  background-color: #f9f7ff;
  border: 2px solid #f9f7ff;
  border-radius: 0 0 1px 1px;

  p {
    margin-bottom: 1.9rem;

    &:last-child {
      margin-bottom: 0; } } }

.credit-panel__title {
  padding: 2.5rem 2rem 2rem;
  margin-bottom: 0; }

.credit-panel__content {
  background-color: white;
  padding: 2.2rem nth($box-inner-space, 2) 2rem;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

// credit row
.credit-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;

  @include media('<mob-s') {
    display: block; }

  &:last-child {
    margin-bottom: 0; }

  ul {
    list-style: none;
    padding: 0;
    margin: 0 0 1.9rem;

    li {
      position: relative;
      margin-top: 0.7rem;

      &:first-child {
        margin-top: 0; }

      &::before {
        content: '-';
        color: $base-color;
        margin-right: 5px; } }

    &:last-child {
      margin-bottom: 0; } } }

.credit-info__col {
  flex-grow: 1;
  flex-basis: 50%;

  [class*="btn"] {
    display: block; }

  &:first-child {
    padding-right: 2rem;

    @include media('<mob-s') {
      padding-top: 0;
      margin-bottom: 1rem; } } }

// card-info
.card-info {
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: $text-color;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

.card-info__title {
  display: block;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem; }

.card-info__content {
  strong {
    @include font-bold; }

  mark {
    color: $base-color;
    background: none; } }

// credit note
.credit-note {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;

  @include media('<mob-s') {
    display: block; }

  &:last-child {
    margin-bottom: 0; } }

.credit-note__col {
  flex-grow: 1;
  flex-basis: 50%;

  &:first-child {
    padding-right: 2rem;

    @include media('<mob-s') {
      margin-bottom: 1rem; } } }

// aside
.p-card__aside {
  width: $card-cont-width;
  float: right;
  clear: right;
  padding-right: 0;
  margin-top: -0.4rem;

  @include media('<net') {
    width: $card-cont-width--net; }

  @include media('<tab') {
    float: none;
    width: auto;
    margin-top: 0;
    margin-bottom: 4rem; }

  // offer
  .p-card-offer {
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0; } } }

.cards-slider {
  padding: 0 2rem;

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none; } }

$card-size: 85px 135px;
.cards-slider__slide {
  &, .swiper-container-android & {
    position: relative;
    height: nth($card-size, 1);
    width: round(nth($card-size, 2) * 0.7);
    margin-top: 2px;
    //border: solid transparent
    //border-width: 3px 1px
    box-sizing: content-box;
    $transform: 0, 32px;
    transform: translate($transform);
    transform: translate3d($transform, 0);
    cursor: default;
    @include clear; } }

.cards-slider-figure {
  border-radius: 10px;
  float: left;
  overflow: hidden;
  height: nth($card-size, 1);
  max-width: round(nth($card-size, 2) * 0.7);
  $transform: 0, 0;
  transform: translate($transform);
  transform: translate3d($transform, 0);
  // transition
  $prop: box-shadow, transform, max-width;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .cards-slider__slide:last-child & {
    float: right; } }

.cards-slider-img {
  height: nth($card-size, 1);
  width: nth($card-size, 2);
  text-indent: -9999px;
  overflow: hidden;
  border-radius: 10px;
  background-color: $base-color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.cards-slider__slide {
  // hover and active
  &:hover, &.current-card {
    z-index: 2;

    .cards-slider-figure {
      max-width: nth($card-size, 2);
      box-shadow: 0 6px 15px rgba(0, 0, 0, 0.5);
      position: relative;
      $transform: -5px, -16px;
      transform: translate($transform);
      transform: translate3d($transform, 0); } }

  &:hover {
    z-index: 3; }

  &.current-card {
    z-index: 2; } }

.cards-slider-nav {
  position: relative;
  height: 0;
  margin: 0 2rem;
  z-index: 5; }

.cards-slider__prev,
.cards-slider__next {
  $size: 38px;
  display: block;
  position: absolute;
  top: 50%;
  width: $size;
  height: $size;
  color: #c3c3c3;
  border-radius: 50%;
  margin-top: -#{round-float($size / 2, 0)};
  background-color: white;
  border: 1px solid currentColor;
  cursor: pointer;
  outline: none;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  // transition
  $prop: color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  & > span {
    @include hide-text; }

  svg {
    $size: 13px 19px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: nth($size, 1);
    width: nth($size, 2);
    margin: -#{round-float(nth($size, 1) / 2, 0)} 0 0 -#{round-float(nth($size, 2) / 2, 0)};

    path {
      fill: currentColor; } }

  &:not(.swiper-button-disabled):not(:hover):focus {
    box-shadow: 0 0 0 2px rgba(77, 144, 254, 0.5); }

  &:not(.swiper-button-disabled):hover {
    color: $base-color; }

  //&:not(.swiper-button-disabled):active
  //top: calc(50% + 1px)

  &.swiper-button-disabled {
    background-color: #f1f1f1;
    cursor: default;
    opacity: 0.3; } }

.cards-slider__prev {
  left: 0; }

.cards-slider__next {
  right: 0; }

/* product card end */

/* product prompts */
.p-prompts {
  position: relative; }

.p-prompts-tabs__nav {
  margin: 0 #{nth($section-inner-space, 2)};
  padding-bottom: 0;
  border: none;
  z-index: 5;

  @include media('<lap-s') {
    margin: 0 #{nth($section-inner-space--tab, 2)}; }

  @include media('<tab') {
    margin: 0 0 2rem; } }

.p-prompts-tabs__panel-nav {
  position: relative;
  //left: #{nth($section-inner-space, 2)}
  //right: #{nth($section-inner-space, 2)}
  //top: 0
  padding: 1.1rem #{nth($section-inner-space, 2)} 0;
  margin: 0.2rem 10rem -4.1rem 0;
  z-index: 3;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<tab') {
    padding: 0;
    margin-top: 0; }

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: -10rem;
    border-top: 1px solid #c5c5c5;

    @include media('<tab') {
