      margin-bottom: 0; } } }

.p-infobar-tabs__nav {
  margin-bottom: 2rem; }

// product info list
.p-infobar-list {
  list-style: none;
  padding: 0;
  margin: 0 0 3rem;
  column-count: 3;
  column-gap: 7rem;
  font-size: 1.4rem;
  line-height: 1.8rem;

