    $line-length: 3;
    min-height: $line-height * $line-length;
    font-size: 1.6rem;
    line-height: $line-height; }

  @include media('<mob-s') {
    min-height: 0; }

  &:last-child {
    margin-bottom: 0; } }

// hover
a.news-elem:hover {
  .news-elem__title {
    color: $base-color; } }

// alternative view
.news-elem_alt {
  box-shadow: none;

  .news-elem__content {
    //display: flex
    //flex-direction: column
    flex-direction: row;
    align-content: flex-end;
