/* news entry */
.news-entry {
  overflow: hidden; }

$news-entry-item-space-outer: 1.8rem 1.8rem;
.news-entry__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -#{round-float(nth($news-entry-item-space-outer, 2) / 2, 1)} -#{nth($news-entry-item-space-outer, 1)}; }

.news-entry__list-item {
  width: 25%;
  padding: 0 round-float(nth($news-entry-item-space-outer, 2) / 2, 1) nth($news-entry-item-space-outer, 1);

  @include media('<net') {
    width: 35%; }

  @include media('<tab-s') {
    width: 50%; }

  @include media('<mob-s') {
    width: 100%; }

