/* news previews list end */

/* cart item */
// figure
$cart-item-figure-width: 23.9rem;
$cart-item-figure-height: 14rem;
$cart-item-figure-kef: $cart-item-figure-height / $cart-item-figure-width;
$cart-item-figure-space: 2.7rem;
// ==
$cart-item-figure-width--tab: 18rem;
$cart-item-figure-height--tab: round-float($cart-item-figure-width--tab * $cart-item-figure-kef, 1);
$cart-item-figure-space--tab: 2rem;
// ==
$cart-item-figure-width--mob: 16rem;
$cart-item-figure-height--mob: round-float($cart-item-figure-width--mob * $cart-item-figure-kef, 1);
$cart-item-figure-space--mob: 0;

.cart-item {
  $element: &;
  display: block;
  text-decoration: none;
  position: relative;
  color: $title-color;
  padding: 0 6rem 0 #{nth($section-inner-space, 2)};
  @include media('<lap-s') {
    padding: 0 4rem 0 #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)}; }

  @include media('<mob-s') {
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

//@at-root
//a#{$element}:hover
//  color: $base-color

.cart-item__box {
  $element: &;
  display: flex;
  //flex-wrap: wrap
  align-items: flex-start;
  //justify-content: flex-start
  justify-content: space-between;
  padding: 1.8rem 0;

  @include media('<mob') {
    align-items: flex-end; }

  @include media('<mob-s') {
    display: block; } }

.cart-item__main {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding-right: nth($section-inner-space, 2);

  @include media('<lap-s') {
    padding-right: nth($section-inner-space--tab, 2); }

  @include media('<mob') {
    display: block;
    padding-right: nth($section-inner-space--mob, 2); }

  @include media('<mob-s') {
    padding-right: 0;
    margin-bottom: 2rem; }

  &:last-child {
    margin-bottom: 0; } }

.cart-item__figure {
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: $cart-item-figure-width;
  height: $cart-item-figure-height;
  padding-right: $cart-item-figure-space;
  text-decoration: none;
  user-select: none;
  //-webkit-tap-highlight-color: transparent
  color: inherit;

  @include media('<lap-s') {
    width: $cart-item-figure-width--tab;
    height: $cart-item-figure-height--tab;
    padding-right: $cart-item-figure-space--tab; }

  @include media('<mob') {
    width: $cart-item-figure-width--mob;
    height: $cart-item-figure-height--mob;
    padding-right: $cart-item-figure-space--mob;
    margin-bottom: 1rem; }

  @include media('<mob-s') {
    width: auto;
    justify-content: flex-start; } }

.cart-item__figure-img {
  display: block;
  max-height: 100%;
  max-width: 100%; }

// content
//.cart-item__content
//display: flex
//flex-grow: 1
//flex-shrink: 0
//justify-content: space-between
//align-items: flex-start
//$width: calc(100% - #{$cart-item-figure-width})
//flex-basis: $width
//max-width: $width

//@include media('<lap-s')
//  $width: calc(100% - #{$cart-item-figure-width--tab})
//  flex-basis: $width
//  max-width: $width

// caption
.cart-item__caption {
  margin-bottom: 2rem;
  padding-top: 2rem;

  @include media('<mob') {
    padding-top: 0; }

  &:last-child {
    margin-bottom: 0; } }

// title
.cart-item__title {
  display: block;
  margin-bottom: 0.6rem;
  @include font-bold;
  font-size: 1.6rem;
  line-height: 2.4rem;
  text-decoration: none;
  color: currentColor;
  transition: color $animation-speed;

  @include media('<mob') {
    font-size: 1.8rem; }

  &:last-child {
    margin-bottom: 0; }

  .cart-item__figure:hover ~ .cart-item__content &,
  &:hover {
    .no-touch & {
      color: $base-color; } } }

// rate
.cart-item__rate {
  margin: 0 0 1.8rem -0.3rem;

  &:last-child {
    margin-bottom: 0; } }

// order
.cart-item__order {
  flex-shrink: 0;
  text-align: right;
  $width: 22rem;
  min-width: $width;
  max-width: $width;
  padding: 1.7rem 0 0;

  @include media('<lap-s') {
    $width: 20rem;
    min-width: $width;
    max-width: $width; }

  @include media('<mob-s') {
    min-width: 0;
    max-width: none;
    padding: 0;
    text-align: left; } }

// stock
.cart-item__stock {
  margin-bottom: 1.8rem;

  @include media('<mob-s') {
    display: none; }

  &:last-child {
    margin-bottom: 0; } }

// counter
.cart-item__counter {
  margin-bottom: 1.5rem;

  &:last-child {
    margin-bottom: 0; } }

// sum
.cart-item__sum {
  & > em {
    @include font-normal; } }

// price
.cart-item__price {
  line-height: 3.2rem;
  // Это значение важно! Оно выравнивает старую и новую цену по базовой линии
  // Для старой и новой цен line-height устанавливить в значение inherit
  margin-bottom: 1.1rem;

  @include media('<mob-s') {
    direction: rtl;
    margin-bottom: 0.5rem; }

  &:last-child {
    margin-bottom: 0; } }

// price old
.cart-item__price-old {
  display: inline-block;
  vertical-align: baseline;
  @include oldPriceStyle(1.8rem, 1.8rem, $text-color-ultra-light);

  @include media('<lap-s') {
    & > strong {
      font-size: 1.6rem; }

    & > em {
      font-size: 1.6rem; } }

  @include media('<mob-s') {
    direction: ltr; }

  .cart-item__price.off & {
    color: $text-color-ultra-light; } }

// price new
.cart-item__price-new {
  display: inline-block;
  vertical-align: baseline;
  margin-left: 0.4rem;
  @include newPriceStyle(3.0rem, 3.0rem, $title-color);

  @include media('<lap-s') {
    & > strong {
      font-size: 2.4rem; }

    & > em {
      font-size: 2.4rem; } }

  @include media('<mob-s') {
    direction: ltr; }

  .cart-item__price.off & {
    color: $text-color-ultra-light; } }

.cart-item__price-note {
  @include font-bold;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-transform: uppercase;
  color: $form-error-color;
  padding-top: 0.5rem;

  @include media('<mob') {
    font-size: 1.2rem;
    line-height: 1.6rem; }

  @include media('<mob-xs') {
    font-size: 1.0rem;
    line-height: 1.6rem; } }

// remove btn
.cart-item__remove {
  $size: 1.8rem;
  position: absolute;
  right: 1.2rem;
  top: 0.9rem;
  width: $size;
  height: $size;
  color: white;
  cursor: pointer;
  background-color: #cecece;
  border-radius: $size;
  text-indent: -9999px;
  overflow: hidden;
  transform: rotate(-90deg) translateZ(0);
  transition: background-color $animation-speed;

  @include media('<mob') {
    $size: 2.4rem;
    width: $size;
    height: $size;
    border-radius: $size; }

  svg {
    display: block;
    $size: 0.8rem;
    @include size($size);
    position: absolute;
    left: 50%;
    top: 50%;
    $space: round-float($size / 2, 1);
    margin: -#{$space} 0 0 -#{$space};

    @include media('<mob') {
      $size: 1.4rem;
      @include size($size);
      $space: round-float($size / 2, 1);
      margin: -#{$space} 0 0 -#{$space}; } }

  path {
    fill: currentColor; }

  // hover
  &:hover {
    background-color: $base-color; } }

/* cart item end */

/* cart */
.cart__list {
  border: solid $border-color;
  border-width: 1px 0;
  background-color: white;

  @include media('<tab') {
    margin-bottom: #{nth($section-outer-space--tab, 1)};
    border-bottom: none; } }

.cart__list-item {
  .cart-item__box {
    border-top: 1px solid $border-color; } }

.cart__list-item:first-child {
  .cart-item__box {
    border-top: none; } }

.cart__empty {
  display: none;
  padding: 0 #{nth($section-inner-space, 2)} 1.8rem;
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

/* cart end */

/* cart similar */
.cart__similar {
  position: relative;
  background-color: white;
  padding-top: 1.5rem;

  &:first-child {
    border-top: 0; } }

.cart__similar-caption {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  //position: absolute
  position: relative;
  padding-left: #{nth($section-inner-space, 2)};
  padding-right: #{nth($section-inner-space, 2)};
  //top: 0
  margin: 0 10rem -4.5rem 0;
  z-index: 3;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)};
    margin-right: 8rem; }

  @include media('<mob') {
    display: block;
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.cart__similar-title {
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  margin: 0 4rem 0 0;

  @include media('<lap-s') {
    margin-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    margin: 0 0 1rem; } }

/* cart similar end */

/* cart banner */
.cart-banner {
  display: block;
  text-decoration: none;

  //img
  //margin: 0 auto
  //max-width: 10rem

  //&[data-loaded="true"]
  //  width: 100%
 }  //  max-width: none

/* cart banner end */

/* delivery */
.delivery {
  padding: 2rem #{nth($box-inner-space, 2)};
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.delivery-item {
  display: flex;
  align-items: center;
  font-size: 1.4rem;
  line-height: 2.0rem;
  margin-bottom: 2.4rem;

  &:last-child {
    margin-bottom: 0; } }

.delivery__icon {
  margin-right: 3rem;

  @include media('<lap-s') {
    margin-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    margin-right: #{nth($section-inner-space--mob, 2)}; }

  & > i {
    display: inline-block;
    vertical-align: top;
    $size: 2.8rem;
    height: $size;
    width: $size; }

  svg {
    display: block;
    $size: 2.8rem;
    height: $size;
    width: $size; }

  path {
    fill: $base-color; } }

.delivery__text {
  flex-grow: 1;

  & > strong {
    display: block; }

  mark {
    background: none;
    color: $base-color; } }

/* delivery end */

/* summary */
.summary {
  padding: 1rem #{nth($box-inner-space, 2)};
  @include font(1.4rem, 2.0rem);
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.summary__item,
.summary__total {
  display: flex;
  align-items: flex-end;
  justify-content: space-between; }

.summary__item {
  mark {
    background: none;
    color: $base-color; } }


.summary__total {
  @include font-bold;
  font-size: 1.8rem;
  margin-top: 0.4rem; }

/* summary end */

/* consist */
.consist {
  padding: 1rem #{nth($box-inner-space, 2)};
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; } }

.consist__title {
  @include font-bold;
  font-size: 2.4rem;
  line-height: 3.2rem;
  color: $title-color;
  margin-bottom: 2rem; }

.consist__overflow {
  position: relative;

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    height: 1.5rem;
    width: 100%;
    left: 0;
    z-index: 1; }

  &::before {
    top: 0;
    $color: white;
    @include linear-gradient(to bottom, $color 0, rgba($color, 0) 100%); }

  &::after {
    bottom: 0;
    $color: white;
    @include linear-gradient(to top, $color 0, rgba($color, 0) 100%); } }

.consist__list {
  margin-bottom: 4rem;
  max-height: 35rem;
  padding-right: 1rem;
  overflow-y: auto;

  &[data-simplebar*="init"] {
    overflow: auto; }

  &:last-child {
    margin-bottom: 0; } }

.consist__item {
  position: relative;
  $element: &;
  padding: 1rem 3rem 1rem 0;
  @include clear;

  //&_not-available

  @include media('<mob') {
    padding-right: 3.5rem; } }

.consist__item-figure {
  $width: 10rem;
  $height: 7.8rem;
  $ratio: $height / $width;
  float: left;
  display: flex;
  align-items: center;
  justify-content: center;
  //width: 11.8rem
  width: $width;
  height: round-float($width * $ratio, 1);
  margin-right: 2.0rem;

  .consist__item_no-available & {
    opacity: 0.2; }

  @include media('<lap-s') {
    $width: 7rem;
    width: $width;
    height: round-float($width * $ratio, 1);
    margin-right: 1.5rem; }

  img {
    display: block;
    max-width: 100%;
    max-height: 100%; } }

.consist__item-content {
  overflow: hidden;

  .consist__item_no-available & {
    opacity: 0.3; } }

.consist__item-title {
  display: block;
  margin-bottom: 1rem;
  @include font-normal;
  font-weight: normal;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: $title-color;
  transition: color $animation-speed; }

.consist__item-meta {
  font-size: 1.4rem;
  line-height: 2.0rem; }

.consist__item-count {
  display: inline-block;
  color: $text-color-ultra-light;
  margin-right: 0.5rem;
  vertical-align: top; }

.consist__item-price {
  display: inline-block;
  vertical-align: top;

  &-old {
    display: inline-block;
    font-size: 1.2rem;
    color: $text-color-ultra-light;
    text-decoration: line-through;
    margin-right: 0.5rem; }

  &-new {
    display: inline-block;
    @include font-bold;
    color: $text-color; } }

.consist__item-warning {
  clear: both;
  margin: 0.5rem -2rem 0 0;
  text-align: right;
  @include font-bold;
  font-size: 1.8rem;
  color: $form-error-color; }

.consist__item-note {
  color: $form-error-color;
  @include font-bold;
  font-size: 1.2rem;
  text-transform: uppercase;
  padding-top: 0.2rem; }

.consist__item-remove {
  position: absolute;
  right: 0.5rem;
  top: 1rem;
  $size: 1.8rem;
  width: $size;
  height: $size;
  color: white;
  //background-color: #d8d8d8
  //background-color: $base-color-light
  background-color: #cecece;
  border-radius: $size;
  text-indent: -9999px;
  cursor: pointer;
  overflow: hidden;
  transform: rotate(-90deg) translateZ(0);
  transition: background-color $animation-speed;
  outline: none;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  @include media('<mob') {
    $size: 2.4rem;
    width: $size;
    height: $size;
    border-radius: $size; }

  & > em {
    $size: 0.8rem;
    @include icon($size);
    position: absolute;
    left: 50%;
    top: 50%;
    $space: round-float($size / 2, 1);
    margin: -#{$space} 0 0 -#{$space};
    background-image: url(data:image/svg+xml;base64,);

    @include media('<mob') {
      $size: 1.4rem;
      @include size($size);
      $space: round-float($size / 2, 1);
      margin: -#{$space} 0 0 -#{$space}; } }

  // hover
  &:hover {
    .no-touch & {
      background-color: $base-color; } }

  // focus
  &:focus {
    .no-touch & {
      box-shadow: 0 0 0 2px rgba($focus-color, 0.5); } } }

// hover (if use link)
a.consist__item {
  display: block;
  text-decoration: none;
  color: inherit;
  outline: none;

  &:hover {
    .consist__item-title {
      color: $base-color; } } }

/* consist end */

/* clearance */
.clearance {
  position: relative; }

