// ===
$cart-container-aside-width--tab: 28rem;
$cart-container-aside-width-full--tab: $cart-container-aside-width--tab + $cart-container-inside-space;

.cart {
  margin-bottom: nth($section-outer-space, 1);
  @include clear;

  @include media('<tab-s') {
    margin-bottom: nth($section-outer-space--tab, 1);

    &::after {
      content: none; } }

  & > form {
    display: block;
    @include clear; } }

.cart__base, .cart__main, .cart__similar, .cart-banner {
  float: left;
  clear: left;
  width: calc(100% - #{$cart-container-aside-width-full});
  background-color: white;

  @include media('<net') {
    width: calc(100% - #{$cart-container-aside-width-full--net}); }

  @include media('<lap-s') {
    width: calc(100% - #{$cart-container-aside-width-full--tab}); }

  @include media('<tab') {
    float: none;
    width: auto;
    margin-bottom: nth($section-outer-space--tab, 1); }

  &:last-child {
    margin-bottom: 0; }

  .cart__base & {
    float: none;
    clear: none;
    width: auto; } }

.cart__base {
  background: none; }

.cart__aside {
  position: sticky;
  top: $header-height--scrolled + 2;
  float: right;
  clear: right;
  width: $cart-container-aside-width;
  margin-left: $cart-container-inside-space;

  @include media('<net') {
    width: $cart-container-aside-width--net; }

  @include media('<lap-s') {
    width: $cart-container-aside-width--tab; }

  @include media('<tab') {
    position: relative;
    top: auto;
    float: none;
    width: auto;
    margin: 0 0 nth($section-outer-space--tab, 1); }

  &:last-child {
    margin-bottom: 0; } }

/* cart end */

/* checkout */
.checkout {
  position: relative;
  margin-bottom: 2rem;
  padding: 0 #{nth($section-inner-space, 2)} 2rem;
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; }

  &:last-child {
    margin-bottom: 0; } }

.checkout__title {
  width: 100%;
  min-width: 100%; }

$checkout-col-inner-space: #{round-float(nth($section-inner-space, 2) / 2, 1)}; // Половина горизонтального отступа
$checkout-col-inner-space--tab: #{round-float(nth($section-inner-space--tab, 2) / 2, 1)}; // Половина горизонтального отступа
.checkout__row {
  display: flex;
  align-items: flex-start;
  margin: 0 -#{$checkout-col-inner-space};

  @include media('<lap-s') {
    margin: 0 -#{$checkout-col-inner-space--tab}; }

  @include media('<mob') {
    display: block; } }

.checkout__col {
  flex-shrink: 0;
  $width: 50.0%;
  flex-basis: $width;
  max-width: $width;
  padding: 0 #{$checkout-col-inner-space};
  //border: solid transparent
  //border-width: 0 #{$checkout-col-inner-space}

  @include media('<lap-s') {
    padding: 0 #{$checkout-col-inner-space--tab};
 }    //border-width: 0 #{$checkout-col-inner-space--tab}

  @include media('<mob') {
    max-width: none;
    margin-bottom: nth($section-outer-space--mob, 1);

    &:last-child {
      margin-bottom: 0; } } }

.checkout__subtitle {
  @include font-normal;
  font-weight: normal;
  @include font(1.8rem, 2.4rem);
  color: $title-color;
  margin-bottom: 1.5rem; }

/* checkout end */

/* payment */
.payment {
  position: relative;
  margin-bottom: 2rem;
  padding: 2rem #{nth($section-inner-space, 2)};
  background-color: white;

  @include media('<lap-s') {
    padding-left: #{nth($section-inner-space--tab, 2)};
    padding-right: #{nth($section-inner-space--tab, 2)}; }

  @include media('<mob') {
    padding-left: #{nth($section-inner-space--mob, 2)};
    padding-right: #{nth($section-inner-space--mob, 2)}; }

  &:last-child {
    margin-bottom: 0; } }

.payment__title {
  @include font-normal;
  font-weight: normal;
  @include font(1.8rem, 2.4rem);
  color: $title-color;
  margin-bottom: 1.5rem; }

/* payment end */

/* order */
$order-inside-space: 2rem;
//$order-aside-width: 41.0rem
//$order-aside-width-full: $order-inside-space + $order-aside-width

$order-aside-width: 36.2rem;
$order-inside-space: 2rem;
$order-aside-width-full: $order-aside-width + $order-inside-space;
// ===
$order-aside-width--net: 32rem;
$order-aside-width-full--net: $order-aside-width--net + $cart-container-inside-space;
// ===
$order-aside-width--tab: 28rem;
$order-aside-width-full--tab: $order-aside-width--tab + $cart-container-inside-space;
.order {
  margin-bottom: nth($section-outer-space, 1);
  @include clear;

  & > form {
    display: block;
    @include clear; } }

.order__main {
  float: left;
  clear: left;
  width: calc(100% - #{$order-aside-width-full});

  @include media('<net') {
    width: calc(100% - #{$order-aside-width-full--net}); }

  @include media('<lap-s') {
    width: calc(100% - #{$order-aside-width-full--tab}); }

  @include media('<tab') {
    float: none;
    width: auto;
    margin-bottom: nth($section-outer-space--tab, 1); }

  &:last-child {
    margin-bottom: 0; } }

.order__aside {
  float: right;
  clear: right;
  width: $order-aside-width;
  margin-left: $order-inside-space;

  @include media('<net') {
    width: $order-aside-width--net; }

  @include media('<lap-s') {
    width: $order-aside-width--tab; }

  @include media('<tab') {
    float: none;
    width: auto;
    margin: 0 0 nth($section-outer-space--tab, 1); }

  &:last-child {
    margin-bottom: 0; } }

/* order end */

/* review article */
.review {
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

.review-caption {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
  max-width: 107.6rem;
  padding-top: 5rem;
  margin: 0 auto 4rem;

  @include media('<tab') {
    display: block;
    padding-top: 2rem; }

  @include media('<mob') {
    margin-bottom: 2rem; } }

.review-caption__meta {
  flex-grow: 1;

  @include media('<tab') {
    margin-bottom: $bottom-space; }

  @include media('<mob') {
    margin-bottom: $bottom-space--mob; } }

$author-photo-size: 14.8rem;
$author-photo-size--tab: 8rem;
$author-photo-size--mob: 6rem;
.review-caption__author {
  flex-shrink: 0;
  max-width: $author-photo-size;
  margin-right: 8rem;

  @include media('<desk-s') {
    margin-right: 4rem; }

  @include media('<tab') {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: none;
    margin: 0; } }

.review-caption__author-photo {
  overflow: hidden;
  @include size($author-photo-size);
  border-radius: $author-photo-size;
  border: 0.7rem solid #efefef;
  margin-bottom: 2rem;

  @include media('<tab') {
    flex-shrink: 0;
    @include size($author-photo-size--tab);
    margin: 0 #{nth($section-inner-space--tab, 2)} 0 0;
    border-radius: $author-photo-size--tab;
    border-width: 0.4rem; }

  @include media('<mob') {
    @include size($author-photo-size--mob);
    margin-right: #{nth($section-inner-space--mob, 2)};
    border-radius: $author-photo-size--mob;
    border-width: 0.3rem; }

  img {
    display: block;
    max-width: none;
    width: 100%;
    height: 100%;
    @include object-fit(cover); } }

.review-caption__author-name {
  @include font-normal;
  @include font(2.0rem, 2.6rem);
  color: white;
  text-align: center;

  @include media('<tab') {
    font-size: 1.8rem;
    line-height: 2.4rem;
    text-align: left; } }

.review-caption__title {
  @include font-bold;
  $font-size: 5.5rem;
  $line-height: 6.9rem;
  $line-height-kef: $line-height / $font-size;
  @include font($font-size, $line-height);
  color: white;

  @include media('<desk-s') {
    $font-size: 4.8rem;
    font-size: $font-size;
    line-height: round-float($line-height-kef * $font-size, 1); }

  @include media('<tab-s') {
    $font-size: 3.6rem;
    font-size: $font-size;
    line-height: round-float($line-height-kef * $font-size, 1); }

  @include media('<mob') {
    font-size: $font-size-h1--mob;
    line-height: $line-height-h1--mob; } }

.review-caption__rate {
  .p-rate_lg {
    background-image: url(../../img/rate-xl-empty-alt.svg);
    background-size: 129px 16px; } }

.review-article-gallery-wrap {
  position: relative;
  overflow: hidden;
  margin: -6rem -#{nth($article-layout-space, 2)} $bottom-space;

  @include media('<lap') {
    padding-top: -4rem;
    margin-left: -#{nth($article-layout-space--lap, 2)};
    margin-right: -#{nth($article-layout-space--lap, 2)}; }

  @include media('<tab') {
    margin-top: -3rem;
    margin-left: -#{nth($article-layout-space--tab, 2)};
    margin-right: -#{nth($article-layout-space--tab, 2)};
    padding-left: 8%;
    padding-right: 8%; }

  @include media('<mob') {
    margin: -2rem -#{nth($article-layout-space--mob, 2)} $bottom-space--mob; }

  &:last-child {
    margin-bottom: 0; }

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    width: 10rem;
    top: 0;
    z-index: 2;
    pointer-events: none;

    @include media('<tab') {
      width: 8%; } }

  &::before {
    $color: white;
    left: 0;
    @include linear-gradient(to right, $color 0%, rgba($color, 0) 100%); }

  &::after {
    $color: white;
    right: 0;
    @include linear-gradient(to left, $color 0%, rgba($color, 0) 100%); } }

.review-article-gallery {
  position: relative;
  padding: 0 0 4.4rem;
  margin-bottom: $bottom-space;

  &:last-child {
    margin-bottom: 0; }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden; }

  &.swiper-container {
    overflow: visible; }

  .swiper-pagination {
    bottom: 1.8rem; }

  .swiper-pagination-bullet {
    background-color: $base-color;
    transform: scale(0.571) translateZ(0);
    @include size(14px); }

  .swiper-pagination-bullet-active {
    transform: scale(1) translateZ(0); }

  .swiper-slide {
    user-select: none; } }

.review-content {
  //overflow: hidden
  //padding-top: #{nth($article-layout-space, 1)}
  padding-top: 6rem;
  padding-bottom: 6rem;
  margin-bottom: 2rem;
  background-color: white;
  border-radius: 0 0 0.5rem 0.5rem;

  @include media('<lap') {
    //padding-top: #{nth($article-layout-space--lap, 1)}
    padding-top: 4rem;
    padding-bottom: 4rem; }

  @include media('<tab') {
    //padding-top: #{nth($article-layout-space--tab, 1)}
    padding-top: 3rem;
    padding-bottom: 3rem; }

  @include media('<mob') {
    //padding-top: #{nth($article-layout-space--mob, 1)}
    padding-top: 2rem;
    padding-bottom: 2rem; }

  & > p, & > ul, & > ol,
  & > h1, & > h2, & > h3, & > h4, & > h5, & > h6,
  & > .h1, & > .h2, & > .h3, & > .h4, & > .h5, & > .h6,
  & > hr, & > mark, & > blockquote,
  & > .layout-text {
    max-width: $text-layout-width; }

  .review-article-gallery {
    max-width: $text-layout-width + 5;
    @include push-auto; }

  & > figure {
    max-width: $text-layout-width + 18; }

  figure {
    figcaption {
      text-align: center; } } }

// review-features
.review-features {
  margin-bottom: 4.0rem;

  &:last-child {
    margin-bottom: 0; }

  h3 {
    @include font-bold;
    @include font(1.8rem, 2.8rem);
    text-transform: uppercase;

    @include media('<net') {
      font-size: 1.6rem;
      line-height: 2.4rem; } } }

// review-features tabs
// nav
.review-features__nav {
  position: relative;
  margin-bottom: 5.3rem;

  @include media('<tab') {
    margin-bottom: 4rem; }

  @include media('<mob') {
    max-width: 24rem;
    margin-bottom: 3rem; }

  &:last-child {
    margin-bottom: 0; } }

// select
.review-features__select {
  @include pseudo-select-common();

  @include media('>=mob') {
    display: none; } }

// thumbs
.review-features__thumbs {
  @include font(2.0rem, 3.5rem);
  cursor: default;
  user-select: none;

  @include media('<mob') {
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 24rem;
    @include font(1.6rem, 2.4rem);
    background-color: white;
    border-radius: 1.2rem;
    box-shadow: 0 1px 2px rgba(black, 0.15);
    z-index: 9;
    // hide
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: left top;
    $translate: -0.2rem, -0.2rem;
    $scale: 0.8, 0.8;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
    // transition
    $prop: transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  a {
    display: inline-block;
    margin-right: 7.7rem;
    padding: 1.7rem 0.7rem 0;
    border-top: 0.8rem solid transparent;
    text-decoration: none;
    color: #000;
    user-select: none;
    -webkit-tap-highlight-color: transparent;

    @include media('<tab') {
      margin-right: 4rem;
      padding-top: 1rem; }

    @include media('<mob') {
      display: block;
      margin: 0;
      padding: 1.2rem 1.4rem;
      border-radius: 0;
      border: none;

      span {
        border: none; }

      &:hover {
        .no-touch & {
          color: white;
          background-color: $base-color-light; } } }

    &.tabs-active {
      color: $base-color;
      border-top-color: $base-color;
      cursor: default;
      pointer-events: none;

      @include media('<mob') {
        color: $base-color;
        background-color: rgba($base-color-light, 0.2); } } }

  // Select opened
  &.tabs-select-open {
    @include media('<mob') {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1); } } }

// text truncate
.truncate-text {
  position: relative;
  overflow: hidden;
  margin-bottom: $bottom-space;
  max-height: #{round-float($line-height * 20, 1)};

  .no-js & {
    max-height: none; }

  p:last-child {
    margin-bottom: 0; } }

.tt__shadow {
  position: absolute;
  bottom: -1rem;
  left: -1rem;
  height: 4rem;
  width: calc(100% + 2rem);
  $color: white;
  @include linear-gradient(to top, $color 0%, rgba($color, 1) 35%, rgba($color, 0.5) 70%, rgba($color, 0) 100%);
  user-select: none;
  -webkit-tap-highlight-color: transparent; }

// read more
.read-more {
  text-align: center; }

.btn-read-more {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0.5rem 2.2rem 0.5rem 4.3rem;
  @include font-bold;
  @include font(1.4rem, 2.0rem);
  text-decoration: none;
  color: $base-color;
  border: 2px solid #edebe6;
  border-radius: 3rem;
  transition: border-color $animation-speed;

  & > span + span {
    display: none; }

  svg {
    display: block;
    position: absolute;
    left: 1.6rem;
    top: 50%;
    $size: 1rem;
    @include size($size);
    margin-top: -#{round-float($size / 2, 1)};
    // transition
    $prop: transform;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; } }

  // active
  &.active {

    svg {
      transform: rotate(180deg) translateZ(0); } }

  // hover
  &:hover {
    .no-touch & {
      border-color: $base-color; } } }

// conclusion
.review-conclusion {
  display: flex;
  margin-bottom: $bottom-space;
  border: 1px solid $border-color;

  @include media('<tab') {
    display: block; }

  &:last-child {
    margin-bottom: 0; } }

.review-conclusion__item {
  flex-shrink: 0;
  $width: 33.33%;
  flex-basis: $width;
  max-width: $width;
  padding: 3rem;
  @include font(1.6rem, 2.3rem);
  color: $text-color-light;
  text-align: center;
  box-shadow: 0 0 0 1px $border-color;
  background-color: white;

  @include media('<lap-s') {
    padding: $section-inner-space--tab; }

  @include media('<tab') {
    max-width: none;
    text-align: left; }

  @include media('<mob') {
    padding: $section-inner-space--mob; }

  p {
    margin-bottom: 2.3rem;

    &:last-child {
      margin-bottom: 0; } } }

.review-conclusion__item-caption {
  margin-bottom: $bottom-space;

  @include media('<tab') {
    display: flex;
    align-items: center; }

  @include media('<mob') {
    margin-bottom: $bottom-space--mob; }

  & > i {
    display: block;
    margin: 0 auto 2rem;
    $size: 5.8rem 6.8rem;
    height: nth($size, 1);
    width: nth($size, 2);

    @include media('<tab') {
      margin: 0 #{nth($section-inner-space--tab, 1)} 0 0;
      @include size(4.8rem, 4.1rem); } }

  //svg
  //display: block
  //margin: 0 auto 2rem
  //$size: 5.8rem 6.8rem
  //height: nth($size, 1)
  //width: nth($size, 2)

  h3, h4 {
    @include font-bold;
    @include font(1.6rem, 2.3rem);
    color: $title-color;
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0; } } }

/* review article end */

/* default overlay */
.popup-overlay,
.filters-shutter-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: $z-overlay;
  background-color: rgba($base-color, 0.9);
  user-select: none;
  pointer-events: none;
  opacity: 0;
  $prop: opacity;
  will-change: $prop;
  transition: $prop $animation-speed; }

//.is-open &
//&
//pointer-events: auto
//opacity: 1

.filters-shutter-overlay.filters_is-open {
  @include media('<#{$p-filters-breakpoint}') {
    pointer-events: auto;
    opacity: 1; } }

/* default overlay end */

/* products compare */
.products-compare {
  table {
    width: 100%;
    margin: 0 auto;
    min-width: 0;

    tr {
      td, th {
        max-width: 20rem;
        min-width: 20rem;
        vertical-align: top;
        padding: 0;
        @include font-normal;
        @include font(1.4rem, 2.2rem);
        letter-spacing: normal;
        color: $text-color;
        text-align: left;
        background-color: white;
        border-top: 1px solid $border-color-light;
        border-left: 1px solid $border-color;

        &:first-child {
          width: 30%; }

        &:last-child {
          border-right: 1px solid $border-color; }

        &:first-child, &:first-child {
          border-left-width: 1px; }

        &:last-child, &:last-child {
          border-right-width: 1px; }

        & > div, & > a {
          padding: 0.4em 1.2rem;
          position: relative;
          height: 100%; }

        &:first-child {
          max-width: 10rem; }

        &.products-compare_accent {
          background-color: tint($base-color, 20%); } }

      &:hover {
        td {
          background-color: $bg-color-alt;

          &.products-compare_accent {
            background-color: tint($base-color, 35%); } } }

      &.products-compare__row-intend {
        td {
          @include font(0, 0);
          padding-top: 0.5rem;
          border-top: none; }

        &:hover {
          td {
            background: none; } }

        & + tr {
          td {
            border-top: none; } } }

      &.products-compare__title {
        td {
          font-size: 1.6rem;
          background-color: #f3f3f3;
          border-top: 1px solid $border-color;
          border-bottom: 1px solid $border-color; } } }

    .products-compare__caption {
      tr {
        td, th {
          font-size: 3.2rem;
          border-top: 1px solid $border-color;
          border-bottom: 1px solid $border-color;

          a {
            display: block;
            text-decoration: none;
            color: currentColor; } }

        &:hover {
          td, th {
            background: none; } } } } } }

.products-compare__prod-figure {
  height: 12rem;
  margin: 0 0 1rem;

  img {
    display: block;
    max-width: 100%;
    max-height: 100%; } }

.products-compare__prod-title {
  @include font-bold;
  font-size: 1.6rem;
  line-height: 1.2;
  margin-bottom: 1rem;
  transition: color $animation-speed;

  .no-touch a:hover & {
    color: $base-color; } }

.products-compare__prod-price {
  @include font-bold;
  font-size: 2.4rem;
  line-height: 1.2;
  margin-bottom: 1rem; }

.products-compare_on {
  color: $form-success-color; }

.products-compare_off {
  color: $form-error-color; }

.products-compare__remove {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  $size: 1.8rem;
  width: $size;
  height: $size;
  color: white;
  //background-color: #d8d8d8
  //background-color: $base-color-light
  background-color: #cecece;
  border-radius: $size;
  text-indent: -9999px;
  cursor: pointer;
  overflow: hidden;
  transform: rotate(-90deg) translateZ(0);
  transition: background-color $animation-speed;
  outline: none;
  user-select: none;
  //-webkit-tap-highlight-color: transparent

  @include media('<mob') {
    $size: 2.4rem;
    width: $size;
    height: $size;
    border-radius: $size; }

  & > em {
    $size: 0.8rem;
    @include icon($size);
    position: absolute;
    left: 50%;
    top: 50%;
    $space: round-float($size / 2, 1);
    margin: -#{$space} 0 0 -#{$space};
    background-image: url(data:image/svg+xml;base64,);

    @include media('<mob') {
      $size: 1.4rem;
      @include size($size);
      $space: round-float($size / 2, 1);
      margin: -#{$space} 0 0 -#{$space}; } }

  // hover
  &:hover {
    .no-touch & {
      background-color: $base-color; } }

  // focus
  &:focus {
    .no-touch & {
      box-shadow: 0 0 0 2px rgba($focus-color, 0.5); } } }

