.flatpickr-months .flatpickr-month,
.flatpickr-current-month .flatpickr-monthDropdown-months,
span.flatpickr-weekday {
  background-color: $base-color; }

.flatpickr-months .flatpickr-next-month:hover svg, .flatpickr-months .flatpickr-prev-month:hover svg {
  fill: $base-color-alt; }

.flatpickr-day.today:focus,
.flatpickr-day.today:hover {
  border-color: $base-color-alt;
  background: $base-color-alt;
  color: #3f4458; }

.flatpickr-day.inRange,
.flatpickr-day.nextMonthDay.inRange,
.flatpickr-day.nextMonthDay.today.inRange,
.flatpickr-day.nextMonthDay:focus,
.flatpickr-day.nextMonthDay:hover,
.flatpickr-day.prevMonthDay.inRange,
.flatpickr-day.prevMonthDay.today.inRange,
.flatpickr-day.prevMonthDay:focus,
.flatpickr-day.prevMonthDay:hover,
.flatpickr-day.today.inRange,
.flatpickr-day:focus,
.flatpickr-day:hover {
  background: $base-color-light;
  border-color: $base-color-light; }

/* flatpickr end */

/* sets */
.sets {
  margin-bottom: 3rem;

  @include media('<tab-s') {
    margin-bottom: 2rem; }

  &:last-child {
    margin-bottom: 0; }

  &__list {
    display: block; }

  &__item {
    padding: 2rem 0;
    border: 1px solid $border-color;
    margin: -1px -1px 0;

    @include media('<tab') {
      margin: -1px 0 0;
      border-width: 1px 0; }

    @include media('<tab-s') {
      padding: 1.5rem 0; }

    @include media('<mob-s') {
      border: none;
      margin: 0;
      padding: 0 0 4rem; }

    &:last-child {
      margin-bottom: 0; } } }

.sets-pagination {
  text-align: center;
  margin-bottom: 3rem;

  @include media('<mob') {
    margin-bottom: 2rem; } }

/* sets end */

/* set card */
.p-card-set {
  position: relative;
  padding: 2rem 4rem 2rem 0;
  margin-bottom: 2.4rem;

  @include media('<tab') {
    padding: 2rem 0;
    display: flex;
    justify-content: center; }

  @include media('<mob-s') {
    margin-left: -#{nth($section-inner-space--mob, 2)};
    margin-right: -#{nth($section-inner-space--mob, 2)}; }

  &:last-child {
    margin-bottom: 0; } }

/* set card end */

/* divisions anchors */
// divisions anchors nav
.divisions-anchors {
  margin-bottom: 3rem;
  position: relative;
  border-bottom: 0.1rem solid $border-color;
  padding-bottom: 0.2rem;

  &__list {
    display: flex;
    flex-wrap: wrap;
    font-size: 1.6rem;
    line-height: 2.4rem; }

  &__item {
    color: rgba(black, 0.7);
    text-decoration: none;
    margin: 0 5rem -0.5rem 0;
    padding-bottom: 0.8rem;
    border-bottom: 0.5rem solid transparent;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    // transition
    $prop: color, border-color;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &:last-child {
      margin-bottom: 0; }

    span {
      border-bottom: 1px dashed currentColor; }

    em {
      display: inline-block;
      font-family: inherit;
      font-style: inherit;
      color: $base-color; }

    // hover
    &:hover {
      color: #000; }

    @include media('<lap-s') {
      margin-right: #{nth($section-inner-space--tab, 2)}; }

    // active
    //&.active
    ////color: #000
    //border-color: $base-color
 } }    ////cursor: default

/* divisions anchors end */

/* recommend */
.recommend {
  &_align-center {
    text-align: center; }

  &__align {
    margin: 0 auto;
    max-width: 42rem; }

  &__title {
    display: block;
    position: relative;
    @include font-bold;
    font-size: 2rem;
    line-height: 2.8rem;
    color: $title-color;
    margin-bottom: 1rem;
    word-break: break-word;

    @include media('<mob-s') {
      font-size: 1.8rem;
      line-height: 2.0rem; } }

  &__figure {
    margin-bottom: 1rem;

    &-img {
      width: 4.4rem;
      margin: 0 auto;

      img {
        height: auto;
        width: 100%; } } }

  &__text {
    font-size: 1.6rem;
    line-height: 2.4rem;
    margin-bottom: 1.5rem;
    color: $text-color;

    @include media('<mob-s') {
      font-size: 1.4rem;
      line-height: 2.0rem; } }

  &__footer {
    [class*="btn-"] {
      width: 180px;
      max-width: 100%; }
    .purchase-btn {
      margin: 0 auto; } } }

/* recommend end */
